import { Pipe, PipeTransform } from '@angular/core';
import { ProfileObject } from 'src/app/models/user/profile.model';

@Pipe({
  name: 'profileSearchFilter'
})
export class ProfileSearchFilterPipe implements PipeTransform {

  transform(profiles: ProfileObject[], searchValue:string): ProfileObject[] {
    if(!profiles || !searchValue){
      return profiles;
    }
    searchValue=searchValue.toLowerCase();
    return profiles.filter((profile:ProfileObject)=>

      (profile.firstName != null && profile.firstName.toLowerCase().includes(searchValue))
      ||
      (profile.lastName != null && profile.lastName.toLowerCase().includes(searchValue)) 
      ||
      (profile.middleName != null && profile.middleName.toLowerCase().includes(searchValue)) 
      ||
      (profile.email != null && profile.email.toLowerCase().includes(searchValue)) 
      ||
      (profile.roles != null && profile.roles.toString().toLowerCase().includes(searchValue)) 
    );
  }

}
