import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { OtpResponse, OtpValidate, RegisterRequest } from 'src/app/models/register/registration.model';
import { resourceEndpointConfig } from 'src/app/constants/resource-endpoint.config';
import { environment } from 'src/environments/environment';
import { Cacheable } from 'ts-cacheable';
import { SupportRequest } from 'src/app/models/support-requests/support-requests.model';
import { ProfileObject } from 'src/app/models/user/profile.model';
import { EventsObject } from 'src/app/models/events/event.model';
import { RequestObject } from 'src/app/models/request/requests.model';

@Injectable({
  providedIn: 'root'
})
export class PublicApiService {

  

  constructor(
    private httpClient: HttpClient, 
  ) {}

  requestOtp(clientObj:any):Observable<any>{
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    const body = {
      name: clientObj.firstname,
      email:clientObj.email
    }
    return this.httpClient.post<OtpResponse>(resourceEndpointConfig.registration.otpEndpoint, body,{headers: headers})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  register(body:any,otp:string,token:string):Observable<any>{
    console.log(body)
    let registerRequest = new RegisterRequest(body);
    registerRequest.validate={
      otp:otp,
      otpServiceToken:token
    };
    console.log(body)
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    return this.httpClient.post<any>(resourceEndpointConfig.registration.registerEndpoint, registerRequest,{headers: headers})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  

  resendOtp(id:string):Observable<any>{   
 
    return this.httpClient.patch(resourceEndpointConfig.registration.otpEndpoint+'/resend/'+encodeURIComponent(id),null)
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  invalidateOtp(id:string) {
    return this.httpClient.patch(resourceEndpointConfig.registration.otpEndpoint+'/invalidate/'+encodeURIComponent(id),null)
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  @Cacheable({async: true,maxAge: 24*60*60*1000*1000})
  getRefData():Observable<any> {
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    .set('hideError','Yes');
    return this.httpClient.post(resourceEndpointConfig.refDataEndpoint.external+'/'+encodeURIComponent('dialcode'),null,{headers: headers})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  @Cacheable({async: true,maxAge: 24*60*60*1000*1000})
  getMemberDetails() :Observable<ProfileObject[]>{
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    .set('hideError','Yes');
    return this.httpClient.post(resourceEndpointConfig.refDataEndpoint.external+'/'+encodeURIComponent('publicmembers'),null,{headers: headers})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  @Cacheable({async: true,maxAge: 24*60*60*1000*1000})
  getEventDetails() :Observable<EventsObject[]>{
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    .set('hideError','Yes');
    return this.httpClient.post(resourceEndpointConfig.refDataEndpoint.external+'/'+encodeURIComponent('publicevents'),null,{headers: headers})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }


  submitQuery(request:SupportRequest,token:string):Observable<any>{ 
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    return this.httpClient.post(resourceEndpointConfig.contactRequest.createEndpoint,request,{headers: headers,params:{token:token}})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  getQueryStatus(id:string,token:string):Observable<SupportRequest[]>{ 
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    return this.httpClient.post(resourceEndpointConfig.contactRequest.searchEndpoint,null,{headers: headers,params:{requestId:id,token:token}})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  getJoinRequestStatus(id:string,token:string):Observable<RequestObject[]>{ 
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    return this.httpClient.post(resourceEndpointConfig.registration.searchEndpoint,null,{headers: headers,params:{requestId:id,token:token}})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  initPasswordResovary(email: string) :Observable<any>{
    const body={
      email: email,
    }
    return this.httpClient.post(resourceEndpointConfig.passwordReset.forgotPassword, body);
  }

  getQuitRequestStatus(id:string,token:string):Observable<RequestObject[]>{ 
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    return this.httpClient.post(resourceEndpointConfig.requests.searchQuitEndpoint,null,{headers: headers,params:{requestId:id,token:token}})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  submitFeedback(request:RequestObject,token:string):Observable<any>{ 
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json');
    return this.httpClient.post(resourceEndpointConfig.requests.submitFeedback,request,{headers: headers,params:{token:token}})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }
  


}
