import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { resourceEndpointConfig } from 'src/app/constants/resource-endpoint.config';
import { EarningAndExpenseObject } from 'src/app/models/earning-and-expense/earning-expense.model';
import { EarningObject } from 'src/app/models/earning-and-expense/earning.model';
import { ExpenseObject } from 'src/app/models/earning-and-expense/expense.model';
import { HistoryObject } from 'src/app/models/history/history.model';

@Injectable({
  providedIn: 'root'
})
export class FundsAndExpenditureService {
  getExpenseHistory(id: string)  : Observable<HistoryObject[]>{
    return this.httpClient.get(resourceEndpointConfig.expenditure.expenseHistory.replace('${id}',id))
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  getEarningHistory(id: string)  : Observable<HistoryObject[]>{
    return this.httpClient.get(resourceEndpointConfig.expenditure.earnigHistory.replace('${id}',id))
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }


  constructor(    private httpClient: HttpClient,   ) { }

  getEarningDetails(): Observable<EarningAndExpenseObject> {
    return this.httpClient.get(resourceEndpointConfig.expenditure.getEarningsEndpoint)
      .pipe(
        map((data: any) => {
          return data.responsePayload;
        })
      );
  }

  getEarningExpenseDetails(...filter:string[]): Observable<EarningAndExpenseObject> {
    let param=new HttpParams();
    if(filter){
      filter.forEach(f=>{
        param=param.set('filter',f);
      })
    }
    return this.httpClient.get(resourceEndpointConfig.expenditure.getEarningsExpenseEndpoint,{params:param})
      .pipe(
        map((data: any) => {
          return data.responsePayload;
        })
      );
  }

  getExpenditureDetails(): Observable<EarningAndExpenseObject> {
    return this.httpClient.get(resourceEndpointConfig.expenditure.getExpenditureEndpoint)
      .pipe(
        map((data: any) => {
          return data.responsePayload;
        })
      );
  }

  addEarningSoure(earning:EarningObject): Observable<any>{
    return this.httpClient.post(resourceEndpointConfig.expenditure.createEarningsEndpoint,earning).pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  addExpenditure(expenditure:ExpenseObject): Observable<any>{
    return this.httpClient.post(resourceEndpointConfig.expenditure.createExpenditureEndpoint,expenditure).pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }


  updateExpenditure(id:string,expenditure:ExpenseObject):Observable<ExpenseObject>{
    return this.httpClient.patch(resourceEndpointConfig.expenditure.updateExpenditureEndpoint.replace('${id}',id),expenditure)
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  updateEarning(id:string,earning:EarningObject):Observable<EarningObject>{
    return this.httpClient.patch(resourceEndpointConfig.expenditure.updateEarningEndpoint.replace('${id}',id),earning)
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  deleteExpenditure(id:string): Observable<any>{
    return this.httpClient.delete(resourceEndpointConfig.expenditure.updateExpenditureEndpoint.replace('${id}',id));
  }

  deleteEarning(id:string): Observable<any>{
    return this.httpClient.delete(resourceEndpointConfig.expenditure.updateEarningEndpoint.replace('${id}',id));
  }


}
