import { Component, EventEmitter, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { MatAccordion } from '@angular/material/expansion';
import { RequestObject } from 'src/app/models/request/requests.model';
import { RequestsService } from 'src/app/services/resource/internal/requests.service';
import { REQUESTS } from 'src/app/constants/requests.constants';
import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { ModalService } from 'src/app/services/modal/modal.service';
import { Title } from '@angular/platform-browser';
import { AlertMode, AlertType } from 'src/app/components/modal/notification-modal/notification-modal.component';
import { AuthUserService } from 'src/app/services/utils/auth-user.service';
import { Location } from '@angular/common';
import { SCOPE } from 'src/app/constants/auth-scope.constats';
import { SpinnerVisibilityService } from 'ng-http-loader';
import { ComprehensiveRequestSummaryObject } from 'src/app/models/dashboard/comprehensive-summary.model';
import { approutes } from 'src/app/constants/routes.config';
import { DashboardService } from 'src/app/services/resource/internal/dashboard.service';
import { buildHistoryMessage, HelperService, historyHeader, isEmpty } from 'src/app/services/utils/helper.service';
import { HISTORY } from 'src/app/constants/history.constants';
import { Router } from '@angular/router';
import { requiredIfValidator } from 'src/app/validator/conditionally-required.validator';


@Component({
  selector: 'app-registration-request',
  templateUrl: './registration-request.component.html',
  styleUrls: ['./registration-request.component.scss']
})
export class RegistrationRequestComponent implements OnInit {
  @ViewChild(MatAccordion)
  accordion!: MatAccordion;

  @ViewChild("requestTemplate")
  requestTemplate!: TemplateRef<any>;

  @ViewChild("updateModalHeader")
  updateModalHeader!: TemplateRef<any>;

  @ViewChild("JoinRequestModalBody")
  JoinRequestModalBody!: TemplateRef<any>;

  
  @ViewChild("updateTemplate")
  updateTemplate!: TemplateRef<any>;

  @ViewChild("updateButtonsActions")
  updateButtonsActions!: TemplateRef<any>;

  outstandingRequests!: RequestObject[];
  resolvedRequests!: RequestObject[];
  requestMetaData = REQUESTS;
  requestsUpdateForm!: UntypedFormGroup;
  searchValue!: string;
  tabIndex: number=0;
  canUpdateJoinRequest!: boolean;
  summary!:ComprehensiveRequestSummaryObject;
  appRoutes=approutes;
  active_status=1;
  allRequestSummary!: ComprehensiveRequestSummaryObject;
  
  history=HISTORY;
  @ViewChild("historyTemplate")
  historyTemplate!: TemplateRef<any>;
  canViewRequestHistory!: boolean;
  canViewSelfRequest!: boolean;
  canViewOtherRequest!: boolean;
  canCreateRequest!: boolean;
  referenceData: any;

  historyHeader=historyHeader;
  historyMessage=buildHistoryMessage;

  constructor(
    private requestService: RequestsService,
    private modal: ModalService,
    private titleService: Title,
    private authUserService:AuthUserService,
    private location: Location,
    private spinner: SpinnerVisibilityService,
    private dashboardService: DashboardService,
    private router:Router,

  ) {
    HelperService.setPageTitle(this.titleService,"Requests");

  }

  ngOnInit(): void {
    let stateData=this.location.getState() as any;

    this.requestService.getReferenceData().subscribe(data => {
      this.referenceData = data;
      if(!stateData.outstandingRequests){
        this.tabIndex=0;
        this.tabChanged(this.tabIndex);
      }else{
        this.outstandingRequests=stateData.outstandingRequests;
      }
      if(stateData.requestSummary){
        this.summary=stateData.requestSummary;
        this.allRequestSummary=stateData.requestSummary;
      }
    })

    if(stateData.delegated === true){
      this.active_status=2;
    }
    
    this.canUpdateJoinRequest=this.authUserService.isAccrediatedToAny(
      SCOPE.update.join_request_G1,
      SCOPE.update.join_request_G2,
      SCOPE.update.join_request_G3,
      SCOPE.update.quit_request_G1,
      SCOPE.update.quit_request_G2,
      SCOPE.update.quit_request_G3,
      SCOPE.update.intermission_request,
      SCOPE.update.other_request
      );
    this.canViewRequestHistory=this.authUserService.isAccrediatedTo(SCOPE.read.request_history);
    this.canViewSelfRequest=this.authUserService.isAccrediatedTo(SCOPE.read.self_request);
    this.canViewOtherRequest=this.authUserService.isAccrediatedToAny(SCOPE.read.join_request,SCOPE.read.quit_request,SCOPE.read.intermission_request,SCOPE.read.other_request);
    //this.active_status=this.canViewOtherRequest?2:1;
    this.canCreateRequest=this.authUserService.isAccrediatedTo(SCOPE.create.request);

  }

  tabChanged(tabIndex:number){
    this.tabIndex = tabIndex;
    this.dashboardService.getComprehensiveSummary({request:true}).subscribe(data => {
      this.allRequestSummary = data.requests;
    })
    if(tabIndex == 0){
      this.requestService.getRequests({includeOutstanding:true,includeResolved:false}).subscribe((requests: RequestObject[]) => {
        this.outstandingRequests=requests;
      });
     
    }else if(tabIndex ==1){
      this.requestService.getRequests({includeOutstanding:false,includeResolved:true}).subscribe((requests: RequestObject[]) => {
        this.resolvedRequests =requests;
      });
    }
  }

  




  refreshRequest() {
    this.tabChanged(this.tabIndex);
    //this.outstandingRequests = requests.filter(r => !this.isResolved(r.status) && this.authUserService.isAccrediatedTo(SCOPE.read.join_request))
    //this.resolvedRequests = requests.filter(r => this.isResolved(r.status) && this.authUserService.isAccrediatedTo(SCOPE.read.join_request))
    
    
  }

  get requestUpdateFormControls() {
    return this.requestsUpdateForm.controls;
  }

  approveJoinRequest(request: RequestObject) {
    let message='Are you sure that you want to approve this request?';
    if(request.type === this.requestMetaData.type.quit && request.status === this.requestMetaData.status.awaitingFeedback){
      message="<b>WARNING</b>: This request is still awaiting requester's feedback. Please ask requester to fill feedback form. <b>We strongly do not recommend to Approve at this stage.</b>  <br><br>Do you still want to approve this request?";
    }
    let modal = this.modal.openNotificationModal('Confirm Approval', message, AlertType.warning, AlertMode.confirmation)
    modal.componentInstance.onSubmit.subscribe(decision => {
      if (decision == true) {
        this.requestService.updateRequests(request.requestNumber, {status: this.requestMetaData.status.approved})
          .subscribe((requests: any) => {
            this.spinner.show();
            this.refreshRequest();
            this.spinner.hide();
          });
      }
      modal.close();
    })

  }

  updateRequest(request: RequestObject){
    let rejectAction= new EventEmitter();
    this.requestsUpdateForm = new UntypedFormGroup({
      status: new UntypedFormControl(request.status, [Validators.required]),
      reasonForRejection: new UntypedFormControl(request.reasonForRejection, [requiredIfValidator(()=>this.requestsUpdateForm.controls.status.value === this.requestMetaData.status.rejected)]),
      message: new UntypedFormControl(request.message, []),
    })
    let dialog = this.modal.openDialog({
      headers: { headerText: 'Update Request' },
      body: { template: this.updateTemplate},
      action: { buttonTemplate: this.updateButtonsActions, buttonContext: { emitter:rejectAction,buttons: { cancel: 'Cancel', reject: 'Update' } } },
      dimention: { width: 600 }
    })
    rejectAction.subscribe(decision => {
      if (decision == true) {
        if(this.requestsUpdateForm.valid){
          let modal = this.modal.openNotificationModal('Confirm Update', 'Are you sure that you want to update this request?', AlertType.warning, AlertMode.confirmation)
        modal.componentInstance.onSubmit.subscribe(action => {
          if (action == true) {
            this.requestService.updateRequests(request.requestNumber,this.requestsUpdateForm.value)
              .subscribe((requests: any) => {
                this.spinner.show();
                this.refreshRequest();
                this.spinner.hide();
              });
              dialog.close();
          }
          modal.close();
        })
        }else{
          this.requestsUpdateForm.markAllAsTouched();
        }
        
      } else {
        dialog.close()
      }
    })
  }

  resumeRequest(request: RequestObject){
    let modal = this.modal.openNotificationModal('Confirm Approval', '<b>You are confirming that the requester is again back to NABARUN.</b></br>'+ 
    'Are you sure that you want to resume this request?', AlertType.warning, AlertMode.confirmation)
    modal.componentInstance.onSubmit.subscribe(decision => {
      if (decision == true) {
        this.requestService.updateRequests(request.requestNumber, {status: this.requestMetaData.status.resumed})
          .subscribe((requests: any) => {
            this.spinner.show();
            this.refreshRequest();
            this.spinner.hide();
          });
      }
      modal.close();
    })
  }

  //rejectAction!: EventEmitter<any>;


  rejectJoinRequest(request: RequestObject) {
    let rejectAction= new EventEmitter();
    this.requestsUpdateForm = new UntypedFormGroup({
      status: new UntypedFormControl(this.requestMetaData.status.rejected, [Validators.required]),
      reasonForRejection: new UntypedFormControl(request.reasonForRejection, [Validators.required]),
    })
    let dialog = this.modal.openDialog({
      headers: { headerText: 'Reject Request' },
      body: { template: this.JoinRequestModalBody },
      action: { buttonTemplate: this.updateButtonsActions, buttonContext: {emitter:rejectAction, buttons: { cancel: 'Cancel', reject: 'Reject' } } },
      dimention: { width: 600, height: 250 }
    })
    rejectAction.subscribe(decision => {
      if (decision == true) {
        let modal = this.modal.openNotificationModal('Confirm Rejection', 'Are you sure that you want to reject this request?', AlertType.warning, AlertMode.confirmation)
        modal.componentInstance.onSubmit.subscribe(action => {
          if (action == true) {
            this.requestService.updateRequests(request.requestNumber,this.requestsUpdateForm.value)
              .subscribe((requests: any) => {
                this.spinner.show();
                this.refreshRequest();
                this.spinner.hide();
              });
              dialog.close();
          }
          modal.close();
        })
      } else {
        dialog.close()
      }
    })

  }


  createRequest(){
    //this.profileService.getAllProfiles().subscribe(data=>{
      this.router.navigate([this.appRoutes.createRequest.url]);
   // })
  }







  colorClassByStatus=(status: string): string =>{
    switch (status) {
      case this.requestMetaData.status.approved:
        return 'text-green-700';
      case this.requestMetaData.status.rejected:
        return 'text-red-700';
      case this.requestMetaData.status.awaitingL1Approval:
        return 'text-indigo-700';
      case this.requestMetaData.status.awaitingL2Approval:
        return 'text-indigo-700';
      case this.requestMetaData.status.onHold:
        return 'text-yellow-700';
      default:
        return 'text-blue-500';
    }
  }

  showRequestHistory(data:RequestObject){
    this.requestService.getRequestHistory(data.requestNumber).subscribe(data=>{
      this.modal.openDialog({
        body: {
          template: this.historyTemplate,
          context:{
            histories:data,
            name : "Request"
          }
        },
        headers: {
          headerText:'View Changes'
        },
        action: { buttonTemplate: this.updateButtonsActions, buttonContext: { buttons: { cancel: 'Close'} } },
        dimention: { width: 800 }
      });
    })
  }



  requestStatus=(code:string)=>{
    //console.log(this.referenceData.requestStatus)
    if(isEmpty(code) || !this.referenceData.requestStatus){
      return code;
    }
    return this.referenceData.requestStatus.find((obj: any )=>obj.key === code)?.displayValue || null;
    //return code;//
  }
 
  requestType=(code:string)=>{
    if(isEmpty(code) || !this.referenceData.requestType){
      return code;
    }
    return this.referenceData.requestType.find((obj: any )=>obj.key === code)?.displayValue || null;
    //return code;//
  }

  getFeedbackUrl(request: RequestObject){
    let url=window.location.origin+this.appRoutes.feedback.url;
    this.requestService.getFeedbackUrl(request.requestNumber,url )
          .subscribe((requests: RequestObject) => {
            request.url=requests.url;
          });
  }

  copyToClipboard(request: RequestObject){
    navigator.clipboard.writeText(request.url).then(d=>{
      
    })
  }


}


