import { Location } from '@angular/common';
import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertMode, AlertType } from 'src/app/components/modal/notification-modal/notification-modal.component';
import { SCOPE } from 'src/app/constants/auth-scope.constats';
import { NOTICE } from 'src/app/constants/notice.constant';
import { approutes } from 'src/app/constants/routes.config';
import { NoticeObject } from 'src/app/models/notices/notice.model';
import { ModalService } from 'src/app/services/modal/modal.service';
import { NoticeService } from 'src/app/services/resource/internal/notice.service';
import { AuthUserService } from 'src/app/services/utils/auth-user.service';
import { buildHistoryMessage, HelperService, historyHeader } from 'src/app/services/utils/helper.service';

@Component({
  selector: 'app-notice-board',
  templateUrl: './notice-board.component.html',
  styleUrls: ['./notice-board.component.scss']
})
export class NoticeBoardComponent implements OnInit {
  appRoutes=approutes;
  noticeMetaData=NOTICE;
  searchValue!:string;
  allNotices!:NoticeObject[];
  canCreateNotice!: boolean;
  canUpdateNotice!: boolean; 
  canDeleteNotice!: boolean;
  singleNotice: boolean=false;
  @ViewChild("historyTemplate")
  historyTemplate!: TemplateRef<any>;
  @ViewChild("updateActionbuttons")
  updateActionbuttons!: TemplateRef<any>;
  canViewNoticeHistory!: boolean;
  isInactiveUser!: boolean;
  historyHeader=historyHeader;
  historyMessage=buildHistoryMessage;
  historyName="Notice";
  preview: boolean = false;

  constructor(
    private noticeService:NoticeService,
    private router:Router,
    private modal:ModalService,
    private title:Title,
    private authUser:AuthUserService,
    private route:ActivatedRoute,

    ) { 
    }
 
  ngOnInit(): void {
    HelperService.setPageTitle(this.title,"Notices");  
    console.log(this.route.snapshot.data)
    this.isInactiveUser=this.authUser.isUserInactive();
    this.canCreateNotice=this.authUser.isAccrediatedTo(SCOPE.create.notice);
    this.canUpdateNotice=this.authUser.isAccrediatedTo(SCOPE.update.notice);
    this.canDeleteNotice=this.authUser.isAccrediatedTo(SCOPE.delete.notice);
    this.canViewNoticeHistory=this.authUser.isAccrediatedTo(SCOPE.read.notice_history);
    if(this.route.snapshot.data.route.mode === 'viewall'){
      this.allNotices=this.route.snapshot.data.notices;
    }else if(this.route.snapshot.data.route.mode === "viewsingle"){
      this.allNotices=[];
      this.allNotices.push(this.route.snapshot.data.notice);
      this.singleNotice=true;
    }
  }

  refresh(){
    this.noticeService.getNotices().subscribe(data=>{
      this.allNotices=data;
      this.singleNotice=false;
      this.preview=false;

    })
  }

  // createNewNotice(){
  //   this.noticeService.getDraftNotice().subscribe(data=>{
  //     this.router.navigate([approutes.createNotice.url],{queryParams:{action:NOTICE.action.create},state:{draftedNotice:data}})
  //   })
  // }

  updateNotice(notice:NoticeObject){
    // this.noticeService.getAvailableRoleNotice().subscribe(data=>{
    //   this.router.navigate([approutes.updateNotice],{queryParams:{action:NOTICE.action.edit},state:{availableRoles:data,editNotice:notice}})
    // })
    this.router.navigate([approutes.updateNotice.url.replace(":id",notice.id)]);
  }

  deleteNotice(notice:NoticeObject){
    let modal=this.modal.openNotificationModal('Delete Notice','Are you sure to delete this notice?',AlertType.warning,AlertMode.confirmation);
          modal.componentInstance.onSubmit.subscribe(response=>{
            if(response == true){
              this.noticeService.deleteNotice(notice.id).subscribe(data=>{
                this.allNotices.splice(this.allNotices.findIndex(e=>e.id === notice.id),1);
              })
            }
            modal.close();
          });
  }

  showNoticeHistory(data:NoticeObject){
    this.noticeService.getNoticeHistory(data.id).subscribe(data=>{
      this.modal.openDialog({
        body: {
          template: this.historyTemplate,
          context:{histories:data}
        },
        headers: {
          headerText:'View Changes'
        },
        action: { buttonTemplate: this.updateActionbuttons, buttonContext: { buttons: { cancel: 'Close'} } },
        dimention: { width: 800 }
      });
    })
  }

  copyToClipboard(str:string){
    navigator.clipboard.writeText(str).then(d=>{
      
    })
  }

}
