import { Component, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { Title } from '@angular/platform-browser';
import { OAuthService } from 'angular-oauth2-oidc';
import { trimValidator } from 'src/app/validator/trim.validator';

@Component({
  selector: 'app-login',
  templateUrl: './logout.component.html',
  styleUrls: ['./logout.component.scss']
})
export class LogoutComponent implements OnInit {
  constructor(
    private titleService: Title,
    private authService:OAuthService,
    ) {
    this.titleService.setTitle(this.titleService.getTitle().split("-")[0] + " - " + "Logout");
  }


  loginForm = new UntypedFormGroup({
    username: new UntypedFormControl('',[Validators.required,trimValidator('Username or Email address')]),
    password:new UntypedFormControl('',[Validators.required,trimValidator('Password')]),
    remember: new UntypedFormControl(false)
  });
  
  // onSubmit(): void {
  //   if (this.loginForm.valid) {
  //     this.authService.login(this.loginForm.controls.username.value,this.loginForm.controls.password.value,this.loginForm.controls.remember.value)
  //   }else {
  //     Object.keys(this.loginForm.controls).forEach(field => {
  //       const control = this.loginForm.get(field);
  //       control?.markAsTouched({ onlySelf: true });

  //     });
  //   }
  // }

  ngOnInit(): void {
    this.gotoLoginPage();
  }
  
  
  get f() {
    return this.loginForm.controls;
  }

  gotoLoginPage(){
    this.authService.initCodeFlow();
  }
  
}
