import { Pipe, PipeTransform } from '@angular/core';
import { CONTRIBUTIONS } from 'src/app/constants/contributions.constants';

@Pipe({
  name: 'contributionStatusFilter'
})
export class ContributionStatusFilterPipe implements PipeTransform {
  contributionMetaData = CONTRIBUTIONS;
  transform(status: any[], contributionStatus?: string): any {
    if (contributionStatus === null || contributionStatus === undefined || contributionStatus === '') {
      return status;
    }
    switch (contributionStatus) {
      case this.contributionMetaData.status.raised:
        return status.filter(data => data.key !== this.contributionMetaData.status.uncleared);
      case this.contributionMetaData.status.pending:
        return status/*.filter(data => data !== this.contributionMetaData.status.uncleared)*/
          .filter(data => data.key !== this.contributionMetaData.status.raised);
      case this.contributionMetaData.status.paid:
        return status.filter(data => data.key === this.contributionMetaData.status.paid || data.key === this.contributionMetaData.status.uncleared);
      case this.contributionMetaData.status.uncleared:
        return status.filter(data => data.key === this.contributionMetaData.status.paid || data.key === this.contributionMetaData.status.uncleared);
      case this.contributionMetaData.status.deferred:
        return status.filter(data => data.key === this.contributionMetaData.status.paid || data.key === this.contributionMetaData.status.uncleared || data.key === this.contributionMetaData.status.pending || data.key === this.contributionMetaData.status.deferred|| data.key === this.contributionMetaData.status.cancelled);
      case this.contributionMetaData.status.cancelled:
        return status.filter(data => data.key === this.contributionMetaData.status.cancelled);
    }

    return status;
  }

}

@Pipe({
  name: 'contributionTypeFilter'
})
export class ContributionTypeFilterPipe implements PipeTransform {

  transform(type: any[], index?: number): any {
    if (index === null || index === undefined) {
      return type;
    }
    switch (index) {
      case 0:
        return type;
      case 1:
        return type.filter(data => data.key == CONTRIBUTIONS.type.onceoff)
    }

    return status;
  }

}
