import { Pipe, PipeTransform } from '@angular/core';
import { CONTRIBUTIONS } from 'src/app/constants/contributions.constants';

@Pipe({
  name: 'contributionFilter'
})
export class ContributionFilterPipe implements PipeTransform {
  
  transform(contributions: any, user: any,
    myContri:boolean, 
    regular:boolean,
    onceoff:boolean,
    raised:boolean,
    paid:boolean,
    pending:boolean,
    uncleared:boolean,
    deferred:boolean,
    cancelled:boolean ): any {
    //console.log('myContri='+myContri+'regular='+regular+'onceoff='+onceoff+'raised='+raised+'paid='+paid+'pending='+pending+'uncleared='+uncleared+'deferred='+deferred+'cancelled='+ cancelled)
    //console.log(myContri)
    if(!contributions){
      return contributions;
    }
    if(myContri){
      contributions=contributions.filter((contribution: any)=>contribution.profile.id ===user.user.profile.id);
    }
    if(regular==false){
      contributions=contributions.filter((contribution: any)=>contribution.contributionType != CONTRIBUTIONS.type.regular);
    }
    if(onceoff==false){
      contributions=contributions.filter((contribution: any)=>contribution.contributionType != CONTRIBUTIONS.type.onceoff);
    }
    if(raised==false){
      contributions=contributions.filter((contribution: any)=>contribution.contributionStatus != CONTRIBUTIONS.status.raised);
    }
    if(paid==false){
      contributions=contributions.filter((contribution: any)=>contribution.contributionStatus != CONTRIBUTIONS.status.paid);
    }
    if(pending==false){
      contributions=contributions.filter((contribution: any)=>contribution.contributionStatus != CONTRIBUTIONS.status.pending);
    }
    if(uncleared==false){
      contributions=contributions.filter((contribution: any)=>contribution.contributionStatus != CONTRIBUTIONS.status.uncleared);
    }
    if(deferred==false){
      contributions=contributions.filter((contribution: any)=>contribution.contributionStatus != CONTRIBUTIONS.status.deferred);
    }
    if(cancelled==false){
      contributions=contributions.filter((contribution: any)=>contribution.contributionStatus != CONTRIBUTIONS.status.cancelled);
    }
    return contributions;
  }

}
