import { Location } from '@angular/common';
import { Component, EventEmitter, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { Title } from '@angular/platform-browser';
import { AlertMode, AlertType } from 'src/app/components/modal/notification-modal/notification-modal.component';
import { SCOPE } from 'src/app/constants/auth-scope.constats';
import { HISTORY } from 'src/app/constants/history.constants';
import { CONTACTREQUESTS } from 'src/app/constants/requests.constants';
import { approutes } from 'src/app/constants/routes.config';
import { ChangeObject } from 'src/app/models/history/history.model';
import { SupportRequest } from 'src/app/models/support-requests/support-requests.model';
import { ModalService } from 'src/app/services/modal/modal.service';
import { RequestsService } from 'src/app/services/resource/internal/requests.service';
import { AuthUserService } from 'src/app/services/utils/auth-user.service';
import { buildHistoryMessage, compareObjects, HelperService, historyHeader, isEmpty, objectsEqual } from 'src/app/services/utils/helper.service';
import { conditionalValidator } from 'src/app/validator/conditionally-required.validator';

@Component({
  selector: 'app-support-center',
  templateUrl: './support-center.component.html',
  styleUrls: ['./support-center.component.scss']
})
export class SupportCenterComponent implements OnInit {
  outstandingContacts!: SupportRequest[];
  resolvedContacts!: SupportRequest[];
  searchValue!: string;
  tabIndex: number = 0;

  @ViewChild("supportRequestTemplate")
  supportRequestTemplate!: TemplateRef<any>;

  @ViewChild("updateTemplate")
  updateTemplate!: TemplateRef<any>;

  @ViewChild("updateActionbuttons")
  updateActionbuttons!: TemplateRef<any>;

  updateContactRequest!: EventEmitter<any>;
  contactRequestForm!: UntypedFormGroup;
  contactRequestMetadata = CONTACTREQUESTS;
  referenceData: any;

  historyHeader=historyHeader;
  historyMessage=buildHistoryMessage;

  history=HISTORY;
  @ViewChild("historyTemplate")
  historyTemplate!: TemplateRef<any>;
  canViewRequestHistory!: boolean;
  canUpdateContactRequest!: boolean;
  isInactiveUser!: boolean;

  constructor(
    private requestService: RequestsService,
    private modal: ModalService,
    private titleService: Title,
    private location: Location,
    private authUserService:AuthUserService,

  ) {
    HelperService.setPageTitle(this.titleService,"Support Center");

  }
  appRoutes = approutes;
  ngOnInit(): void {
    this.requestService.getReferenceData().subscribe(data => this.referenceData = data)
    this.isInactiveUser=this.authUserService.isUserInactive();

    let stateData = this.location.getState() as any;
    if (!stateData.outstandingContacts) {
      this.tabIndex = 0;
      this.tabChanged(this.tabIndex);
    } else {
      this.outstandingContacts = stateData.outstandingContacts;
    }
    this.canViewRequestHistory=this.authUserService.isAccrediatedTo(SCOPE.read.request_history);
    this.canUpdateContactRequest=this.authUserService.isAccrediatedTo(SCOPE.update.contact_request);

  }
  tabChanged(tabIndex: number) {
    this.tabIndex = tabIndex;
    if (tabIndex == 0) {
      this.requestService.getContactRequests({ includeOutstanding: true, includeResolved: false }).subscribe((requests: SupportRequest[]) => {
        this.outstandingContacts = requests;
      });
    } else if (tabIndex == 1) {
      this.requestService.getContactRequests({ includeOutstanding: false, includeResolved: true }).subscribe((requests: SupportRequest[]) => {
        this.resolvedContacts = requests;
      });
    }
  }

  refreshRequest() {
    this.tabChanged(this.tabIndex);
  }


  editContactRequest(contactRequest: SupportRequest) {
    this.updateContactRequest = new EventEmitter();
    this.contactRequestForm = new UntypedFormGroup({
      status: new UntypedFormControl(contactRequest.status, [Validators.required]),
      //assignedTo: new FormControl(contactRequest.assigneTo, []),
      messageToUser: new UntypedFormControl('',
        [conditionalValidator(() =>
        (this.contactRequestForm.get('status')?.value === this.contactRequestMetadata.status.completed
          || this.contactRequestForm.get('status')?.value === this.contactRequestMetadata.status.rejected
        ),
          Validators.required)]
      )
    });
    this.contactRequestForm.get('status')?.valueChanges
      .subscribe(value => {
        this.contactRequestForm.get('messageToUser')?.updateValueAndValidity();
      });
    let dialog = this.modal.openDialog({
      body: {
        template: this.updateTemplate,
        context:{request:contactRequest}
      },
      headers: {
        headerText: 'Update Request'
      },
      action: { buttonTemplate: this.updateActionbuttons, buttonContext: { buttons: { cancel: 'Cancel', update: 'Update' } } },
      dimention: { width: 700 }
    });

    this.updateContactRequest.subscribe((decision: boolean) => {
      if (decision === true) {

        if (this.contactRequestForm.valid) {
          let modifiedObj = new SupportRequest;
          // modifiedObj.assigneTo=this.contactRequestForm.value.assignedTo;
          modifiedObj.messageToUser = this.contactRequestForm.value.messageToUser;
          modifiedObj.status = this.contactRequestForm.value.status;

          if (modifiedObj.status === contactRequest.status && isEmpty(modifiedObj.messageToUser)) {
            this.modal.openNotificationModal('Error', 'No changes have been made', AlertType.error, AlertMode.notification);
          } else {
            this.requestService.updateContactRequests(contactRequest.id, compareObjects(modifiedObj, contactRequest))
              .subscribe((data: SupportRequest) => {
                dialog.close();
                this.tabChanged(this.tabIndex);
              })
          }
        } else {
          Object.keys(this.contactRequestForm.controls).forEach(field => {
            const control = this.contactRequestForm.get(field);
            control?.markAsTouched({ onlySelf: true });
          });
        }
      }
    })
  }

  reopenRequest(contactRequest: SupportRequest) {
    let dialog = this.modal.openNotificationModal('Confirmation', 'Are you sure to reopen this request?', AlertType.warning, AlertMode.confirmation);
    dialog.componentInstance.onSubmit.subscribe(declaration => {
      dialog.close();
      if (declaration) {
        let modifiedObj = new SupportRequest;
        modifiedObj.status = this.contactRequestMetadata.status.reopen;
        this.requestService.updateContactRequests(contactRequest.id, compareObjects(modifiedObj, contactRequest))
          .subscribe((data: SupportRequest) => {
            this.tabChanged(this.tabIndex);
          })
      }
    })
  }

  profileDisplay(profile: { firstname: string; lastname: string; email: string; }) {
    if (profile.firstname === undefined || profile.lastname === undefined) {
      return '';
    }
    return profile.firstname.toString().toUpperCase() + ' ' + profile.lastname.toString().toUpperCase();
  }

  isRequiredField(controlName: string): boolean {
    return this.contactRequestForm.controls[controlName].hasValidator(Validators.required)
      || this.contactRequestForm.controls[controlName].errors?.required;
  }

  get CRFControl() {
    return this.contactRequestForm.controls;
  }


  colorClassByStatus(status: string): string {
    switch (status) {
      case this.contactRequestMetadata.status.completed:
        return 'text-green-700';
      case this.contactRequestMetadata.status.submitted:
        return 'text-blue-700';
      case this.contactRequestMetadata.status.reopen:
        return 'text-blue-700';
      case this.contactRequestMetadata.status.rejected:
        return 'text-red-700';
      case this.contactRequestMetadata.status.pending:
        return 'text-pink-700';
      case this.contactRequestMetadata.status.onHold:
        return 'text-yellow-700';
      case this.contactRequestMetadata.status.inprogress:
        return 'text-indigo-700';
      default:
        return 'text-gray-500';
    }
    
    
  }

  showRequestHistory(data:SupportRequest){
    this.requestService.getRequestHistory(data.id).subscribe(data=>{
      this.modal.openDialog({
        body: {
          template: this.historyTemplate,
          context:{
            histories:data,
            name: 'Support Request'
          }
        },
        headers: {
          headerText:'View Changes'
        },
        action: { buttonTemplate: this.updateActionbuttons, buttonContext: { buttons: { cancel: 'Close'} } },
        dimention: { width: 800 }
      });
    })
  }

  

  requestStatus=(code:string)=>{
    if(isEmpty(code)){
      return code;
    }
    //console.log(this.referenceData)
    return this.referenceData.contactRequestStatus.find((obj: any )=>obj.key === code)?.displayValue || null;
  }

}
