import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { resourceEndpointConfig } from 'src/app/constants/resource-endpoint.config';
import { HistoryObject } from 'src/app/models/history/history.model';
import { NoticeObject } from 'src/app/models/notices/notice.model';

@Injectable({
  providedIn: 'root'
})
export class NoticeService {
  createNoticeMeeting(code:string, id: string)   : Observable<NoticeObject>{
    return this.httpClient.post(resourceEndpointConfig.notice.meeting.replace('${id}',id),{},{params:{code:code}})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  getNoticeHistory(id: string)   : Observable<HistoryObject[]>{
    return this.httpClient.get(resourceEndpointConfig.notice.history.replace('${id}',id))
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  constructor(    private httpClient: HttpClient,   ) { }

  getNotices():Observable<NoticeObject[]>{
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    
    return this.httpClient.get(resourceEndpointConfig.notice.getNotice,{headers: headers})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  getDraftNotice():Observable<NoticeObject>{
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    
    return this.httpClient.get(resourceEndpointConfig.notice.getDraftNotice,{headers: headers})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  getNotice(id:string):Observable<NoticeObject>{
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    
    return this.httpClient.get(resourceEndpointConfig.notice.updateNotice.replace('${id}',id),{headers: headers})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }


  createNotice(object:NoticeObject):Observable<NoticeObject>{
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    
    return this.httpClient.post(resourceEndpointConfig.notice.getNotice,object,{headers: headers})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  updateNotice(id:string,object:NoticeObject):Observable<NoticeObject>{
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    
    return this.httpClient.patch(resourceEndpointConfig.notice.updateNotice.replace('${id}',id),object,{headers: headers})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  deleteNotice(id:string,isDraft:boolean=false):Observable<NoticeObject>{
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    
    if(isDraft){
      return this.httpClient.delete(resourceEndpointConfig.notice.getDraftNotice, { headers:headers,params:{noticeId:id} })
      .pipe(
        map((data: any) => {
          return data.responsePayload;
        })
      );
    }else{
      return this.httpClient.delete(resourceEndpointConfig.notice.updateNotice.replace('${id}',id),{headers: headers})
      .pipe(
        map((data: any) => {
          return data.responsePayload;
        })
      );
    }
    
  }


  getAvailableRoleNotice():Observable<string[]>{
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    
    return this.httpClient.get(resourceEndpointConfig.notice.getRoleNotice,{headers: headers})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }
}
