import { Location } from '@angular/common';
import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { UntypedFormArray, UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { Title } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertMode, AlertType } from 'src/app/components/modal/notification-modal/notification-modal.component';
import { EVENTS } from 'src/app/constants/events.constants';
import { approutes } from 'src/app/constants/routes.config';
import { EventsObject } from 'src/app/models/events/event.model';
import { FileUploadObject } from 'src/app/models/fileupload.model';
import { ModalService } from 'src/app/services/modal/modal.service';
import { EventsService } from 'src/app/services/resource/internal/events.service';
import { compareObjects, HelperService, isDuplicateArray, isEmpty, isEmptyObject, openWindow, saveAs } from 'src/app/services/utils/helper.service';

@Component({
  selector: 'app-event-action',
  templateUrl: './event-action.component.html',
  styleUrls: ['./event-action.component.scss']
})
export class EventActionComponent implements OnInit {
  eventsMetadata = EVENTS;
  appRoutes = approutes;
  eventCreationForm: UntypedFormGroup = new UntypedFormGroup({
    eventTitle: new UntypedFormControl('', [Validators.required, Validators.minLength(5), Validators.maxLength(100)]),
    eventDescription: new UntypedFormControl('', [Validators.required, Validators.minLength(50), Validators.maxLength(500)]),
    eventDate: new UntypedFormControl('', [Validators.required]),
    eventLocation: new UntypedFormControl('', [Validators.required, Validators.minLength(2), Validators.maxLength(30)]),
    eventStage: new UntypedFormControl(this.eventsMetadata.states.publishPublicly, [Validators.required]),
    eventBudget: new UntypedFormControl('', [Validators.required]),
  });
  eventData!: EventsObject;
  expenseForm!: UntypedFormGroup;
  isUpdate: boolean = false;
  initialPictureCount!: number;
  initialDocumentCount!: number;
  initialExpenditureCount!: number;
  isDraft: boolean = false;
  canAddExpentiture: boolean = false;

  constructor(
    private eventsService: EventsService,
    private activeRoute: ActivatedRoute,
    private router: Router,
    private modal: ModalService,
    private title: Title,
    private location: Location,

  ) { }

  ngOnInit(): void {

    let statedata = this.location.getState() as any;
    if (this.activeRoute.snapshot.queryParams['action'] === this.eventsMetadata.action.edit) {
      HelperService.setPageTitle(this.title,"Update Event");
      this.isUpdate = true;
      this.expenseForm = new UntypedFormGroup({
        expenditures: new UntypedFormArray([])
      })
      this.eventData = statedata.event;
      this.eventCreationForm.setValue({
        eventTitle: this.eventData.eventTitle,
        eventDescription: this.eventData.eventDescription,
        eventDate: this.eventData.eventDate,
        eventLocation: this.eventData.eventLocation,
        eventStage: this.eventData.eventStage,
        eventBudget: this.eventData.eventBudget
      });
      this.coverPhoto = { link: this.eventData.coverPicture, file: null, name: '' };
      this.initialPictureCount = this.eventData.pictures.length;
      this.initialDocumentCount = this.eventData.documents.length;
      this.initialExpenditureCount = this.eventData.expenditures.length;
      if (this.eventData.expenditures.length === 0) {
        this.ExpenditureFormArray.push(this.initExpenseRow());
      } else {
        this.eventData.expenditures.forEach(e => {
          this.ExpenditureFormArray.push(this.initExpenseRow({ id: e.id, amount: e.amount, description: e.description, title: e.title }));
        })
      }
      this.canAddExpentiture = new Date() >= new Date(this.eventData.eventDate);

    } else {
      HelperService.setPageTitle(this.title,"Create Event");
      if (statedata && statedata.draftedEvent) {
        this.eventData = statedata.draftedEvent;
        this.eventCreationForm.setValue({
          eventTitle: statedata.draftedEvent.eventTitle,
          eventDescription: statedata.draftedEvent.eventDescription,
          eventDate: statedata.draftedEvent.eventDate,
          eventLocation: statedata.draftedEvent.eventLocation,
          eventStage: statedata.draftedEvent.eventStage,
          eventBudget: this.eventData.eventBudget
        });
        this.coverPhoto = { link: statedata.draftedEvent.coverPicture, file: null, name: '' };
        this.isDraft = true;
      }

    }

  }

  /*
  * CREATE EVENT START *
  */

  createEvent(createAsDraft: boolean = false) {
    if (this.eventCreationForm.valid && this.coverPhoto && (!isEmpty(this.coverPhoto.link) || !isEmpty(this.coverPhoto.file))) {
      let event = new EventsObject;
      event.eventTitle = this.eventCreationForm.value.eventTitle;
      event.eventDescription = this.eventCreationForm.value.eventDescription;
      event.eventDate = this.eventCreationForm.value.eventDate;
      event.eventLocation = this.eventCreationForm.value.eventLocation;
      event.eventStage = this.eventCreationForm.value.eventStage;
      event.isDraft = createAsDraft;
      event.eventBudget = this.eventCreationForm.value.eventBudget;

      if (createAsDraft) {
        this.eventsService.createEvent(event).subscribe((data: EventsObject) => {
          this.eventData = data;
          this.isDraft = true;
          this.eventsService.addOrChangeEventCoverPicture(data.id, this.coverPhoto.file).subscribe()
        });
      } else {
        let modal = this.modal.openNotificationModal('Publish Event', 'Are you sure to publish this event?', AlertType.warning, AlertMode.confirmation);
        modal.componentInstance.onSubmit.subscribe(response => {
          if (response == true) {
            this.eventsService.createEvent(event).subscribe((data: EventsObject) => {
              this.eventData = data;
              this.eventsService.addOrChangeEventCoverPicture(data.id, this.coverPhoto.file).subscribe(data => {
                this.backToEvents();
              })
            });
          }
          modal.close();
        });
      }
    } else {
      if (!this.coverPhoto || (this.coverPhoto && isEmpty(this.coverPhoto.file))) {
        this.coverPhotoErrors = 'Please select a cover photo';
      }
      Object.keys(this.eventCreationForm.controls).forEach(field => {
        const control = this.eventCreationForm.get(field);
        control?.markAsTouched({ onlySelf: true });
      });
    }
  }

  updateEvent(id: string, updateAsDraft: boolean = false) {
    if (this.eventCreationForm.valid && this.coverPhoto && !(isEmpty(this.coverPhoto.file) && isEmpty(this.coverPhoto.link))) {
      let event = new EventsObject;
      event.eventTitle = this.eventCreationForm.value.eventTitle;
      event.eventDescription = this.eventCreationForm.value.eventDescription;
      event.eventDate = this.eventCreationForm.value.eventDate;
      event.eventLocation = this.eventCreationForm.value.eventLocation;
      event.eventStage = this.eventCreationForm.value.eventStage;
      event.isDraft = updateAsDraft;
      event.eventBudget = this.eventCreationForm.value.eventBudget;
      let diff = compareObjects(event, this.eventData)
      if (isEmptyObject(diff) && isEmpty(this.coverPhoto.file)) {
        this.modal.openNotificationModal('Error', 'No changes have been made', AlertType.error, AlertMode.notification);
      } else if (updateAsDraft) {
        if (isEmptyObject(diff) && !isEmpty(this.coverPhoto.file)) {
          this.eventsService.addOrChangeEventCoverPicture(this.eventData.id, this.coverPhoto.file).subscribe(data => { })
        }
        else if (!isEmptyObject(diff) && isEmpty(this.coverPhoto.file)) {
          this.eventsService.updateEvent(this.eventData.id, diff).subscribe((data: EventsObject) => { this.eventData = data });
        } else if (!isEmptyObject(diff) && !isEmpty(this.coverPhoto.file)) {
          this.eventsService.updateEvent(this.eventData.id, diff).subscribe((data: EventsObject) => {
            this.eventData = data;
            this.eventsService.addOrChangeEventCoverPicture(this.eventData.id, this.coverPhoto.file).subscribe(data => { })
          });
        }
      }
      else {

        let modal = this.modal.openNotificationModal(this.isDraft ? 'Publish Event' : 'Update Event', this.isDraft ? 'Are you sure to publish this event?' : 'Are you sure to update event data you modified?', AlertType.warning, AlertMode.confirmation);
        modal.componentInstance.onSubmit.subscribe(response => {
          if (response == true) {

            if (isEmptyObject(diff) && !isEmpty(this.coverPhoto.file)) {
              this.eventsService.addOrChangeEventCoverPicture(this.eventData.id, this.coverPhoto.file).subscribe(data => {
                this.backToEvents();
              })
            }
            else if (!isEmptyObject(diff) && isEmpty(this.coverPhoto.file)) {
              this.eventsService.updateEvent(this.eventData.id, diff).subscribe((data: EventsObject) => {
                this.backToEvents();

              });
            } else if (!isEmptyObject(diff) && !isEmpty(this.coverPhoto.file)) {
              this.eventsService.updateEvent(this.eventData.id, diff).subscribe((data: EventsObject) => {
                this.eventsService.addOrChangeEventCoverPicture(this.eventData.id, this.coverPhoto.file).subscribe(data => {
                  this.backToEvents();
                })
              });
            }

          }
          modal.close();
        })
      }
    } else {
      if (!this.coverPhoto || (this.coverPhoto && isEmpty(this.coverPhoto.file) && isEmpty(this.coverPhoto.link))) {
        this.coverPhotoErrors = 'Please select a cover photo';
      }
      Object.keys(this.eventCreationForm.controls).forEach(field => {
        const control = this.eventCreationForm.get(field);
        control?.markAsTouched({ onlySelf: true });
      });
    }
  }

  discardDraft() {
    let modal = this.modal.openNotificationModal('Update Event', 'Are you sure to discard the saved event data?', AlertType.warning, AlertMode.confirmation);
    modal.componentInstance.onSubmit.subscribe(response => {
      if (response == true) {
        this.eventsService.deleteEvent(this.eventData.id, true).subscribe(data => {
          this.eventCreationForm.reset();
          this.isDraft = false;
          this.coverPhoto = { name: '', file: null, link: '' };
        })
      }
      modal.close();
    });
  }


  updateExpenditures() {
    
    if (this.expenseForm.valid) {
      if (this.documents.length === 0 && this.eventData.documents.length === this.initialDocumentCount && isDuplicateArray(this.expenseForm.value.expenditures, this.eventData.expenditures)) {
        this.modal.openNotificationModal('Error', 'No changes have been made', AlertType.error, AlertMode.notification);
      } else {
        let modal = this.modal.openNotificationModal('Update Event', 'Are you sure to update event data you modified?', AlertType.warning, AlertMode.confirmation);
        modal.componentInstance.onSubmit.subscribe(response => {
          if (response == true) {
            this.eventsService.updateEventExpences(this.eventData.id, this.expenseForm.value.expenditures).subscribe();
            this.eventsService.updateEventAttachment(this.eventData.id, this.eventData.documents, false).subscribe(d => {
              let filesTo: File[] = [];
              this.documents.forEach(element => {
                filesTo.push(element.file)
              });
              if (filesTo.length > 0) {
                this.eventsService.addEventAttachment(this.eventData.id, false, filesTo).subscribe(res => {
                  this.backToEvents();
                });
              } else {
                this.backToEvents();
              }

            });
          }
          modal.close();
        });
      }
    } else {
      this.ExpenditureFormArray.controls.forEach((element: any, i: number) => {
        Object.keys(this.getExpenditureFormGroupControls(i)).forEach(field => {
          const control = this.ExpenditureFormArray.controls[i].get(field);
          control?.markAsTouched({ onlySelf: true });
        });
      });
    }

  }

  updatePictures() {
    if (this.images.length === 0 && this.eventData.pictures.length === this.initialPictureCount) {
      this.modal.openNotificationModal('Error', 'No changes have been made', AlertType.error, AlertMode.notification);
    } else {
      let modal = this.modal.openNotificationModal('Update Event', 'Are you sure to update event data you modified?', AlertType.warning, AlertMode.confirmation);
      modal.componentInstance.onSubmit.subscribe(response => {
        if (response == true) {
          this.eventsService.updateEventAttachment(this.eventData.id, this.eventData.pictures, true).subscribe(d => {
            let imagess: File[] = [];
            this.images.forEach(element => {
              imagess.push(element.file);
            });
            if (imagess.length > 0) {
              this.eventsService.addEventAttachment(this.eventData.id, true, imagess).subscribe(response => {
                this.backToEvents();
              });
            } else {
              this.backToEvents();
            }
          });
        }
        modal.close();
      })
    }

  }

  /***
   * HELPERS UPDATE EXPENSE
   * 
   */

  viewAttachment(id: string) {
    this.eventsService.viewAttachment(id, true).subscribe(data => {
      openWindow(data);
    })
  }

  downloadAttachment(id: string, fileName: string) {
    this.eventsService.viewAttachment(id, false).subscribe(data => {
      saveAs(data, fileName);
    })
  }


  initExpenseRow(expense?: { id: string, title:string, description: string, amount: number }) {
    return new UntypedFormGroup({
      id: new UntypedFormControl(expense?.id),
      title: new UntypedFormControl(expense?.title, [Validators.required]),
      description: new UntypedFormControl(expense?.description, [Validators.maxLength(30)]),
      amount: new UntypedFormControl(expense?.amount, [Validators.required]),
    });
  }

  addNewExpense() {
    this.ExpenditureFormArray.push(this.initExpenseRow());
  }

  deleteExpense(index: number) {
    this.ExpenditureFormArray.removeAt(index);
  }



  /***
    * HELPERS UPDATE ATTACHMENTS
    * 
    */

  coverPhoto!: { link: string; file: any; name: string; }
  coverPhotoErrors: any
  coverPhotoUrl!: string;

  previewCoverPhoto(event: any) {
    if (event.target.files && event.target.files[0]) {
      if (!event.target.files[0].type.match("image.*")) {
        this.coverPhotoErrors = 'File type must be PNG or JPG or JPEG';
        this.coverPhoto = { link: '', file: null, name: '' };
        return;
      }
      if (!(event.target.files[0].size > 50000 && event.target.files[0].size < 1000000)) {
        this.coverPhotoErrors = 'File size should be greter than 50Kb and less than 1Mb';
        this.coverPhoto = { link: '', file: null, name: '' };
        return;
      }
      this.coverPhotoErrors = null;
      const reader = new FileReader();
      reader.onload = (_event: any) => {
        this.coverPhoto = {
          link: _event.target.result,
          file: event.srcElement.files[0],
          name: event.srcElement.files[0].name
        };
      };
      reader.readAsDataURL(event.target.files[0]);
    }
  }

  images: FileUploadObject[] = [];
  selectedFileError!: string;
  selectPicturesEvent(imgFile: any) {
    if (imgFile.target.files && imgFile.target.files[0]) {
      this.selectedFileError = '';
      if (this.images.length + this.eventData.pictures.length >= this.eventsMetadata.props.maxImages) {
        this.selectedFileError = 'Only ' + this.eventsMetadata.props.maxImages + ' pictures can be uploaded. Remove some pictures to add new.'
        return;
      }
      if (!imgFile.target.files[0].type.match("image.*")) {
        this.selectedFileError = 'File type must be PNG or JPG or JPEG'
        return;
      }
      if (!(imgFile.target.files[0].size > 50000 && imgFile.target.files[0].size < 1000000)) {
        this.selectedFileError = 'File size should be greter than 50Kb and less than 1Mb';
        return;
      }
      let file = new FileUploadObject;
      file.file = imgFile.target.files[0];
      console.log(imgFile)
      //HTML5 FileReader API
      let reader = new FileReader();
      reader.onload = (e: any) => {
        let image = new Image();
        image.src = e.target.result;
        image.onload = rs => {
          file.preview = e.target.result;
        };
      };
      reader.readAsDataURL(imgFile.target.files[0]);
      this.images.push(file);
    }
  }

  selectedDocumentError: string = '';

  documents: FileUploadObject[] = [];

  selectDocumentsEvent(event: any) {
    if (event.target.files && event.target.files[0]) {
      this.selectedFileError = '';
      if (this.documents.length + this.eventData.documents.length >= this.eventsMetadata.props.maxDocuments) {
        this.selectedDocumentError = 'Only ' + this.eventsMetadata.props.maxDocuments + ' documents can be uploaded. Remove some documents to add new.'
        return;
      }

      if (!(event.target.files[0].size > 10000 && event.target.files[0].size < 1000000)) {
        this.selectedDocumentError = 'File size should be greter than 10Kb and less than 1Mb';
        return;
      }
      let file = new FileUploadObject;
      file.file = event.target.files[0];

      //HTML5 FileReader API
      let reader = new FileReader();
      reader.onload = (e: any) => {
        let image = new Image();
        image.src = e.target.result;
        image.onload = rs => {
          file.preview = e.target.result;
        };
      };
      reader.readAsDataURL(event.target.files[0]);
      this.documents.push(file);
    }
  }





  removeDocument(id: any, isExisting: boolean) {
    if (isExisting) {
      this.eventData.documents.splice(this.eventData.documents.findIndex(a => a.id === id), 1)
    } else {
      this.documents.splice(id, 1);
    }
  }

  removePicture(id: any, isExisting: boolean) {
    if (isExisting) {
      this.eventData.pictures.splice(this.eventData.documents.findIndex(a => a.id === id), 1)
    } else {
      this.images.splice(id, 1);
    }
  }


  getFileSize(file: File) {
    return file.size > 1024
      ? file.size > 1048576
        ? Math.round(file.size / 1048576) + "MB"
        : Math.round(file.size / 1024) + "kB"
      : file.size + "b";
  }

  /***
   * FORM CONTROL HELPERS
   */
  get ECF() {
    return this.eventCreationForm.controls;
  }

  get ExpenditureFormArray() {
    return this.expenseForm.controls.expenditures as UntypedFormArray;
  }

  getExpenditureFormGroupControls(index: number) {
    return (this.ExpenditureFormArray.controls[index] as UntypedFormGroup).controls;
  }


  isRequiredField(controlName: string): boolean {
    return this.eventCreationForm.controls[controlName].hasValidator(Validators.required)
      || this.eventCreationForm.controls[controlName].errors?.required;
  }

  backToEvents(showAll: boolean = false) {
    if (showAll) {
      this.eventsService.getEventsInternal().subscribe(data => {
        this.router.navigate([this.appRoutes.ourEvents.url], { state: { allEvents: data } });
      })
    } else {
      this.eventsService.getEvent(this.eventData.id).subscribe(data => {
        this.router.navigate([this.appRoutes.ourEvents.url], { state: { viewEvent: data } });
      })
      // this.eventsService.getEventsInternal().subscribe(data => {
      //   this.router.navigate([this.appRoutes.ourEvents.url], { state: { allEvents: data },fragment:this.eventData.id });
      // })
    }

  }
}



