export function requiredIfValidator(predicate: () => any) {
  return ((formControl: AbstractControl) => {
    if (!formControl.parent) {
      return null;
    }
   // console.log(formControl, predicate())
    if (predicate()) {
      return Validators.required(formControl);
    }
    return null;
  })
}


export interface BooleanFn {
  (): boolean;
}


export function conditionalValidator(predicate: BooleanFn,
  validator: ValidatorFn,
  errorNamespace?: string): ValidatorFn {
  return (formControl => {
    if (!formControl.parent) {
      return null;
    }
    let error = null;
    if (predicate()) {
      error = validator(formControl);
      //Changeing this 28/09/2021
      //console.log('formControl',validator,predicate())
    }
    if (errorNamespace && error) {
      const customError:any = {};
      customError[errorNamespace] = error;
      error = customError
    }
    return error;
  })
}