import { Pipe, PipeTransform } from '@angular/core';
import { SupportRequest } from 'src/app/models/support-requests/support-requests.model';

@Pipe({
  name: 'contactRequestSearch'
})
export class ContactRequestSearchPipe implements PipeTransform {

  
  transform(requests: SupportRequest[], search: string): any {
    if(!requests || !search){
      return requests;
    }
    return requests.filter((request:SupportRequest)=> 
      request.id.toLowerCase().includes(search.toLowerCase())  
      ||
      request.email.toLowerCase().includes(search.toLowerCase())
      ||
      request.fullName.toLowerCase().includes(search.toLowerCase())
      ||
      request.status.toLowerCase().includes(search.toLowerCase())
      ||
      request.phoneNumber.toLowerCase().includes(search.toLowerCase())
    );

    //
  }

}
