
export class RegisterRequest {
    firstName!: string;
    middleName!: string;
    lastName!: string;
    email!: string;
    dialCode!: string;
    mobileNumber!: string;
    hometown!: string;
    reasonForJoining!: string;
    attachedToAnyOtherSocialWork!: string;
    validate!:any;
     
    constructor(regObj: any) {
        this.firstName= regObj.firstname;
        this.middleName= regObj.middlename;
        this.lastName= regObj.lastname;
        this.email= regObj.email;
        this. dialCode= regObj.code;
        this.mobileNumber= regObj.mobile;
        this.hometown= regObj.hometown;
        this.reasonForJoining= regObj.reasonForJoining;
        this.attachedToAnyOtherSocialWork= regObj.otherSocialWork
    }

    

}

export class OtpValidate{
    otp!: string;
    otpServiceToken!: string;
    
    constructor(otp:any,token:string){
        this.otp=otp;
        this.otpServiceToken=token;
    }
    
}


export class OtpResponse {
    otpServiceToken!: string;
    otpLength!: number;
    expireAfter!: number;
    maskedIdentifier!: string;

}