export class ProfileObject {
    about!: string;
    address!: string;
    avatarUrl!: string;
    contactNumber!: string;
    country!: string;
    gender!:string;
    birthDay!: Date;
    dialCode!: string;
    email!: string;
    firstName!: string;
    hometown!: string; 
    id!: string;
    isUnblocked!: boolean;
    isActiveContributor!: boolean;
    lastName!: string;
    middleName!: string;
    memberSince!:Date;
    state!: string;
    district!: string;
    publicProfileConsent!:boolean;
    roles!:Roles[] ;
    pastRoles!:PastRoles[] ;
    userId!: string;
    status!: string;
}

export class Roles{
    id!:string;
    name!:string;
}

export class PastRoles{
    createdOn!:Date;
    roleNames!:string[];
    endDate!:Date;
    startDate!:Date;
}