import { Pipe, PipeTransform } from '@angular/core';
import { REQUESTS } from 'src/app/constants/requests.constants';
import { HelperService } from 'src/app/services/utils/helper.service';

@Pipe({
  name: 'requestStatusFilter'
})
export class RequestStatusFilterPipe implements PipeTransform {

  transform(allStatus: any, requestType: string, currentStatus: string): any {
    if (HelperService.isEmpty(currentStatus)) {
      return allStatus;
    }

    if (requestType === REQUESTS.type.join){
      switch (currentStatus) {
        case REQUESTS.status.awaitingL1Approval:
          return allStatus.filter((data: any) => (
            data.key === REQUESTS.status.awaitingL1Approval || 
            data.key === REQUESTS.status.onHold || 
            data.key === REQUESTS.status.rejected
            ));
        case REQUESTS.status.onHold:
          return allStatus.filter((data: any) => (data.key === REQUESTS.status.onHold || data.key === REQUESTS.status.awaitingL2Approval));
        case REQUESTS.status.approved:
          return allStatus.filter((data: any) => data.key === REQUESTS.status.approved);
        case REQUESTS.status.rejected:
          return allStatus.filter((data: any) => data.key === REQUESTS.status.rejected);
        default:
          return allStatus.filter((data: any) => data.key === REQUESTS.status.rejected);
      }
    }
    else
      allStatus = allStatus.filter((data: any) => (data.key === currentStatus))
    
  }
}
