import { Pipe, PipeTransform } from '@angular/core';
import { Configuration } from 'src/app/models/admin-console/admin.model';

@Pipe({
  name: 'propertySearch'
})
export class PropertySearchPipe implements PipeTransform {

  transform(configs: Configuration[], searchValue: string): Configuration[] {
    if(!searchValue){
      return configs;
    }
    
    return configs.filter((config: Configuration)=>
    (config.parameter_name && config.parameter_name.toLowerCase().includes(searchValue.toLowerCase())) ||
    (config.parameter_type && config.parameter_type.toLowerCase().includes(searchValue.toLowerCase())) ||
    (config.parameter_group && config.parameter_group.toLowerCase().includes(searchValue.toLowerCase()))
    );
  }

}
