import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { RequestObject } from 'src/app/models/request/requests.model';
import { resourceEndpointConfig } from 'src/app/constants/resource-endpoint.config';
import { SupportRequest } from 'src/app/models/support-requests/support-requests.model';
import { HistoryObject } from 'src/app/models/history/history.model';

@Injectable({
  providedIn: 'root'
})
export class RequestsService {

  constructor(
    private httpClient: HttpClient, 
    private route: Router,
    ){}


  createRequest(reqObj: RequestObject) : Observable<RequestObject>{
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    return this.httpClient.post(resourceEndpointConfig.requests.getRequestsEndpoint,reqObj,{headers: headers})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }
  getRequestHistory(id: string)  : Observable<HistoryObject[]>{
    return this.httpClient.get(resourceEndpointConfig.requests.history.replace('${id}',id))
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }


 

    getRequests(options:{includeOutstanding:boolean,includeResolved:boolean}):Observable<RequestObject[]>{
        const headers = new HttpHeaders()
        .set('Content-Type', 'application/json')
        const param = new HttpParams()
        .set('outstanding',options.includeOutstanding)
        .set('resolved',options.includeResolved);
        return this.httpClient.get<RequestObject[]>(resourceEndpointConfig.requests.getRequestsEndpoint,{headers: headers,params:param})
        .pipe(
          map((data: any) => {
            return data.responsePayload;
          })
        );
      }
      getReferenceData():Observable<any>{
        const headers = new HttpHeaders()
        .set('Content-Type', 'application/json')
        return this.httpClient.get<any>(resourceEndpointConfig.requests.referenceData,{headers: headers})
        .pipe(
          map((data: any) => {
            return data.responsePayload;
          })
        );
      }

      updateRequests(id:string,body:any):Observable<any>{
        const headers = new HttpHeaders()
        .set('Content-Type', 'application/json');
        return this.httpClient.patch(resourceEndpointConfig.requests.updateJoinRequestsEndpoint.replace('${id}',id),body,{headers: headers})
        .pipe(
          map((data: any) => {
            return data.responsePayload;
          })
        )
        ;
      }

      getContactRequests(options:{includeOutstanding:boolean,includeResolved:boolean}):Observable<SupportRequest[]>{
        const headers = new HttpHeaders()
        .set('Content-Type', 'application/json')
        const param = new HttpParams()
        .set('outstanding',options.includeOutstanding)
        .set('resolved',options.includeResolved);
        return this.httpClient.get(resourceEndpointConfig.contactRequest.getEndpoint,{headers: headers,params:param})
        .pipe(
          map((data: any) => {
            return data.responsePayload;
          })
        );
      }

      updateContactRequests(id:string,body:SupportRequest):Observable<any>{
        const headers = new HttpHeaders()
        .set('Content-Type', 'application/json');
        return this.httpClient.patch(resourceEndpointConfig.contactRequest.updateEndpoint.replace('${id}',id),body,{headers: headers})
        .pipe(
          map((data: any) => {
            return data.responsePayload;
          })
        )
        ;
      }



      getFeedbackUrl(id:string,base_url:string):Observable<RequestObject>{
        const headers = new HttpHeaders()
        .set('Content-Type', 'application/json')
        const param = new HttpParams()
        .set('url',base_url)
        return this.httpClient.get(resourceEndpointConfig.requests.getFeedbackUrlEndpoint.replace('${id}',id),{headers: headers,params:param})
        .pipe(
          map((data: any) => {
            return data.responsePayload;
          })
        );
      }
}
