import { Component, EventEmitter, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-qr-modal',
  templateUrl: './qr-modal.component.html',
  styleUrls: ['./qr-modal.component.scss']
})
export class QrModalComponent implements OnInit {
  
  qRSource!:string;
  heading!:string;
  message!:string;

  onExit = new EventEmitter();

  defaultClose:boolean=true;

  constructor(
    private dialogRef: MatDialogRef<QrModalComponent>,
    @Inject(MAT_DIALOG_DATA) data:any
  ) {
    this.qRSource = data.qRSource;
    this.heading = data.heading;
    this.message = data.message;
  }
  ngOnInit(): void {
  }

  close() {
    this.onExit.emit(this.defaultClose);
  }

  setDefaultClose(arg0: boolean) {
    this.defaultClose=arg0;
   // console.log(this.defaultClose)
  }

}
