import { Location } from '@angular/common';
import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { AlertMode, AlertType } from 'src/app/components/modal/notification-modal/notification-modal.component';
import { REQUESTS } from 'src/app/constants/requests.constants';
import { approutes } from 'src/app/constants/routes.config';
import { JoineeDetails, RequestObject } from 'src/app/models/request/requests.model';
import { ProfileObject } from 'src/app/models/user/profile.model';
import { ModalService } from 'src/app/services/modal/modal.service';
import { ProfileService } from 'src/app/services/resource/internal/profile.service';
import { RequestsService } from 'src/app/services/resource/internal/requests.service';
import { AuthUserService } from 'src/app/services/utils/auth-user.service';
import { HelperService, pastDateFilter } from 'src/app/services/utils/helper.service';
import { requiredIfValidator } from 'src/app/validator/conditionally-required.validator';
import { phoneNumberValidator } from 'src/app/validator/phone-number.validator';

@Component({
  selector: 'app-create-request',
  templateUrl: './create-request.component.html',
  styleUrls: ['./create-request.component.scss']
})
export class CreateRequestComponent implements OnInit {
  //requesterProfile: ProfileObject | undefined;
  appRoutes=approutes;
  requestMetaData=REQUESTS;
  pastDateFilter=pastDateFilter;

  @ViewChild("profileSelectTemplate")
  profileSelectTemplate!: TemplateRef<any>;

  @ViewChild("updateButtonsActions")
  updateButtonsActions!: TemplateRef<any>;

  requestForm:UntypedFormGroup=new UntypedFormGroup({
    requestType: new UntypedFormControl(null,[Validators.required]),
    isDelegated: new UntypedFormControl('false',[Validators.required]),
    requesterProfile: new UntypedFormControl(null,[requiredIfValidator(()=>this.createForm.isDelegated.value === 'true')]),
    requesterSearch: new UntypedFormControl(null),
    description:new UntypedFormControl(null,[
      requiredIfValidator(()=>(
        (this.createForm.requestType.value === this.requestMetaData.type.quit && this.createForm.isDelegated.value === 'false') 
        || this.createForm.requestType.value === this.requestMetaData.type.other)),
      Validators.minLength(10),
      Validators.maxLength(500)]),
    doComeBack:new UntypedFormControl(null,[
      requiredIfValidator(()=>
      this.createForm.requestType.value === this.requestMetaData.type.quit 
      && this.createForm.isDelegated.value === 'false'
      )
    ]),
    getUpdate:new UntypedFormControl(null,[
      requiredIfValidator(()=>
      this.createForm.requestType.value === this.requestMetaData.type.quit
      && this.createForm.isDelegated.value === 'false'
      )
    ]),
    rejoinDate:new UntypedFormControl(null,[
      requiredIfValidator(()=>this.createForm.requestType.value === this.requestMetaData.type.intermission)
    ]),
    continueDonation:new UntypedFormControl('false',[
      requiredIfValidator(()=>this.createForm.requestType.value === this.requestMetaData.type.intermission)
    ]),
    suggession:new UntypedFormControl(null,[Validators.minLength(10),Validators.maxLength(500)]),
    memberFirstName:new UntypedFormControl('',[
      requiredIfValidator(()=>this.createForm.requestType.value === this.requestMetaData.type.join),
      Validators.minLength(2),Validators.maxLength(50)
    ]),
    memberLastName:new UntypedFormControl('',[
      requiredIfValidator(()=>this.createForm.requestType.value === this.requestMetaData.type.join),
      Validators.minLength(2),Validators.maxLength(50)
    ]),
    memberEmail:new UntypedFormControl('',[
      requiredIfValidator(()=>this.createForm.requestType.value === this.requestMetaData.type.join),
      Validators.email
    ]),
    memberPhoneCode:new UntypedFormControl('91',[
      requiredIfValidator(()=>this.createForm.requestType.value === this.requestMetaData.type.join),
     
    ]),
    memberMobile:new UntypedFormControl('',[
      requiredIfValidator(()=>this.createForm.requestType.value === this.requestMetaData.type.join),
      phoneNumberValidator('Mobile number', 'memberPhoneCode')
    ]),
    memberHometown:new UntypedFormControl('',[
      requiredIfValidator(()=>this.createForm.requestType.value === this.requestMetaData.type.join),
      Validators.minLength(2),Validators.maxLength(100)
    ]),
    memberReasonForJoining:new UntypedFormControl('',[
      Validators.minLength(5),Validators.maxLength(200)
    ]),
    memberHowKnowNabarun:new UntypedFormControl('',[
      Validators.minLength(5),Validators.maxLength(200)

    ]),
  });
  allOtherProfiles!: ProfileObject[];
  showInputBox:boolean=true;
  referanceData: any;

  constructor(
    private title:Title,
    private location : Location,
    private authUserService:AuthUserService,
    private requestService: RequestsService,
    private profileService:ProfileService,
    private router:Router,
    private modal: ModalService,

  ) {    
}

  ngOnInit(): void {    
    HelperService.setPageTitle(this.title,"Create Request");

    this.requestService.getReferenceData().subscribe(data => {
      this.referanceData=data;
    })

    this.requestForm.controls.isDelegated.valueChanges.subscribe(value=>{
      //console.log(typeof value)
      if(value === 'true'){
        this.profileService.getAllProfiles({roleRequired:false}).subscribe(data=>{
          this.allOtherProfiles=data.filter((f: { userId: string; })=>f.userId !== this.authUserService.getUserId());
          this.showInputBox=true;
        })
      }else{
        //this.requesterProfile=undefined;
        this.createForm.requesterProfile.reset();
        this.showInputBox=false;
        this.createForm.requesterSearch.reset();
      }
      this.updateValidity();
      this.reset();
    })
    this.requestForm.controls.requestType.valueChanges.subscribe(value=>{
      this.reset();
    })
  }

  private updateValidity(){
    this.requestForm.controls.requesterProfile.updateValueAndValidity();
      this.requestForm.controls.description.updateValueAndValidity();
      this.requestForm.controls.doComeBack.updateValueAndValidity();
      this.requestForm.controls.getUpdate.updateValueAndValidity();
      this.requestForm.controls.rejoinDate.updateValueAndValidity();
      this.requestForm.controls.continueDonation.updateValueAndValidity();
      this.requestForm.controls.suggession.updateValueAndValidity();
  }
  private reset(){
    this.requestForm.controls.description.reset();
      this.requestForm.controls.doComeBack.reset();
      this.requestForm.controls.getUpdate.reset();
      this.requestForm.controls.rejoinDate.reset();
      this.requestForm.controls.continueDonation.reset('false');
      this.requestForm.controls.suggession.reset();
  }
  
  get createForm(){
    return this.requestForm.controls;
  }

  back(isdeleteged?:boolean){
    if(isdeleteged){
      this.router.navigate([this.appRoutes.requests.url],{state:{delegated:isdeleteged}});

    }else{
      this.router.navigate([this.appRoutes.requests.url]);
    }
  }


  selectProfile(profile:ProfileObject){
    //this.requesterProfile=profile;
    this.createForm.requesterProfile.setValue(profile);
    this.showInputBox=false;
  }

  isRequiredField(controlName:string) :boolean{
    //this.requestForm.controls[controlName].updateValueAndValidity();
    return this.requestForm.controls[controlName].hasValidator(Validators.required)
    || this.requestForm.controls[controlName].errors?.required;
  }

  raiseRequest(){
    console.log(this.requestForm.controls)

    if(this.requestForm.valid){
      let modal=this.modal.openNotificationModal('Create Request','Are you sure to create this request?',AlertType.warning,AlertMode.confirmation);
      modal.componentInstance.onSubmit.subscribe(response=>{
        if(response == true){
         let reqObj=new RequestObject;
         reqObj.type=this.requestForm.value.requestType;
         reqObj.requestDetails=this.requestForm.value.description;
         if(this.requestForm.value.requestType === this.requestMetaData.type.join){
          reqObj.delegated=true;
          let joinee=new JoineeDetails;
          joinee.firstName=this.requestForm.value.memberFirstName;
          joinee.lastName=this.requestForm.value.memberFirstName;
          joinee.email=this.requestForm.value.memberEmail;
          joinee.dialCode=this.requestForm.value.memberPhoneCode;
          joinee.mobileNumber=this.requestForm.value.memberMobile;
          joinee.hometown=this.requestForm.value.memberHometown;
          joinee.reasonForJoining=this.requestForm.value.memberReasonForJoining;
          reqObj.joinerDetails=joinee;
         }else{
          reqObj.delegated=this.requestForm.value.isDelegated==='true'?true:false;
         }
         reqObj.getNotificationPostQuit=this.requestForm.value.getUpdate;
         reqObj.suggession=this.requestForm.value.suggession;
         reqObj.rejoinDatePostIntermission=this.requestForm.value.rejoinDate;
         reqObj.rejoinDecision=this.requestForm.value.doComeBack;
         reqObj.requesterProfile=this.requestForm.value.requesterProfile;
         reqObj.continueDonationInIntermission=this.requestForm.value.continueDonation==='true'?true:false;
         if(reqObj.type === this.requestMetaData.type.quit){
            reqObj.url=window.location.origin+this.appRoutes.feedback.url;
         }
         this.requestService.createRequest(reqObj).subscribe(data=>{
           this.back(data.delegated);
         })
        }
        modal.close();
      });
    }else{
      this.requestForm.markAllAsTouched();



      
    }
  }

  shouldDisplay(code:string){
    return !(this.requestMetaData.type.join == code);
  }

  tooltipText(code:string){
    switch(code){
      case this.requestMetaData.type.intermission:
        return 'Take a break from NABARUN for certain duration of time then back again.';
      case this.requestMetaData.type.quit:
        return 'Permanently detach from NABARUN.';
      case this.requestMetaData.type.other:
        return 'Request for any assistance.';
      default:
        return null;
    }
  }
 

}
