import { Component, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { Title } from '@angular/platform-browser';
import { OAuthService } from 'angular-oauth2-oidc';
import { ModalService } from 'src/app/services/modal/modal.service';
import { PublicApiService } from 'src/app/services/resource/public/public-api.service';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.scss']
})
export class ForgotPasswordComponent implements OnInit {

  constructor(private titleService: Title,
    private authService: OAuthService,
    private passwordService: PublicApiService,
    private dialog: ModalService,

  ) {
  }


  passwordRecovaryForm = new UntypedFormGroup({
    email: new UntypedFormControl('', [Validators.required, Validators.email]),
  });

  get f() {
    return this.passwordRecovaryForm.controls;
  }

  onSubmit(): void {
    if (this.passwordRecovaryForm.valid) {
      this.passwordService.initPasswordResovary(this.passwordRecovaryForm.value.email)
        .subscribe((message: string) => {
          this.passwordRecovaryForm.reset();
          // let modal = this.dialog.openNotificationModal('Success', message, AlertType.info, AlertMode.notification, { okayButtonText: 'Okay' });
          // modal.componentInstance.onSubmit.subscribe(a => {
          //   modal.close();
          // })

        })
    } else {
      Object.keys(this.passwordRecovaryForm.controls).forEach(field => {
        const control = this.passwordRecovaryForm.get(field);
        control?.markAsTouched({ onlySelf: true });

      });
    }

  }

  login() {
    this.authService.initCodeFlow();
  }



  ngOnInit(): void {
    this.titleService.setTitle(this.titleService.getTitle().split("-")[0] + " - " + "Forgot Password");
  }

}
