import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { approutes } from 'src/app/constants/routes.config';
import { EventsObject } from 'src/app/models/events/event.model';
import { HelperService, isEmpty } from 'src/app/services/utils/helper.service';

@Component({
  selector: 'app-event-see-more',
  templateUrl: './event-see-more.component.html',
  styleUrls: ['./event-see-more.component.scss']
})
export class EventSeeMoreComponent implements OnInit {
  event!: EventsObject;
  appRoutes=approutes;
  constructor(
    private location: Location,
    private router: Router,
    private titleService: Title,

  ) { 
    //this.titleService.setTitle(this.titleService.getTitle().split("-")[0] + " - " + "Events");
    HelperService.setPageTitle(this.titleService,'Events');
  }

  ngOnInit(): void {
    let stateData=this.location.getState() as any;
    if(stateData.eventData){
      this.event=stateData.eventData;
    }else{
      this.router.navigate([approutes.events.url]);
    }
  }

  getEventPictureAtIndex(index:number){
    if(this.event.pictures && this.event.pictures[index]){
      return this.event.pictures[index].url;
    }
    return null;
  }

}
