import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { resourceEndpointConfig } from 'src/app/constants/resource-endpoint.config';
import { ExpenseObject } from 'src/app/models/earning-and-expense/expense.model';
import { AttachmentObject, EventsObject } from 'src/app/models/events/event.model';
import { HistoryObject } from 'src/app/models/history/history.model';
import { isEmpty } from '../../utils/helper.service';

@Injectable({
  providedIn: 'root'
})
export class EventsService {
  getEventHistory(id: string): Observable<HistoryObject[]> {
    return this.httpClient.get(resourceEndpointConfig.events.history.replace('${id}', id))
      .pipe(
        map((data: any) => {
          return data.responsePayload;
        })
      );
  }

  constructor(private httpClient: HttpClient,) { }


  getEventsInternal(): Observable<EventsObject[]> {
    const headers = new HttpHeaders()
      .set('Content-Type', 'application/json')

    return this.httpClient.get(resourceEndpointConfig.events.eventsEndpointInternal.replace('/${id}', ''), { headers: headers })
      .pipe(
        map((data: any) => {
          return data.responsePayload;
        })
      );
  }

  getEvent(id: string): Observable<EventsObject> {
    const headers = new HttpHeaders()
      .set('Content-Type', 'application/json')
    return this.httpClient.get(resourceEndpointConfig.events.eventsEndpointInternal.replace('${id}', id), { headers: headers })
      .pipe(
        map((data: any) => {
          return data.responsePayload;
        })
      );
  }

  updateEvent(id: string, eventObject: EventsObject): Observable<EventsObject> {
    const headers = new HttpHeaders()
      .set('Content-Type', 'application/json')
    return this.httpClient.patch(resourceEndpointConfig.events.eventsEndpointInternal.replace('${id}', id), eventObject, { headers: headers })
      .pipe(
        map((data: any) => {
          return data.responsePayload;
        })
      );
  }

  updateEventExpences(id: string, expenseObject: ExpenseObject[]): Observable<any> {
    const headers = new HttpHeaders()
      .set('Content-Type', 'application/json')
    return this.httpClient.post(resourceEndpointConfig.events.eventsExpenditureEndpoint.replace('${id}', id), expenseObject, { headers: headers });
  }


  createEvent(body: EventsObject): Observable<EventsObject> {
    const headers = new HttpHeaders()
      .set('Content-Type', 'application/json')

    return this.httpClient.post(resourceEndpointConfig.events.eventsEndpointInternal.replace('/${id}', ''), body, { headers: headers })
      .pipe(
        map((data: any) => {
          return data.responsePayload;
        })
      );
  }

  addOrChangeEventCoverPicture(id: string, file: File): Observable<any> {
    let headers = new HttpHeaders()
    //this is the important step. You need to set content type as null
    // headers.set('Content-Type', null);
    headers.set('Accept', "multipart/form-data");
    let params = new HttpParams()
    params.set('eventCoverPicture', true);
    console.log(file.name)
    const formData: FormData = new FormData();
    formData.append('eventCoverPicture', file, file.name);

    return this.httpClient.post(resourceEndpointConfig.events.eventsCoverPictureEndpoint.replace('${id}', id), formData, { params, headers: headers });

  }


  addEventAttachment(id: string, isImage: boolean, files: File[]): Observable<any> {
    let headers = new HttpHeaders()
    //this is the important step. You need to set content type as null
    // headers.set('Content-Type', null);
    headers.set('Accept', "multipart/form-data");
    const formData: FormData = new FormData();
    files.forEach(file => {
      formData.append('file', file, file.name);
    })
    // let params = new HttpParams()
    // params.set('isPicture',isImage);
    return this.httpClient.post(resourceEndpointConfig.events.eventsAttachemntEndpoint.replace('${id}', id), formData, { params: { isPicture: isImage }, headers: headers });

  }
  updateEventAttachment(id: string, existing: AttachmentObject[], isImage: boolean): Observable<any> {
    const headers = new HttpHeaders()
      .set('Content-Type', 'application/json');

    return this.httpClient.patch(resourceEndpointConfig.events.eventsAttachemntEndpoint.replace('${id}', id), existing, { params: { isPicture: isImage }, headers: headers });

  }

  viewAttachment(id: string, viewOnly: boolean): Observable<any> {
    if (viewOnly) {
      const headers = new HttpHeaders()
        .set('Content-Type', 'application/json');
      return this.httpClient.get(resourceEndpointConfig.events.viewAttachmentEndpoint.replace('${id}', id), { headers: headers,params:{viewOnly:true} })
        .pipe(
          map((data: any) => {
            return data.responsePayload;
          })
        );
    }
    return this.httpClient.get(resourceEndpointConfig.events.viewAttachmentEndpoint.replace('${id}', id), { responseType: 'blob' });
  }

  getDraftedEvent(): Observable<EventsObject> {
    const headers = new HttpHeaders()
      .set('Content-Type', 'application/json');
    return this.httpClient.get(resourceEndpointConfig.events.draftedEvent, { headers: headers })
      .pipe(
        map((data: any) => {
          return data.responsePayload;
        })
      );
  }

  deleteEvent(id: string, isDraft: boolean = false): Observable<any> {
    const headers = new HttpHeaders()
      .set('Content-Type', 'application/json');
    if (isDraft) {
      return this.httpClient.delete(resourceEndpointConfig.events.draftedEvent, { headers: headers, params: { eventId: id } });
    } else {
      return this.httpClient.delete(resourceEndpointConfig.events.eventsEndpointInternal.replace('${id}', id), { headers: headers });
    }
  }

}
