import { AttachmentObject, EventsObject } from "../events/event.model";
import { HistoryObject } from "../history/history.model";

export class ProfileContribution {
    id!: string;
    avatarUrl!: string;
    email!: string;
    firstName!: string;
    middleName!: string;
    lastName!: string;
    contributions!:Contributions[];
    searchFilter!: string;
    comprehensiveContributionSummary!:ComprehensiveContributionSummary;
    status!: string;

}

export class ComprehensiveContributionSummary{
    totalOutstanding!:number;
    totalOutstandingAmount!:number;
    totalPaidAmount!:number;
    outstandingContributions!:OutstandingContribution[]; 
    outstandingMonths!:string[]; 
    totalAvailableFunds!:number;
    totalFundsTillDate!:number;
    currentMonthDonation!:number;
    currentMonthExpense!:number;

}

export class OutstandingContribution{
    amount!:number;
    contributionStatus!:string;
    contributionType!:string;
    endDate!:Date;
    startDate!:Date;
}


export class Contributions{
    id!:string;
    amount!: number;
    comment!: string;
    confirmedBy!: string;
    confirmedOn!: string;
    contributionId!: string;
    contributionStatus!: string;
    contributionType!: string;
    contributor!:string;
    endDate!: string;
    paidOn!: string;
    raisedOn!: string;
    startDate!: string;
    profile!:ProfileContribution;
    guestFullName!: string;
    guestEmail!: string;
    guestMobile!: string;
    history!: HistoryObject[];
    documents!:AttachmentObject[];
    paymentMethod!: string;
    isGuest!:boolean;
    event!: EventsObject;
    paidToAccount!:string;
}



