import { Pipe, PipeTransform } from '@angular/core';
import { NoticeObject } from 'src/app/models/notices/notice.model';

@Pipe({
  name: 'noticeSearchFilter'
})
export class NoticeSearchFilterPipe implements PipeTransform {

  transform(notices: NoticeObject[], search: string): any {
    //console.log(search,notices)
    if(!notices || !search){
      return notices;
    }
    
    return notices.filter((notice:NoticeObject)=>
    (notice.noticeNumber && notice.noticeNumber.toString().toLowerCase().includes(search.toString().toLowerCase())) ||
    (notice.id && notice.id.toString().toLowerCase().includes(search.toString().toLowerCase())) ||
    (notice.title && notice.title.toString().toLowerCase().includes(search.toString().toLowerCase())) ||
    (notice.description && notice.description.toString().toLowerCase().includes(search.toString().toLowerCase()))
    );
  }

}
