import { HttpClient } from '@angular/common/http';
import { Component, EventEmitter, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { FormArray, UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { Title } from '@angular/platform-browser';
import { JsonEditorOptions } from '@maaxgr/ang-jsoneditor';
import { AlertMode, AlertType } from 'src/app/components/modal/notification-modal/notification-modal.component';
import { staticUrls } from 'src/app/constants/admin.constants';
import { SCOPE } from 'src/app/constants/auth-scope.constats';
import { HISTORY } from 'src/app/constants/history.constants';
import { resourcePathVars } from 'src/app/constants/resource-endpoint.config';
import { approutes } from 'src/app/constants/routes.config';
import { Configuration, TrashObject } from 'src/app/models/admin-console/admin.model';
import { Contributions } from 'src/app/models/contributions/contributions.model';
import { EarningObject } from 'src/app/models/earning-and-expense/earning.model';
import { ExpenseObject } from 'src/app/models/earning-and-expense/expense.model';
import { ChangeObject } from 'src/app/models/history/history.model';
import { NoticeObject } from 'src/app/models/notices/notice.model';
import { ModalService } from 'src/app/services/modal/modal.service';
import { ApplicationMonitoringService } from 'src/app/services/resource/internal/application-monitoring.service';
import { ContributionService } from 'src/app/services/resource/internal/contribution.service';
import { EventsService } from 'src/app/services/resource/internal/events.service';
import { FundsAndExpenditureService } from 'src/app/services/resource/internal/funds-and-expenditure.service';
import { NoticeService } from 'src/app/services/resource/internal/notice.service';
import { AuthUserService } from 'src/app/services/utils/auth-user.service';
import { buildHistoryMessage, HelperService, historyHeader, openWindow, saveAs } from 'src/app/services/utils/helper.service';

@Component({
  selector: 'app-application-monitoring',
  templateUrl: './application-monitoring.component.html',
  styleUrls: ['./application-monitoring.component.scss']
})
export class ApplicationMonitoringComponent implements OnInit {

  actuators: any[] = [];
  logFiles: any[]= [];
  searchValue!: string;
  appRoutes=approutes;
  trashData!: TrashObject;
  trashedContributionSearch!:string;
  trashedEarningsSearch!:string;
  trashedExpendituresSearch!:string;
  trashedEventsSearch!:string;
  trashedNoticeSearch!:string;
  trashName=resourcePathVars.names;
  tabIndex:number=0;
  urls=staticUrls;
  @ViewChild("updateActionbuttons")
  updateActionbuttons!: TemplateRef<any>;

  @ViewChild("historyTemplate")
  historyTemplate!: TemplateRef<any>;
  history=HISTORY;
  canViewFundsHistory!: boolean;
  canViewDonationHistory!: boolean;
  canViewEventsHistory!: boolean;
  canViewExpenseHistory!: boolean;
  canViewNoticeHistory!: boolean;
  isInactiveUser!: boolean;
  configs!: Configuration[];
  configUpdateForm!: UntypedFormGroup;
  canViewConfig!: boolean;
  canUpdateConfig!: boolean;
  configGroups: any;

  historyHeader=historyHeader;
  historyMessage=buildHistoryMessage;


  constructor(
    private applicationMonitoringService: ApplicationMonitoringService,
    private title: Title,
    private modal:ModalService,
    private contributionService: ContributionService,
    private noticeService:NoticeService,
    private fundsAndExpenditureService : FundsAndExpenditureService,
    private eventsService:EventsService,
    private authUserService : AuthUserService,


    ) { 
      HelperService.setPageTitle(this.title,"Admin Console");

    }

  
    

  ngOnInit(): void {
    this.isInactiveUser=this.authUserService.isUserInactive();
    // need to amend more access spec
    this.canViewDonationHistory=this.authUserService.isAccrediatedTo(SCOPE.read.donation_history);
    this.canViewFundsHistory=this.authUserService.isAccrediatedTo(SCOPE.read.fund_history);
    this.canViewEventsHistory=this.authUserService.isAccrediatedTo(SCOPE.read.event_history);
    this.canViewExpenseHistory=this.authUserService.isAccrediatedTo(SCOPE.read.expense_history);
    this.canViewNoticeHistory=this.authUserService.isAccrediatedTo(SCOPE.read.notice_history);

    this.canViewConfig=this.authUserService.isAccrediatedTo(SCOPE.read.config);
    this.canUpdateConfig=this.authUserService.isAccrediatedTo(SCOPE.update.config);


    this.refresh();

  }

  refresh(){
    this.selectedTab(this.tabIndex);
  }


  selectedTab(tabIndex: number) {
    this.tabIndex=tabIndex;
    this.searchValue='';
    if (tabIndex === 0) {
      //this.getActuatorEndpoints();
    }else if (tabIndex === 1) {
      this.applicationMonitoringService.getAllTrash().subscribe(data=>{
        this.trashData=data;
      })
    }else if (tabIndex === 2) {
      this.applicationMonitoringService.getConfig().subscribe(data=>{
        this.configs=data.configurations;
        this.configGroups=data.configGroups;
      })
    }

  }

  getActuatorEndpoints() {
    this.applicationMonitoringService.getActuatorEndPoints()
      .subscribe((response: any) => {
        let keys = Object.keys(response._links);
        this.actuators = [];
        keys.forEach(key => {
          let object = response._links[key];
          if (!object.templated) {
            object.key = key;
            object.response = null;
            this.actuators.push(object);
          }
        })
      });
  }

  getActuatorResponse(actuator: any) {
    this.applicationMonitoringService.callActuatorEndPoints(actuator.href)
      .subscribe((response: any) => {
        let index = this.actuators.findIndex((act) => act.key === actuator.key);
        this.actuators[index].response = response;
      });

  }

  downloadContributions(){
    this.applicationMonitoringService.exportContributions().subscribe(data=>{
      const blob = new Blob([data], {
        type: 'text/csv',
      });
      saveAs(blob,'Donations-'+new Date()+'.csv')
    })
  }

  delete(name:string,id:string){
    let dialog=this.modal.openNotificationModal('Confirm Delete','Are you sure to delete this '+name+' permanently?',AlertType.warning,AlertMode.confirmation)
    dialog.componentInstance.onSubmit.subscribe(decision=>{
      if(decision){
        this.applicationMonitoringService.deleteTrash(name,id).subscribe(data=>{
          this.refresh();
        })
      }
      dialog.close();
    })
  }

  restore(name:string,id:string){
  let dialog=this.modal.openNotificationModal('Confirm Restore','Are you sure to restore this '+name+'?',AlertType.warning,AlertMode.confirmation)
    dialog.componentInstance.onSubmit.subscribe(decision=>{
      if(decision){
        this.applicationMonitoringService.restoreTrash(name,id).subscribe(data=>{
          this.refresh();
        })
      }
      dialog.close();
    })

  }

  viewAttachment(id:string){
    this.eventsService.viewAttachment(id,true).subscribe(data=>{
      openWindow(data);
    })
  }
  
  downloadAttachment(id:string,fileName:string){
    this.eventsService.viewAttachment(id,false).subscribe(data=>{
      saveAs(data,fileName);
    })
  }

  donationHistory(contribution: Contributions){
    this.contributionService.getHistory(contribution.contributionId).subscribe(data=>{
      this.modal.openDialog({
        body: {
          template: this.historyTemplate,
          context:{
            histories:data,
            name: 'Donation'
          }
        },
        headers: {
          headerText:'View Changes'
        },
        action: { buttonTemplate: this.updateActionbuttons, buttonContext: { buttons: { cancel: 'Close'} } },
        dimention: { width: 800 }
      });
    })
  }

  showNoticeHistory(data:NoticeObject){
    this.noticeService.getNoticeHistory(data.id).subscribe(data=>{
      this.modal.openDialog({
        body: {
          template: this.historyTemplate,
          context:{
            histories:data,
            name: 'Notice'
          }
        },
        headers: {
          headerText:'View Changes'
        },
        action: { buttonTemplate: this.updateActionbuttons, buttonContext: { buttons: { cancel: 'Close'} } },
        dimention: { width: 800 }
      });
    })
  }

  showEarningHistory(data:EarningObject){
    this.fundsAndExpenditureService.getEarningHistory(data.id).subscribe(data=>{
      this.modal.openDialog({
        body: {
          template: this.historyTemplate,
          context:{
            histories:data,
            name: 'Fund source'
          }
        },
        headers: {
          headerText:'View Changes'
        },
        action: { buttonTemplate: this.updateActionbuttons, buttonContext: { buttons: { cancel: 'Close'} } },
        dimention: { width: 800 }
      });
    })
  }

  showExpenseHistory(data:ExpenseObject){
    this.fundsAndExpenditureService.getExpenseHistory(data.id).subscribe(data=>{
      this.modal.openDialog({
        body: {
          template: this.historyTemplate,
          context:{
            histories:data,
            name: 'Expense'
          }
        },
        headers: {
          headerText:'View Changes'
        },
        action: { buttonTemplate: this.updateActionbuttons, buttonContext: { buttons: { cancel: 'Close'} } },
        dimention: { width: 800 }
      });
    })
  }

  showEventHistory(id:string){
    this.eventsService.getEventHistory(id).subscribe(data=>{
      this.modal.openDialog({
        body: {
          template: this.historyTemplate,
          context:{
            histories:data,
            name: 'Event'
          }
        },
        headers: {
          headerText:'View Changes'
        },
        action: { buttonTemplate: this.updateActionbuttons, buttonContext: { buttons: { cancel: 'Close'} } },
        dimention: { width: 800 }
      });
    })
  }


  @ViewChild("updateTemplate")
  updateTemplate!: TemplateRef<any>;

  

  modifyConfig(config:Configuration){
    let updateConfig = new EventEmitter();
    let editorOptions = new JsonEditorOptions()
    editorOptions.modes =['code', 'tree']; // set all allowed modes
    //this.editorOptions.mode = 'code'; 


    this.configUpdateForm= new UntypedFormGroup({
      configGroup:new UntypedFormControl({value:config.parameter_group,disabled:true},[]),
      configName:new UntypedFormControl({value:config.parameter_name,disabled:true},[]),
      configType:new UntypedFormControl({value:config.parameter_type,disabled:true}),
      configValue:new UntypedFormControl(config.parameter_value,[Validators.required]),
      configDescription:new UntypedFormControl(config.parameter_description)
    })

    
    let modalA = this.modal.openDialog({
      body: {
        template: this.updateTemplate,
        context:{config: this.configUpdateForm.value,editorOptions:editorOptions}
      },
      headers: {
        headerText: 'Update Config'
      },
      action: { buttonTemplate: this.updateActionbuttons, buttonContext: { emitObject:updateConfig, buttons: { cancel: 'Cancel', update: 'Update' } } },
      dimention: { width: 700 }
    });

    updateConfig.subscribe((decision: boolean) => {
      if (decision === true) {
        if(this.configUpdateForm.valid){
          let dialog=this.modal.openNotificationModal('Confirm','Are you sure to update this property?',AlertType.warning,AlertMode.confirmation)
          dialog.componentInstance.onSubmit.subscribe(decision=>{
          if(decision){
            config.parameter_value=this.configUpdateForm.value.configValue;
            config.parameter_description=this.configUpdateForm.value.configDescription;
            this.applicationMonitoringService.updateConfig(config).subscribe(da=>{
              //this.refresh();
            })
          }
          modalA.close();
          dialog.close();
        })
        }
      }
    })
  }

  
  addConfig(){
    let updateConfig = new EventEmitter();
    let editorOptions = new JsonEditorOptions()
    //editorOptions.modes =['code', 'tree']; // set all allowed modes
    editorOptions.mode = 'code'; 

    this.configUpdateForm= new UntypedFormGroup({
      configGroup:new UntypedFormControl('',[Validators.required]),
      configName:new UntypedFormControl('',[Validators.required]),
      configType:new UntypedFormControl('',[Validators.required]),
      configValue:new UntypedFormControl('',[Validators.required]),
      configDescription:new UntypedFormControl('')
    })
    
    let modalA = this.modal.openDialog({
      body: {
        template: this.updateTemplate,
        context:{config:this.configUpdateForm.value,editorOptions:editorOptions}
      },
      headers: {
        headerText: 'Add Config '
      },
      action: { buttonTemplate: this.updateActionbuttons, buttonContext: { emitObject:updateConfig, buttons: { cancel: 'Cancel', update: 'Add' } } },
      dimention: { width: 700 }
    });

    updateConfig.subscribe((decision: boolean) => {
      if (decision === true) {
        if(this.configUpdateForm.valid){
          let dialog=this.modal.openNotificationModal('Confirm','Are you sure to add this property?',AlertType.warning,AlertMode.confirmation)
          dialog.componentInstance.onSubmit.subscribe(decision=>{
          if(decision){
            let config = new Configuration()
            config.parameter_value=this.configUpdateForm.value.configValue;
            config.parameter_description=this.configUpdateForm.value.configDescription;
            config.parameter_name=this.configUpdateForm.value.configName;
            config.parameter_type=this.configUpdateForm.value.configType;
            config.parameter_group=this.configUpdateForm.value.configGroup;
            this.applicationMonitoringService.updateConfig(config).subscribe(da=>{
              this.refresh();
            })
          }
          modalA.close();
          dialog.close();
      })
        }
        
      }
    })
  }    
    


  beautifyJson(value:any){
    return JSON.stringify(value, undefined, 4);
  }

  parseJsonString(value:string){
    return JSON.parse(value);
  }

}
