import { AfterViewInit, Component, ElementRef, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { AlertMode, AlertType, NotificationModalComponent } from '../modal/notification-modal/notification-modal.component';
import { OAuthService } from 'angular-oauth2-oidc';
import { auth0LogoutConfig, environment } from 'src/environments/environment';
import { approutes } from 'src/app/constants/routes.config';
import { ModalService } from 'src/app/services/modal/modal.service';
import { AuthUserService } from 'src/app/services/utils/auth-user.service';
import { ProfileService } from 'src/app/services/resource/internal/profile.service';
import { Router } from '@angular/router';
// import { AngularFireAuth } from '@angular/fire/auth';


@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  profile: any = {};
  @Input('showDashboardNav')
  showDBNav!: string;
  authUserAvatarUrl!: string;
  //authUser!: any;
  appRoutes = approutes;
  constructor(
    private oauth2Service: OAuthService,
    private modalService: ModalService,
    private authUserService: AuthUserService,
    private profileService: ProfileService,
    private router: Router,
    // private auth: AngularFireAuth

  ) { }

  @Input('showNav') showNav: string | undefined;

  ngOnInit(): void {

    if (this.oauth2Service.hasValidAccessToken() && this.oauth2Service.hasValidIdToken() /*&& this.showNav!="true" */) {
      this.authUserAvatarUrl = this.authUserService.getAvatarUrl();
      // this.notificationService.requestPermission();
      // this.notificationService.receiveMessage();
      // this.notificationService.currentMessage;
    }

  }

 

  visitMyProfile() {
    this.profileService.getReferenceData().subscribe(referenceData => {
      this.profileService.getMyProfile({ roleRequired: true }).subscribe(data => {
        this.router.navigate([approutes.myprofile.url], { state: { myProfile: data, profileReferenceData: referenceData } });
      })
    })
  }

  isProfileOptVisible() {
    return this.oauth2Service.hasValidAccessToken()
      //&& this.oauth2Service.hasValidIdToken()
      && this.showNav != "true";
  }

  isNavVisible() {
    if (this.showNav == "true") {
      return true;
    }
    return false;
  }

  isDashboardNavVisible() {
    if (this.showNav == 'true' || this.showDBNav == "false") {
      return false;
    }
    return true;
  }

  login() {
    this.oauth2Service.initLoginFlow()
    //this.oauth2Service.initCodeFlow();
  }

  logout() {
    let modal = this.modalService.openNotificationModal(
      'Confirm Logout',
      'Are you sure that you want to logged out from your current session?', AlertType.warning,
      AlertMode.confirmation);

    modal.componentInstance.onSubmit.subscribe(action => {
      if (action == true) {
        // this.notificationService.deleteToken();
        this.oauth2Service.revokeTokenAndLogout(auth0LogoutConfig).then(res=>{
          this.authUserService.clearUserCache();
        }).finally(()=>{
          // this.auth.signOut().finally(()=>{console.log('Firebase user signned out')});
        })
      }
      modal.close();
    });
  }

}
