import { Pipe, PipeTransform } from '@angular/core';
import { RequestObject } from 'src/app/models/request/requests.model';
import { AuthUserService } from 'src/app/services/utils/auth-user.service';

@Pipe({
  name: 'requestSearch'
})
export class RequestSearchPipe implements PipeTransform {

  constructor(private authUserService:AuthUserService){}

  transform(requests: RequestObject[], search: string,index:number): any {
    //input 0 to index to ignore index filtering
    //input 1 to index to get authenticated users requests
    //input 2 to index to get other users requests
    //input null to index to ignore search filtering

    if(!requests){
      return requests;
    }

    if(index == 1){
      requests= requests.filter((request:RequestObject)=>this.authUserService.getUserId() === request.requesterUserId);
    } else if(index == 2){
      requests= requests.filter((request:RequestObject)=>this.authUserService.getUserId() !== request.requesterUserId);
     }


     if(!search){
      return requests;
    }

    return requests.filter((request:RequestObject)=> 
    request.requestName && request.requestName.toLowerCase().includes(search.toLowerCase())  
      ||
      request.requestNumber && request.requestNumber.toLowerCase().includes(search.toLowerCase())
      ||
      request.requesterName &&  request.requesterName.toLowerCase().includes(search.toLowerCase())
      ||
      request.status && request.status.toLowerCase().includes(search.toLowerCase())
      ||
      request.createdOn && request.createdOn.toLowerCase().includes(search.toLowerCase())
    );

    //
  }

}

