import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { SCOPE } from 'src/app/constants/auth-scope.constats';
import { PROFILE } from 'src/app/constants/profile.constants';
import { approutes } from 'src/app/constants/routes.config';
import { ProfileObject, Roles } from 'src/app/models/user/profile.model';
import { ProfileService } from 'src/app/services/resource/internal/profile.service';
import { AuthUserService } from 'src/app/services/utils/auth-user.service';
import { HelperService } from 'src/app/services/utils/helper.service';



@Component({
  selector: 'app-members',
  templateUrl: './members.component.html',
  styleUrls: ['./members.component.scss']
}) 
export class MembersComponent implements OnInit {
  allProfiles!: ProfileObject[];
  searchValue!:string;
  appRoutes=approutes;
  profileMetadata=PROFILE;
 // authUser!: AuthUser;
  canUpdateOtherProfile!: boolean;
  canReadOtherProfile!: boolean;
  //canDeleteProfile!: boolean;
  constructor(
    private profileService:ProfileService,
    private title:Title,
    private router : Router,
    private location : Location,

    private authUserService:AuthUserService,
   // private authService:OAuthService,
  ) { 
    HelperService.setPageTitle(this.title,"Members");

  }

  ngOnInit(): void {
    //this.authUser=new AuthUser(this.authService.getIdentityClaims(),this.authService.getGrantedScopes());
    this.canUpdateOtherProfile=this.authUserService.isAccrediatedToAny(SCOPE.update.other_profile_info,SCOPE.update.volunteer_donation_attributes,SCOPE.update.volunteer_admin_attributes)
    this.canReadOtherProfile=this.authUserService.isAccrediatedTo(SCOPE.read.other_profile_info)
   // this.canDeleteProfile=this.authUserService.isAccrediatedTo(SCOPE.delete.user_profile)
    let stateData=this.location.getState() as any;
    if(!stateData.allProfiles){
     this.refresh();
    }else{
      this.allProfiles=stateData.allProfiles;
    }
    
  }

  refresh(){
    this.profileService.getAllProfiles({roleRequired:true}).subscribe(data=>{
      this.allProfiles=data;
    })
  }


  visitProfile(id:string) {
    this.profileService.getReferenceData().subscribe(referenceData => {
      this.profileService.getProfile(id).subscribe(data=>{
        this.router.navigate([approutes.profile.url], { queryParams: { action:  this.canUpdateOtherProfile ?this.profileMetadata.action.viewOrEdit :this.profileMetadata.action.view } ,state:{myProfile:data,profileReferenceData:referenceData}});
      })
    })
  }

  deleteProfile(id:string){
    this.profileService.deleteProfile(id).subscribe(data=>{
      this.allProfiles.splice(this.allProfiles.findIndex(p=>p.id === id),1);
    }) 
  }

  displayRoles(roles:Roles[]){
    if(roles == null){
      return roles;
    }
    return roles.toString();
  }

  tooltipMessage=(profile:ProfileObject)=>{
    if(profile.status === this.profileMetadata.status.inactive && profile.isUnblocked === false){
      return 'This user is currently <b>Inactive</b> and <b>Blocked</b>.';
    }
    if(profile.status === this.profileMetadata.status.inactive){
      return 'This user is currently <b>Inactive</b>.';
    }
    if(profile.isUnblocked === false){
      return 'This user is currently <b>Blocked</b>.';
    }
    return null;
  }

}
