import { Component, OnInit } from '@angular/core';
import { UntypedFormArray, UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { Title } from '@angular/platform-browser';
import { AlertMode, AlertType } from 'src/app/components/modal/notification-modal/notification-modal.component';
import { approutes } from 'src/app/constants/routes.config';
import { BankingDetails, PaymentDetails, UPIDetails } from 'src/app/models/contributions/payment-details.model';
import { ModalService } from 'src/app/services/modal/modal.service';
import { PaymentDetailsService } from 'src/app/services/resource/internal/payment-details.service';
import { SettingsService } from 'src/app/services/resource/internal/settings.service';
import { AuthUserService } from 'src/app/services/utils/auth-user.service';
import { HelperService } from 'src/app/services/utils/helper.service';
import { ProfileService } from 'src/app/services/resource/internal/profile.service';
import { availableUpis } from 'src/app/constants/payment-details.constants';

@Component({
  selector: 'app-payment-details',
  templateUrl: './payment-details.component.html',
  styleUrls: ['./payment-details.component.scss']
})
export class PaymentDetailsComponent implements OnInit {

  availableUpis=availableUpis;
 
  bankingDetailsForm: UntypedFormGroup= new UntypedFormGroup({
    bankName: new UntypedFormControl('', [Validators.required,]),
    branchName: new UntypedFormControl('', [Validators.required]), 
    ifscNumber: new UntypedFormControl('', [Validators.required]),
    accountHolderName: new UntypedFormControl('', [Validators.required]),
    accountNumber: new UntypedFormControl('', [Validators.required]),
    accountType: new UntypedFormControl('', [Validators.required]),
  });

  upiDetailsForm: UntypedFormGroup =  new UntypedFormGroup({
    upiPhoneCode: new UntypedFormControl('', [Validators.required,]),
    upiNumber: new UntypedFormControl('', [Validators.required,]),
    payeeName: new UntypedFormControl('', [Validators.required,]),         
    upiName:  new UntypedFormArray([
    
    ],[Validators.required]), 
    upiId: new UntypedFormControl('', [Validators.required,]), 
  });;

  appRoutes=approutes;
  referenceData:any={};
  constructor(
    private modalService: ModalService,
    private titleService: Title,
    private paymentDetailsService:PaymentDetailsService,
    private profileService:ProfileService,


    ) { 
      HelperService.setPageTitle(this.titleService,"Payment Details");
    }

    get bankingform(){
      return this.bankingDetailsForm.controls;
    }

    get upiform(){
      return this.upiDetailsForm.controls;
    }

  ngOnInit(): void {
    this.profileService.getReferenceData().subscribe(data => {
      this.referenceData = data;
    })
    this.paymentDetailsService.getPaymentDetails().subscribe(data=>{
      this.bankingDetailsForm.patchValue({
        bankName:data.banking.bankName,
        branchName: data.banking.branchName,
        ifscNumber: data.banking.IFSCNumber,
        accountHolderName:data.banking.accountHolderName,
        accountNumber:data.banking.accountNumber,
        accountType:data.banking.accountType
      })

      this.upiDetailsForm.patchValue({
        upiPhoneCode: data.upi.UPINumberCode,
        upiNumber: data.upi.UPINumber,
        payeeName: data.upi.payeeName,
        upiId : data.upi.UPIId,
      })

      const oldNames = (this.upiform.upiName as UntypedFormArray);
      if(data.upi.UPIName){
        data.upi.UPIName.map(m=>{
          oldNames.push(new UntypedFormControl(m))
        });
      
        this.availableUpis=this.availableUpis.map(u=>{
          u.selected=data.upi.UPIName.includes(u.value);
          return u;
        })
      }
      
      

    })
      
  }

  submitBankingDetails(){
    if(this.bankingDetailsForm.valid){
      let modal = this.modalService.openNotificationModal('Confirm Submission',
        'Banking details will not be verified. Are you sure that provided information are correct ?',
        AlertType.warning, AlertMode.confirmation);
        modal.componentInstance.onSubmit.subscribe(data => {
          modal.close();
          if (data === true) {
            let bankingdetails=new BankingDetails;
            bankingdetails.IFSCNumber= this.bankingDetailsForm.value.ifscNumber;
            bankingdetails.accountHolderName= this.bankingDetailsForm.value.accountHolderName;
            bankingdetails.accountNumber= this.bankingDetailsForm.value.accountNumber;
            bankingdetails.accountType= this.bankingDetailsForm.value.accountType;
            bankingdetails.bankName= this.bankingDetailsForm.value.bankName;
            bankingdetails.branchName= this.bankingDetailsForm.value.branchName;
            let data= new PaymentDetails;
            data.banking=bankingdetails;
            this.paymentDetailsService.addOrUpdatePaymentDetails('banking',data).subscribe(response=>{
              this.ngOnInit();
            });
          }
        })  
    }else{
      Object.keys(this.bankingDetailsForm.controls).forEach(field => {
        const control = this.bankingDetailsForm.get(field);
        control?.markAsTouched({ onlySelf: true });
      });
    }

  }
  
  onCheckboxChange(event: any) {
    const selectedNames = (this.upiform.upiName as UntypedFormArray);
    if (event.target.checked) {
      console.log(event.target.value)
      selectedNames.push(new UntypedFormControl(event.target.value));
    } else {
      const index = selectedNames.controls
      .findIndex(x => x.value === event.target.value);
      selectedNames.removeAt(index);
    }
  }

  submitUPIDetails(){
    //console.log(this.upiDetailsForm.controls)
    if(this.upiDetailsForm.valid){
      let modal = this.modalService.openNotificationModal('Confirm Submission',
        'UPI details will not be verified. Are you sure that provided information are correct ?',
        AlertType.warning, AlertMode.confirmation);
        modal.componentInstance.onSubmit.subscribe(data => {
          modal.close();
          if (data === true) {
            let upidetails= new UPIDetails;
            upidetails.UPIId= this.upiDetailsForm.value.upiId;
            upidetails.UPIName= this.upiDetailsForm.value.upiName;
            upidetails.payeeName= this.upiDetailsForm.value.payeeName;
            upidetails.UPINumber= this.upiDetailsForm.value.upiNumber;
            upidetails.UPINumberCode= this.upiDetailsForm.value.upiPhoneCode;
            let data= new PaymentDetails;
            data.upi=upidetails;
            this.paymentDetailsService.addOrUpdatePaymentDetails('upi',data).subscribe(response=>{
              this.ngOnInit();
            });
          }
        })  
    }else{
      Object.keys(this.upiDetailsForm.controls).forEach(field => {
        const control = this.upiDetailsForm.get(field);
        control?.markAsTouched({ onlySelf: true });
      });
    }

  }


  clearDetails(type:string){
    //console.log(this.upiDetailsForm.controls)
    if(this.upiDetailsForm.valid){
      let modal = this.modalService.openNotificationModal('Confirm Remove',
        'Are you sure to remove details ?',
        AlertType.warning, AlertMode.confirmation);
        modal.componentInstance.onSubmit.subscribe(data1 => {
          modal.close();
          if (data1 === true) {            
            let data= new PaymentDetails;
            data.upi=new UPIDetails;
            data.banking= new BankingDetails;
            this.paymentDetailsService.addOrUpdatePaymentDetails(type,data).subscribe(response=>{
              this.ngOnInit();
              // if(type == 'upi'){
              //   this.availableUpis=availableUpis;
              // }
            });
          }
        })  
    }

  }
  
}
