import { Component, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Route } from '@angular/router';
import { AlertMode, AlertType } from 'src/app/components/modal/notification-modal/notification-modal.component';
import { REQUESTS } from 'src/app/constants/requests.constants';
import { approutes } from 'src/app/constants/routes.config';
import { RequestObject } from 'src/app/models/request/requests.model';
import { ModalService } from 'src/app/services/modal/modal.service';
import { PublicApiService } from 'src/app/services/resource/public/public-api.service';

@Component({
  selector: 'app-feedback',
  templateUrl: './feedback.component.html',
  styleUrls: ['./feedback.component.scss']
})
export class FeedbackComponent implements OnInit {
  message:string="Please wait....";
  requestMetadata=REQUESTS;
  appRoutes=approutes;
  showForm!: boolean;
  requestDetails!: RequestObject;
  constructor(
        private route: ActivatedRoute,
        private apiService: PublicApiService,
        private modal: ModalService,

        ) { }

  ngOnInit(): void {
    let params=this.route.snapshot.queryParams;
    this.apiService.getQuitRequestStatus(params['id'], params['token']).subscribe(data=>{
      if(data.length > 0){
        this.requestDetails=data[0];
        if(this.requestDetails.message){
          this.message="Hello "+this.requestDetails.requesterName+", Your response has already been recorded.";
          this.showForm=false;
        }else{
          let message="Hello "+this.requestDetails.requesterName+", Greetings form NABARUN. We found you're leaving us. No issues but we are happy to hear your experience about  the group. Hope we have spent many good or bad times together."
          this.message=message;
          this.showForm=true;
        }
        
      }else{
        this.showForm=false;
        this.message="Oops !! Something went wrong.";
      }
    })
  }


  feedbackForm = new UntypedFormGroup({
    reasonForLeave:new UntypedFormControl('',[
      Validators.required, 
      Validators.minLength(10),
      Validators.maxLength(500)
    ]),
    doComeBack:new UntypedFormControl('',[
      Validators.required,
    ]),
    getUpdate:new UntypedFormControl('',[
      Validators.required, 
    ]),
    suggession:new UntypedFormControl(''),
  });
  
  onSubmit(): void {
    //console.log(this.feedbackForm.controls)
    if (this.feedbackForm.valid) {
      let modal=this.modal.openNotificationModal('Confirm','Are you sure to submit feedback?',AlertType.warning,AlertMode.confirmation);
      modal.componentInstance.onSubmit.subscribe(response=>{
        if(response == true){
          let params=this.route.snapshot.queryParams;
          let request= new RequestObject;
          request.requestNumber=params['id'];
          request.requestDetails=this.feedbackForm.value.reasonForLeave;
          request.rejoinDecision=this.feedbackForm.value.doComeBack;
          request.getNotificationPostQuit=this.feedbackForm.value.getUpdate;
          request.suggession=this.feedbackForm.value.suggession;
          this.apiService.submitFeedback(request, params['token']).subscribe(data=>{
            this.message="Hello "+this.requestDetails.requesterName+", Thanks for your response.";
            this.showForm=false;
          })
        }
        modal.close();
      })

      
    }else {
      this.feedbackForm.markAllAsTouched();
    }
  }

  
  
  
  get f() {
    return this.feedbackForm.controls;
  }

  gotoLoginPage(){
    //this.authService.initCodeFlow();
  }

}
