import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { approutes } from 'src/app/constants/routes.config';
import { EventsObject } from 'src/app/models/events/event.model';
import { PublicApiService } from 'src/app/services/resource/public/public-api.service';
import { HelperService } from 'src/app/services/utils/helper.service';

@Component({
  selector: 'app-events',
  templateUrl: './events.component.html',
  styleUrls: ['./events.component.scss']
})
export class EventsComponent implements OnInit {
  eventsData!: EventsObject[];

  constructor(
    private titleService: Title,
    private apiService: PublicApiService,
    private router: Router,

    ) {
    //this.titleService.setTitle(this.titleService.getTitle().split("-")[0] + " - " + "");
    HelperService.setPageTitle(this.titleService,'Events');
  }

  ngOnInit(): void {
    this.apiService.getEventDetails().subscribe(data=>{
      this.eventsData=data;
    });
  }

  seeMore(event:EventsObject){
    this.router.navigate([approutes.seeMoreEvent.url], { state:{eventData: event} });
  }
 

}
