import { Pipe, PipeTransform } from '@angular/core';
import { EarningObject, MonthlyEarningObject } from 'src/app/models/earning-and-expense/earning.model';
import { ExpenseObject, MonthlyExpenseObject } from 'src/app/models/earning-and-expense/expense.model';

@Pipe({
  name: 'monthlyEarningExpenseFilter'
})
export class MonthlyEarningExpenditureFilterPipe implements PipeTransform {

  transform(earning: any , searchValue:string): any{
    if(!earning || !searchValue){
      return earning;
    }
    searchValue=searchValue.toLowerCase();
    return earning.filter((profile:MonthlyEarningObject | MonthlyExpenseObject )=>
      profile.monthName.toLowerCase().includes(searchValue)
    );

  }

}


@Pipe({
  name: 'earningFilter'
})
export class EarningFilterPipe implements PipeTransform {

  transform(expenses: EarningObject[], searchValue:string): EarningObject[] {
    if(!expenses || !searchValue){
      return expenses;
    }
    searchValue=searchValue.toLowerCase();
    return expenses.filter((data:EarningObject)=>
      (data.source && data.source.toLowerCase().includes(searchValue)) ||
      (data.description && data.description.toLowerCase().includes(searchValue)) ||
      (data.createdBy && data.createdBy.toLowerCase().includes(searchValue))
    );
    
  }

}

@Pipe({
  name: 'expenditureFilter'
})
export class ExpenditureFilterPipe implements PipeTransform {

  transform(expenses: ExpenseObject[], searchValue:string): ExpenseObject[] {
    if(!expenses || !searchValue){
      return expenses;
    }
    searchValue=searchValue.toLowerCase();

    return expenses.filter((data:ExpenseObject)=>
      (data.title && data.title.toLowerCase().includes(searchValue)) ||
      (data.description && data.description.toLowerCase().includes(searchValue)) ||
      (data.createdBy && data.createdBy.toLowerCase().includes(searchValue))
    );
  }

}

