import { Location } from '@angular/common';
import { Component, EventEmitter, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { FormBuilder, UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { MatTabChangeEvent } from '@angular/material/tabs';
import { Title } from '@angular/platform-browser';
import { AlertMode, AlertType } from 'src/app/components/modal/notification-modal/notification-modal.component';
import { SCOPE } from 'src/app/constants/auth-scope.constats';
import { HISTORY } from 'src/app/constants/history.constants';
import { approutes } from 'src/app/constants/routes.config';
import { EarningAndExpenseObject } from 'src/app/models/earning-and-expense/earning-expense.model';
import { EarningObject } from 'src/app/models/earning-and-expense/earning.model';
import { ExpenseObject } from 'src/app/models/earning-and-expense/expense.model';
import { ChangeObject } from 'src/app/models/history/history.model';
import { ModalService } from 'src/app/services/modal/modal.service';
import { FundsAndExpenditureService } from 'src/app/services/resource/internal/funds-and-expenditure.service';
import { AuthUserService } from 'src/app/services/utils/auth-user.service';
import { ExcelService } from 'src/app/services/utils/excel.service';
import { buildHistoryMessage, compareObjects, futureDateFilter, HelperService, historyHeader, isEmptyObject } from 'src/app/services/utils/helper.service';

@Component({
  selector: 'app-funds-and-expenditure',
  templateUrl: './funds-and-expenditure.component.html',
  styleUrls: ['./funds-and-expenditure.component.scss']
})
export class FundsAndExpenditureComponent implements OnInit {
  appRoutes=approutes;
  earningAndExpense!:EarningAndExpenseObject;
  canViewFunds!: boolean;
  canCreateFunds!: boolean;
  canUpdateFunds!: boolean;
  canViewExpenditure!: boolean;
  canCreateExpenditure!: boolean;
  canUpdateExpenditure!: boolean;
  canDeleteFunds!: boolean;
  canDeleteExpenditure!: boolean;
  futureDateFilter =futureDateFilter;

  myFilter = (d: Date | null): boolean => {
    const day = (d || new Date()).getDay();
    // Prevent Saturday and Sunday from being selected.
    return day !== 0 && day !== 6;
  };

  @ViewChild("updateTemplate")
  updateTemplate!: TemplateRef<any>;

  @ViewChild("updateActionbuttons")
  updateActionbuttons!: TemplateRef<any>;
  addFundsEvent!: EventEmitter<any>;
  updateExpenditureEvent!: EventEmitter<any>;
  updateFundsEvent!: EventEmitter<any>;
  history=HISTORY;
  @ViewChild("historyTemplate")
  historyTemplate!: TemplateRef<any>;
  canViewFundsHistory!: boolean;
  canViewExpenseHistory!: boolean;
  isInactiveUser!: boolean;

  historyHeader=historyHeader;
  historyMessage=buildHistoryMessage;

  constructor(
    private fundsAndExpenditureService : FundsAndExpenditureService,
    private location : Location,
    private authUserService : AuthUserService,
    private modal : ModalService,
    private title : Title,
    private excelService:ExcelService,

    ) { }

  searchValue!: string;
  tabIndex:number=1;
  earningExpenseForm!:UntypedFormGroup;
  addExpenditureEvent!:EventEmitter<any>;


  selectedTab(tabIndex:number) {
    this.tabIndex=tabIndex;

    if (tabIndex === 0) {
      this.fundsAndExpenditureService.getEarningDetails().subscribe(data=>{this.earningAndExpense=data})
    } else if (tabIndex === 1) {
      this.fundsAndExpenditureService.getExpenditureDetails().subscribe(data=>{this.earningAndExpense=data})
    }

  }
  ngOnInit(): void {
    HelperService.setPageTitle(this.title,"Funds and Expenditure");

    this.isInactiveUser=this.authUserService.isUserInactive();
    
    this.canViewFunds=this.authUserService.isAccrediatedTo(SCOPE.read.funds);
    this.canCreateFunds=this.authUserService.isAccrediatedTo(SCOPE.create.funds);
    this.canUpdateFunds=this.authUserService.isAccrediatedTo(SCOPE.update.funds);
    this.canDeleteFunds=this.authUserService.isAccrediatedTo(SCOPE.delete.funds);
    this.canViewFundsHistory=this.authUserService.isAccrediatedTo(SCOPE.read.fund_history);

    this.canViewExpenditure=this.authUserService.isAccrediatedTo(SCOPE.read.expenditure);
    this.canCreateExpenditure=this.authUserService.isAccrediatedTo(SCOPE.create.expenditure);
    this.canUpdateExpenditure=this.authUserService.isAccrediatedTo(SCOPE.update.expenditure);
    this.canDeleteExpenditure=this.authUserService.isAccrediatedTo(SCOPE.delete.expenditure);
    this.canViewExpenseHistory=this.authUserService.isAccrediatedTo(SCOPE.read.expense_history);

    if(!this.canViewFunds){
      this.tabIndex=1;
    }
    let stateData=this.location.getState() as any;
    if(stateData.fundAndExpense){
      this.earningAndExpense=stateData.fundAndExpense;
    }else{
      this.selectedTab(this.tabIndex);
    }

   
    this.earningExpenseForm=new UntypedFormGroup({
      title:new UntypedFormControl('',[Validators.required,Validators.minLength(2),Validators.maxLength(30)]),
      description:new UntypedFormControl('',[]),
      amount:new UntypedFormControl('',[Validators.required,Validators.min(1)]),
      date:new UntypedFormControl('',[]),
    });
    

  }

  createExpense(create:boolean,data?:ExpenseObject){
    this.earningExpenseForm.reset();
    let buttons = create? { cancel: 'Cancel', add: 'Add' } :{ cancel: 'Cancel', update: 'Update' };
    let dialog = this.modal.openDialog({
      body: {
        template: this.updateTemplate,
        context: {}
      },
      headers: {
        headerText: create?'Add New Expenditure' : 'Update Expenditure',
      },
      action: { buttonTemplate: this.updateActionbuttons, buttonContext: { buttons: buttons } },
      dimention: { width: 700 }
    });

    if(create){
      this.addExpenditureEvent= new EventEmitter();
    this.addExpenditureEvent.subscribe(decision=>{
    
      if(decision && this.earningExpenseForm.valid){
        let object = new ExpenseObject;
        object.title=this.earningExpenseForm.value.title;
        object.description=this.earningExpenseForm.value.description;
        object.amount=this.earningExpenseForm.value.amount;
        object.date=this.earningExpenseForm.value.date;
        this.fundsAndExpenditureService.addExpenditure(object).subscribe(data=>{
          //this.selectedTab(this.tabIndex);
          //dialog.close()
          console.log(data)
          let expenseObj=new ExpenseObject;
          expenseObj.isApproved=true;
          this.fundsAndExpenditureService.updateExpenditure(data.id,expenseObj).subscribe(data=>{
            this.selectedTab(this.tabIndex);
            dialog.close()
          })
        })
      }else{
        Object.keys(this.earningExpenseForm.controls).forEach(field => {
          const control = this.earningExpenseForm.get(field);
          control?.markAsTouched({ onlySelf: true });
        });
      }
    })
      
    }else if(data){
      this.earningExpenseForm.patchValue({title:data.title,description:data.description,amount:data.amount,date:data.date});
      this.updateExpenditureEvent= new EventEmitter();
      this.updateExpenditureEvent.subscribe(decision=>{
        if(decision && this.earningExpenseForm.valid){
          let updatedobject = new ExpenseObject;
          updatedobject.title=this.earningExpenseForm.value.title;
          updatedobject.description=this.earningExpenseForm.value.description;
          updatedobject.amount=this.earningExpenseForm.value.amount;
          updatedobject.date=this.earningExpenseForm.value.date;
          if(isEmptyObject(compareObjects(updatedobject,data))){
            this.modal.openNotificationModal('Error','No changes have been made.',AlertType.error,AlertMode.notification);
          }else{
            this.fundsAndExpenditureService.updateExpenditure(data.id,compareObjects(updatedobject,data)).subscribe(data=>{
              this.selectedTab(this.tabIndex);
              dialog.close()
            })
          }
          
        }else{
          Object.keys(this.earningExpenseForm.controls).forEach(field => {
            const control = this.earningExpenseForm.get(field);
            control?.markAsTouched({ onlySelf: true });
          });
        }
      })
    }
    
    
    
  }

  createFunds(create:boolean,data?:EarningObject){
    this.earningExpenseForm.reset();
    let buttons = create? { cancel: 'Cancel', add: 'Add' } :{ cancel: 'Cancel', update: 'Update' };
    let dialog = this.modal.openDialog({
      body: {
        template: this.updateTemplate,
        context: {}
      },
      headers: {
        headerText: create?'Add New Fund Source' : 'Update Fund Source',
      },
      action: { buttonTemplate: this.updateActionbuttons, buttonContext: { buttons: buttons } },
      dimention: { width: 700 }
    });

    if(create){
      this.addFundsEvent= new EventEmitter();
    this.addFundsEvent.subscribe(decision=>{
    
      if(decision && this.earningExpenseForm.valid){
        let object = new EarningObject;
        object.source=this.earningExpenseForm.value.title;
        object.description=this.earningExpenseForm.value.description;
        object.amount=this.earningExpenseForm.value.amount;
        object.date=this.earningExpenseForm.value.date;
        this.fundsAndExpenditureService.addEarningSoure(object).subscribe(data=>{
          // this.selectedTab(this.tabIndex);
          // dialog.close()
          //
          let earningObj=new EarningObject;
          earningObj.isApproved=true;
          this.fundsAndExpenditureService.updateEarning(data.id,earningObj).subscribe(data=>{
            this.selectedTab(this.tabIndex);
            dialog.close()
          })
          //
        })
      }else{
        Object.keys(this.earningExpenseForm.controls).forEach(field => {
          const control = this.earningExpenseForm.get(field);
          control?.markAsTouched({ onlySelf: true });
        });
      }
    })
      
    }else if(data){
      this.earningExpenseForm.patchValue({title:data.source,description:data.description,amount:data.amount,date:data.date});
      this.updateFundsEvent= new EventEmitter();
      this.updateFundsEvent.subscribe(decision=>{
        if(decision && this.earningExpenseForm.valid){
          let updatedobject = new EarningObject;
          updatedobject.source=this.earningExpenseForm.value.title;
          updatedobject.description=this.earningExpenseForm.value.description;
          updatedobject.amount=this.earningExpenseForm.value.amount;
          updatedobject.date=this.earningExpenseForm.value.date;
          if(isEmptyObject(compareObjects(updatedobject,data))){
            this.modal.openNotificationModal('Error','No changes have been made.',AlertType.error,AlertMode.notification);
          }else{
            this.fundsAndExpenditureService.updateEarning(data.id,compareObjects(updatedobject,data)).subscribe(data=>{
              this.selectedTab(this.tabIndex);
              dialog.close()
            })
          }
          
        }else{
          Object.keys(this.earningExpenseForm.controls).forEach(field => {
            const control = this.earningExpenseForm.get(field);
            control?.markAsTouched({ onlySelf: true });
          });
        }
      })
    }
    
    
    
  }
  deleteFunds(data:EarningObject){
    let dialog=this.modal.openNotificationModal('Confirmation','Are you sure to delete this ?',AlertType.warning,AlertMode.confirmation);
    
    dialog.componentInstance.onSubmit.subscribe(decision=>{
      if(decision){
        this.fundsAndExpenditureService.deleteEarning(data.id).subscribe(data=>{
          this.selectedTab(this.tabIndex);
        })
      }
      dialog.close();
    })
   
  }

  deleteExpenditure(data:ExpenseObject){
    let dialog=this.modal.openNotificationModal('Confirmation','Are you sure to delete this ?',AlertType.warning,AlertMode.confirmation);
    
    dialog.componentInstance.onSubmit.subscribe(decision=>{
      if(decision){
        this.fundsAndExpenditureService.deleteExpenditure(data.id).subscribe(data=>{
          this.selectedTab(this.tabIndex);
        })
      }
      dialog.close();
    })
   
  }

  isRequiredField(controlName:string) :boolean{
    return this.earningExpenseForm.controls[controlName].hasValidator(Validators.required)
    || this.earningExpenseForm.controls[controlName].errors?.required;
  }

  get CFControl() {
    return this.earningExpenseForm.controls;
  }

  emitCreateEvent(){
    if(this.tabIndex == 0) {
      this.addFundsEvent.emit(true);
    }else{
      this.addExpenditureEvent.emit(true);
    }
  }

  emitUpdateEvent(){
    if(this.tabIndex == 0) {
      this.updateFundsEvent.emit(true);
    }else{
      this.updateExpenditureEvent.emit(true);
    }
  }

  showEarningHistory(data:EarningObject){
    this.fundsAndExpenditureService.getEarningHistory(data.id).subscribe(data=>{
      this.modal.openDialog({
        body: {
          template: this.historyTemplate,
          context:{
            histories:data,
            name :'Fund source'
          }
        },
        headers: {
          headerText:'View Changes'
        },
        action: { buttonTemplate: this.updateActionbuttons, buttonContext: { buttons: { cancel: 'Close'} } },
        dimention: { width: 800 }
      });
    })
  }

  showExpenseHistory(data:ExpenseObject){
    this.fundsAndExpenditureService.getExpenseHistory(data.id).subscribe(data=>{
      this.modal.openDialog({
        body: {
          template: this.historyTemplate,
          context:{
            histories:data,
            name :'Expense'
          }
        },
        headers: {
          headerText:'View Changes'
        },
        action: { buttonTemplate: this.updateActionbuttons, buttonContext: { buttons: { cancel: 'Close'} } },
        dimention: { width: 800 }
      });
    })
  }

  exportFundDetails(){
    this.fundsAndExpenditureService.getEarningDetails().subscribe(data=>{
      let fundsSummary:any[]=[];
      let detailedOtherEarning:any[]=[];

      let earningObject= data.monthlyEarning;
      for (var i=0;i<earningObject.length;i++){
        fundsSummary.push({
          'Month Name' : earningObject[i].monthName,
          'Earning from Donations' : earningObject[i].totalDonationAmount,
          'Earning from Others' : earningObject[i].totalAmountFromOtherSource,
          'Total Earning' : earningObject[i].totalAmount,
        });
        if(earningObject[i].otherEarnings){
          for (var j=0;j<earningObject[i].otherEarnings.length;j++){
            detailedOtherEarning.push({
              'Earning Source Name' : earningObject[i].otherEarnings[j].source,
              'Description' : earningObject[i].otherEarnings[j].description,
              'Amount' : earningObject[i].otherEarnings[j].amount,
              'Date' : earningObject[i].otherEarnings[j].date? new Date(earningObject[i].otherEarnings[j].date):'',
              'Added By' : earningObject[i].otherEarnings[j].createdBy,
            });
            
           }
        }
        
       }
       let dataToExport= new Map()
       .set("Earning_Summary",fundsSummary)
       .set("Other_Earning_Details",detailedOtherEarning)
       this.excelService.exportAsExcelFile(dataToExport,"Earning_details")
    })

  }

  exportExpenseDetails(){
    this.fundsAndExpenditureService.getExpenditureDetails().subscribe(data=>{
      let expenseSummary:any[]=[];
      let eventDetails:any[]=[];
      let otherDetails:any[]=[];

      let expenseObject= data.monthlyExpense;
      for (var i=0;i<expenseObject.length;i++){
        expenseSummary.push({
          'Month Name' : expenseObject[i].monthName,
          'Event Expense' : expenseObject[i].totalEventExpense,
          'Other Expense' : expenseObject[i].totalOtherExpense,
          'Total Expense' : expenseObject[i].totalAmount,
        });
        if(expenseObject[i].eventObject){
          for (var j=0;j<expenseObject[i].eventObject.length;j++){
            eventDetails.push({
              'Event Title' : expenseObject[i].eventObject[j].eventTitle,
              'Description' : expenseObject[i].eventObject[j].eventDescription,
              'Total Expense' : expenseObject[i].eventObject[j].totalExpenditure,
              'Event date' : expenseObject[i].eventObject[j].eventDate?new Date(expenseObject[i].eventObject[j].eventDate):'',
              'Organizer' : expenseObject[i].eventObject[j].creatorName,
            });
           }
        }

        if(expenseObject[i].otherExpenses){
          for (var j=0;j<expenseObject[i].otherExpenses.length;j++){
            otherDetails.push({
              'Title' : expenseObject[i].otherExpenses[j].title,
              'Description' : expenseObject[i].otherExpenses[j].description,
              'Amount' : expenseObject[i].otherExpenses[j].amount,
              'Date' : expenseObject[i].otherExpenses[j].date?new Date(expenseObject[i].otherExpenses[j].date):'',
              'Added by' : expenseObject[i].otherExpenses[j].createdBy,
            });
           }
        }   
       }
       let dataToExport= new Map()
       .set("Expense_Summary",expenseSummary)
       .set("Event_Details",eventDetails)
       .set("Other_Expense_Details",otherDetails)
       this.excelService.exportAsExcelFile(dataToExport,"Expenditure_details")
    })
  }

 

}
