import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { approutes } from 'src/app/constants/routes.config';
import { AlertMessageObject } from 'src/app/models/dashboard/comprehensive-summary.model';

@Component({
  selector: 'app-alert',
  templateUrl: './alert.component.html',
  styleUrls: ['./alert.component.scss']
})
export class AlertComponent implements OnInit {

  display:boolean=true;

  @Input('message')
  message!: string; 

  @Input('type')
  type!: string; 

  @Input('action')
  action!: string; 

  @Input('actionButton')
  actionButton!: string; 

  @Input('alertData')
  alertData!:AlertMessageObject;

  @Input('displayClose')
  displayClose:boolean=true;

  constructor( 
       private router: Router,
) { }

  ngOnInit(): void {
    if(this.alertData){
      this.message=this.alertData.message;
      this.type=this.alertData.type;
      this.action=this.alertData.action;
      this.actionButton=this.alertData.actionButton;
    }
  }

  performAction(){
    let routes=new Map(Object.entries(approutes));
      this.router.navigate([routes.get(this.action)], { state: { isAlert:true } });
   
  }

  getAlertClass(type:string){
    if(type === "WARNING"){
      return 'bg-yellow-400';
    }else if(type === "ERROR"){
      return 'bg-red-400';
    }
    return 'bg-blue-400';
  }

}
