export class NoticeObject{
    creator!:string;
    creatorRole!:string;
    description!:string;
    id!:string;
    isDraft!:boolean;
    noticeDate!:Date;
    noticeNumber!:Date;
    priorityDays!:number;
    title!:string;
    visibility!:string;
   // editable!:boolean;
    needMeeting!: boolean;
    meetingInfo!: MeetingInfo;
    redirectUrl!: string;
    authorizationUrl!: string;
}

export class MeetingInfo{
    meetingId!: string;
    meetingStatus!: string;
    summary!: string;
    description!: string;
    location!: string;
    meetingDate!:Date;
    startTime!: Date;
    endTime !: string;
    attendees!:Attendee[]; 
    emailReminderBeforeMin!: number[];
    popupReminderBeforeMin!: number[];
    meetingLink!: string;

}

export class Attendee{
    constructor(name:string,email:string){
        this.email=email;
        this.name=name;
    }
    name!: string;
    email!: string;
}