@Component({
  selector: 'app-contributions',
  templateUrl: './contributions.component.html',
  styleUrls: ['./contributions.component.scss'],
  providers: [
  ]
})
export class ContributionsComponent implements OnInit {
  insuffucientPermissionMsg="You dont have sufficient permission";
  appRoutes=approutes;
  isCashier: boolean = false;
  history=HISTORY;
  contributionForm: UntypedFormGroup = new UntypedFormGroup({});
  tabIndex: number = 0;
  searchValue!: string;
  contributionsReferencedata!: any;
  contributionsMetadata = CONTRIBUTIONS;
  profileMetadata=PROFILE;

  contributionsProfileWise!: ProfileContribution[];
  allContributions!: Contributions[];
  myContributions!: Contributions[];
  allowedModes = { add: 1, update: 2, other: 3 }
  futureDateFilter =futureDateFilter;
  historyHeader=historyHeader;
  historyMessage=buildHistoryMessage;

  @ViewChild("updateTemplate")
  updateTemplate!: TemplateRef<any>;

  @ViewChild("updateModalHeader")
  updateModalHeader!: TemplateRef<any>;

  @ViewChild("updateActionbuttons")
  updateActionbuttons!: TemplateRef<any>;

  @ViewChild("historyTemplate")
  historyTemplate!: TemplateRef<any>;

  @ViewChild("addDocumentTemplate")
  addDocumentTemplate!: TemplateRef<any>;

  @ViewChild("paymentTemplate")
  paymentTemplate!: TemplateRef<any>;

  readContribution!: boolean;
  readMyContribution!: boolean;
  canCreateContribution!: boolean;
  canUpdateContribution!: boolean;
  myContributionSummary!: ComprehensiveContributionSummary;
  allContributionSummary!: ComprehensiveContributionSummary;
  canViewDonationHistory!: boolean;
  canDeleteDonation!: boolean;
  pageSize: number=25;
  pageIndex: number=0;
  pageSizeOptions: number[] = [25,50,100];
  pageEvent!: PageEvent;
  pagination!:Pagination<Contributions>;
  selectedFileError!: string;
  documents :FileUploadObject[] = [];
  canViewAttachment!: boolean;
  canCreateAttachment!: boolean;
  canDeleteAttachment!: boolean;
  isInactiveUser!: boolean;
  filterEnabled: boolean=false;
  canReadPaymentDetails!: boolean;
  canUpdatePaymentDetails!: boolean;
  paymentDetails!: PaymentDetails;
  allEvents!: EventsObject[];
  payableAccounts!:Account[];

  constructor(
    private contributionService: ContributionService,
    private dashboardService: DashboardService,
    private titleService: Title,
    private location: Location,
    private modal: ModalService,
    private authUserService:AuthUserService,
    private sanitizer: DomSanitizer,
    private excelService:ExcelService,
    private profileService:ProfileService,
    private router: Router,
    private paymentDetailsService: PaymentDetailsService,
    private eventService: EventsService,
    private accountService: AccountService
  ) {
      HelperService.setPageTitle(this.titleService,"Donations");
  }

  ngOnInit(): void {
    this.isInactiveUser=this.authUserService.isUserInactive();

    this.canUpdateContribution = this.authUserService.isAccrediatedTo(SCOPE.update.contribution);
    this.canCreateContribution = this.authUserService.isAccrediatedTo(SCOPE.create.contribution);
    this.readMyContribution = this.authUserService.isAccrediatedTo(SCOPE.read.my_contribution);
    this.readContribution = this.authUserService.isAccrediatedTo(SCOPE.read.contribution);
    this.canDeleteDonation = this.authUserService.isAccrediatedTo(SCOPE.delete.donation);
    this.canViewDonationHistory=this.authUserService.isAccrediatedTo(SCOPE.read.donation_history);

    this.canViewAttachment=this.authUserService.isAccrediatedTo(SCOPE.read.donation_attachment);
    this.canCreateAttachment=this.authUserService.isAccrediatedTo(SCOPE.create.donation_attachment);
    this.canDeleteAttachment=this.authUserService.isAccrediatedTo(SCOPE.delete.donation_attachment);

    this.canReadPaymentDetails=this.authUserService.isAccrediatedTo(SCOPE.read.payment_details);
    this.canUpdatePaymentDetails=this.authUserService.isAccrediatedTo(SCOPE.update.payment_details);

    let stateData=this.location.getState() as any;
    
    if(!stateData.contributionsReferencedata /*&& this.canUpdateContribution*/){
      this.contributionService.getReferenceData().subscribe(data => { this.contributionsReferencedata = data });
    }else{
      this.contributionsReferencedata = stateData.contributionsReferencedata;
    }

    if(!stateData.myContributions){
      this.tabIndex=0;
      this.tabChanged(this.tabIndex);
    }else if(stateData.tabId){
      this.tabChanged(stateData.tabId);
    }else{
      this.myContributions = stateData.myContributions.contributions;
      this.myContributionSummary = stateData.myContributions.comprehensiveContributionSummary;
    }

    if(this.canReadPaymentDetails){
      this.paymentDetailsService.getPaymentDetails().subscribe(data=>{
        this.paymentDetails=data;
      });
    }
  }

  tabChanged(tabIndex: number) {
    this.tabIndex = tabIndex;
    this.filterEnabled=false;
    this.selectedFileError='';
    if (tabIndex === 0) {
      this.contributionService.getMyContributions().subscribe(data => {
        this.myContributions = data.contributions;
        this.myContributionSummary = data.comprehensiveContributionSummary;
      })
    } else if (tabIndex === 1) {
      this.dashboardService.getComprehensiveSummary({donation:true}).subscribe(data => {
        this.allContributionSummary = data.contributions;
        //console.log(data)
      })
      
      this.contributionService.getAllContributions(
        new Map()/*.set("pageIndex",this.pageIndex).set('pageSize',this.pageSize)*/.set('guest',true) 
        ).subscribe(data => {
          if(data.content){
            this.allContributions = data.content;
            this.pagination=data;
          }else{
            this.allContributions = data;
          }
        
        //console.log(data)
      })
    } else if (tabIndex === 2) {
      this.contributionService.getContributionsProfileWise().subscribe(data => {
        this.contributionsProfileWise = data;
        //console.log(data)
      });
    }
  }

  paginationEvent(event: PageEvent){
    this.pageIndex=event.pageIndex;
    this.pageSize=event.pageSize;
    this.tabChanged(this.tabIndex);
    
  }

  showFilterForm(decision:boolean){
    this.tabChanged(1)
    console.log(this.filterEnabled);

    this.filterEnabled=decision;
    console.log(this.filterEnabled);

  }


  addContribution(event: MouseEvent, profile: ProfileContribution) {
    event.stopPropagation();
    let addContributionEvent = new EventEmitter();
    this.contributionForm = new UntypedFormGroup({
      contributor: new UntypedFormControl(profile.id),
      contributionType: new UntypedFormControl(this.contributionsMetadata.type.regular, [Validators.required]),
      amount: new UntypedFormControl('', [Validators.required, Validators.min(1)]),
      startDate: new UntypedFormControl('', [
        conditionalValidator(() =>
          (this.contributionForm.get('contributionType')?.value === this.contributionsMetadata.type.regular),
          Validators.compose([Validators.required])!
        )
      ]),
      endDate: new UntypedFormControl('', [
        conditionalValidator(() =>
          (this.contributionForm.get('contributionType')?.value === this.contributionsMetadata.type.regular),
          Validators.compose([Validators.required, DateValidators.dateGreterThan('startDate', 'endDate', { 'dateValidationError': true })])!
        ),
      ]),
      isForEvent : new UntypedFormControl(null, [conditionalValidator(() =>
        (this.contributionForm.get('contributionType')?.value === this.contributionsMetadata.type.onceoff),
        Validators.compose([Validators.required,])!
      ),]),
      eventId : new UntypedFormControl('', [ conditionalValidator(() =>
        (this.contributionForm.get('isForEvent')?.value === 'true'),
        Validators.compose([Validators.required,])!
      ),]),
    });
    this.contributionForm.get('isForEvent')?.valueChanges
      .subscribe((value: any) => {
        this.contributionForm.get('eventId')?.updateValueAndValidity();
        if (value === 'true') {
          this.eventService.getEventsInternal().subscribe(data=>this.allEvents=data);
        }else{
          this.contributionForm.get('eventId')?.reset();
        }
      });

    this.contributionForm.get('contributionType')?.valueChanges
      .subscribe((value: string) => {
        this.contributionForm.get('startDate')?.updateValueAndValidity();
        this.contributionForm.get('endDate')?.updateValueAndValidity();
        if (value === this.contributionsMetadata.type.onceoff) {
          this.contributionForm.controls.startDate.reset();
          this.contributionForm.controls.endDate.reset();
        }
      });

    this.contributionForm.get('startDate')?.valueChanges
      .subscribe((value: any) => {
        this.contributionForm.get('endDate')?.updateValueAndValidity();
      });

    let dialog = this.modal.openDialog({
      body: {
        template: this.updateTemplate
      },
      headers: {
        headerTemplate: this.updateModalHeader,
        headerContext: {
          heading: 'Add Donation for ' + profile.firstName + ' ' + profile.lastName
        }
      },
      action: { buttonTemplate: this.updateActionbuttons, buttonContext: { emitter:{add:addContributionEvent}, buttons: { cancel: 'Cancel', add: 'Add' } } },
      dimention: { width: 700 }
    });
    //this.contributionForm.valueChanges.subscribe(d=>console.log(this.contributionForm))
    addContributionEvent.subscribe(decision => {

      if (decision === true) {
        if (this.contributionForm.valid) {
          console.log(this.contributionForm.value)
          let object =new Contributions;
          object.contributor=this.contributionForm.value.contributor;
          object.contributionType=this.contributionForm.value.contributionType;
          object.amount=this.contributionForm.value.amount;
          object.endDate=this.contributionForm.value.endDate;
          object.startDate=this.contributionForm.value.startDate;

          if(this.contributionForm.value.isForEvent === 'true'){
            let event = new EventsObject;
            event.id=this.contributionForm.value.eventId;
            object.event=event;
          }

          this.contributionService.createContribution(object).subscribe((data: Contributions) => {
            dialog.close();
            // this.modal.openNotificationModal('Success', 'Contribution created successfully.', AlertType.success, AlertMode.notification);
            this.contributionsProfileWise.find(obj => obj.id === profile.id)?.contributions.unshift(data);
            //this.tabChanged(this.tabIndex);
          })
        } else {
          Object.keys(this.contributionForm.controls).forEach(field => {
            const control = this.contributionForm.get(field);
            control?.markAsTouched({ onlySelf: true });
          });
          
        }
      }
    })

  }


  addGuestContribution() {
    let addContributionEvent = new EventEmitter();
    this.contributionForm = new UntypedFormGroup({
      guestFullName: new UntypedFormControl('', [Validators.required, Validators.minLength(2),Validators.maxLength(30)]),
      guestEmail: new UntypedFormControl('', [Validators.email]),
      guestMobile: new UntypedFormControl('', [Validators.required]),
      contributionType: new UntypedFormControl({value:this.contributionsMetadata.type.onceoff,disabled:true}, [Validators.required]),
      amount: new UntypedFormControl('', [Validators.required, Validators.min(1)]),
      isForEvent : new UntypedFormControl(null, [conditionalValidator(() =>
        (this.contributionForm.get('contributionType')?.value === this.contributionsMetadata.type.onceoff),
        Validators.compose([Validators.required,])!
      ),]),
      eventId : new UntypedFormControl('', [ conditionalValidator(() =>
        (this.contributionForm.get('isForEvent')?.value === 'true'),
        Validators.compose([Validators.required,])!
      ),]),
    });

    this.contributionForm.get('isForEvent')?.valueChanges
      .subscribe((value: any) => {
        this.contributionForm.get('eventId')?.updateValueAndValidity();
        if (value === 'true') {
          this.eventService.getEventsInternal().subscribe(data=>this.allEvents=data);
        }else{
          this.contributionForm.get('eventId')?.reset();
        }
      });

   // console.log(this.contributionForm)
    let dialog = this.modal.openDialog({
      body: {
        template: this.updateTemplate,
        context: { mode: this.allowedModes.other }
      },
      headers: {
        headerTemplate: this.updateModalHeader,
        headerContext: {
          heading: "Add Guest's Donation"
        }
      },
      action: { buttonTemplate: this.updateActionbuttons, buttonContext: {emitter:{add:addContributionEvent}, buttons: { cancel: 'Cancel', add: 'Add' } } },
      dimention: { width: 700 }
    });
   // this.contributionForm.valueChanges.subscribe(d=>console.log(this.contributionForm))
    addContributionEvent.subscribe(decision => {

      if (decision === true) {
        if (this.contributionForm.valid) {
          console.log(this.contributionForm.value)
          let object =new Contributions;
          object.amount=this.contributionForm.value.amount;
          object.contributionType=this.contributionForm.value.contributionType;
          object.guestMobile=this.contributionForm.value.guestMobile;
          object.guestEmail=this.contributionForm.value.guestEmail;
          object.guestFullName=this.contributionForm.value.guestFullName;
          
          if(this.contributionForm.value.isForEvent === 'true'){
            let event = new EventsObject;
            event.id=this.contributionForm.value.eventId;
            object.event=event;
          }
         
          this.contributionService.createContribution(object,true).subscribe((data: Contributions) => {
            dialog.close();
            this.allContributions.unshift(data);
          })
        } else {
          Object.keys(this.contributionForm.controls).forEach(field => {
            const control = this.contributionForm.get(field);
            control?.markAsTouched({ onlySelf: true });
          });
        }
      }
    })

  }



  editContributions(contribution: Contributions) {
    this.selectedFileError='';
    this.documents=[];
    let updateContributionEvent = new EventEmitter();
    this.contributionForm = new UntypedFormGroup({
      contributionType: new UntypedFormControl({ value: contribution.contributionType, disabled: true }, [Validators.required]),
      amount: new UntypedFormControl(contribution.amount, [Validators.required]),
      startDate: new UntypedFormControl({ value: new Date(contribution.startDate), disabled: true }, [conditionalValidator(() =>
        (this.contributionForm.get('contributionType')?.value === this.contributionsMetadata.type.regular),
        Validators.required
      )]),
      endDate: new UntypedFormControl({ value: new Date(contribution.endDate), disabled: true }, [conditionalValidator(() =>
        (this.contributionForm.get('contributionType')?.value === this.contributionsMetadata.type.regular),
        Validators.required
      )]),
      contributionStatus: new UntypedFormControl(contribution.contributionStatus, [Validators.required]),
      paidOn: new UntypedFormControl(contribution.paidOn != null ? new Date(contribution.paidOn) : null, [conditionalValidator(() =>
        (this.contributionForm.get('contributionStatus')?.value === this.contributionsMetadata.status.paid),
        Validators.required
      )]),
      paymentMethod:new UntypedFormControl(contribution.paymentMethod?contribution.paymentMethod:null, [conditionalValidator(() =>
        (this.contributionForm.get('contributionStatus')?.value === this.contributionsMetadata.status.paid),
        Validators.required
      )]),
      paidTo:new UntypedFormControl(contribution.paidToAccount?contribution.paidToAccount:null, [conditionalValidator(() =>
        (this.contributionForm.get('contributionStatus')?.value === this.contributionsMetadata.status.paid),
        Validators.required
      )]),
      comment: new UntypedFormControl(contribution.comment,
        [conditionalValidator(() =>
        (this.contributionForm.get('contributionStatus')?.value === this.contributionsMetadata.status.cancelled
        || this.contributionForm.get('contributionStatus')?.value === this.contributionsMetadata.status.uncleared
        ),
        Validators.required)]
        ),
      
    });

    

    this.contributionForm.get('contributionStatus')?.valueChanges
      .subscribe((value: string) => {
        this.contributionForm.get('paidOn')?.updateValueAndValidity();
        this.contributionForm.get('paymentMethod')?.updateValueAndValidity();
        this.contributionForm.get('paidTo')?.updateValueAndValidity();
        this.contributionForm.get('comment')?.updateValueAndValidity();
        if (value !== this.contributionsMetadata.status.paid) {
          this.contributionForm.controls.paidOn.reset();
        }
        this.contributionForm.controls.comment.reset();
        /*if(value === this.contributionsMetadata.status.deferred || value === this.contributionsMetadata.status.cancelled){
          this.contributionForm.controls.comment.reset();
        }*/
        if(value === this.contributionsMetadata.status.paid){
          this.accountService.getAccounts().subscribe(data=>this.payableAccounts=data);
        }
      });
      //this.contributionForm.valueChanges.subscribe(d=>console.log(this.contributionForm))

    let dialog = this.modal.openDialog({
      body: {
        template: this.updateTemplate,
        context: { contribution: contribution, mode: this.allowedModes.update }
      },
      headers: {
        headerTemplate: this.updateModalHeader,
        headerContext: {
          heading: 'Update Donation'
        }
      },
      action: { buttonTemplate: this.updateActionbuttons, buttonContext: {emitter:{update:updateContributionEvent},  buttons: { cancel: 'Cancel', update: 'Update' } } },
      dimention: { width: 700 }
    });

    updateContributionEvent.subscribe((decision: boolean) => {
      if (decision === true) {
        //console.log(this.contributionForm)

        if (this.contributionForm.valid) {
          let modifiedObj =new Contributions;
          modifiedObj.amount=this.contributionForm.value.amount;
          modifiedObj.comment=this.contributionForm.value.comment;
          modifiedObj.contributionStatus=this.contributionForm.value.contributionStatus;
          modifiedObj.contributionType=this.contributionForm.value.contributionType;
          modifiedObj.endDate=this.contributionForm.value.endDate;
          modifiedObj.paidOn=this.contributionForm.value.paidOn;
          modifiedObj.startDate=this.contributionForm.value.startDate;
          modifiedObj.paymentMethod=this.contributionForm.value.paymentMethod;
          modifiedObj.paidToAccount=this.contributionForm.value.paidTo;

          console.log(contribution)
          console.log(modifiedObj)
           /** IF Changing contribution status to paid from some other status */
          if(this.documents.length == 0 && objectsEqual(modifiedObj,contribution)){
            this.modal.openNotificationModal('Error','No changes have been made',AlertType.error,AlertMode.notification);
          }else if(contribution.contributionStatus != this.contributionsMetadata.status.paid 
            && modifiedObj.contributionStatus === this.contributionsMetadata.status.paid 
            && modifiedObj.paymentMethod != this.contributionsMetadata.paymentMethod.cash
            && this.documents.length == 0
            && (contribution.documents == null || contribution.documents.length ==0)
            ){
              this.selectedFileError = 'Please select a document';
              this.modal.openNotificationModal('Error',this.selectedFileError,AlertType.error,AlertMode.notification);

          }
          else{
            let pageRefreshed=false;
            let filesTo: File[] = [];
            this.documents.forEach(element => { filesTo.push(element.file) });
            if(filesTo.length > 0 ){
               this.contributionService.addAttachment(contribution.contributionId,filesTo).subscribe(data=>{
                if(!pageRefreshed){
                  pageRefreshed=true;
                  this.tabChanged(this.tabIndex);
                }
               })
            }
           // console.log(modifiedObj.paymentMethod,contribution.paymentMethod)
            if(!objectsEqual(modifiedObj,contribution)){
              //
              //modifiedObj.paidToAccount=modifiedObj.paidToAccount;
              console.log(modifiedObj,contribution)
              //
              this.contributionService.updateContribution(contribution.contributionId, compareObjects(modifiedObj,contribution))
              .subscribe((data: Contributions) => {
                if(!pageRefreshed){
                  pageRefreshed=true;
                  this.tabChanged(this.tabIndex);
                }
              })
            }
            dialog.close();
          }
        } else {
          Object.keys(this.contributionForm.controls).forEach(field => {
            const control = this.contributionForm.get(field);
            control?.markAsTouched({ onlySelf: true });
          });
        }
      }
    })
  }


  isRequiredField(controlName:string) :boolean{
    return this.contributionForm.controls[controlName].hasValidator(Validators.required)
    || this.contributionForm.controls[controlName].errors?.required;
  }

  public hasRequiredField = (abstractControl: AbstractControl): boolean => {
    if (abstractControl.validator) {
      const validator = abstractControl.validator({}as AbstractControl);
      if (validator && validator.required) {
        return true;
      }
    }
    return false;
  }


 

  get CFControl() {
    return this.contributionForm.controls;
  }


  
  deleteDonation(donation: Contributions){
    let modal=this.modal.openNotificationModal('Delete Donation','Are you sure to delete this donation?',AlertType.warning,AlertMode.confirmation);
    modal.componentInstance.onSubmit.subscribe(response=>{
      if(response == true){
        this.contributionService.deleteDonation(donation.contributionId).subscribe(data=>{
          
          this.tabChanged(this.tabIndex);
        })
      }
      modal.close();
    });
  }

  getCommentColumnName=(status:string)=>{
    if(status === this.contributionsMetadata.status.cancelled){
      return 'Reason for cancel';
    }else if(status === this.contributionsMetadata.status.uncleared){
      return 'Payment failure details';
    }
    return 'Comment';
  }


  colorClassByStatus=(status: string): string =>{
    switch (status) {
      case this.contributionsMetadata.status.paid:
        return 'text-green-700';
      case this.contributionsMetadata.status.raised:
        return 'text-blue-700';
      case this.contributionsMetadata.status.uncleared:
        return 'text-red-700';
      case this.contributionsMetadata.status.pending:
        return 'text-pink-700';
      case this.contributionsMetadata.status.deferred:
        return 'text-yellow-700';
      default:
        return 'text-gray-500';
    }
  }

  showHistory(contribution: Contributions){
    this.contributionService.getHistory(contribution.contributionId).subscribe(data=>{
      this.modal.openDialog({
        body: {
          template: this.historyTemplate,
          context:{
            histories:data,
            name : 'Donation'
          }
        },
        headers: {
          headerText:'View Changes'
        },
        action: { buttonTemplate: this.updateActionbuttons, buttonContext: { buttons: { cancel: 'Close'} } },
        dimention: { width: 800 }
      });
    })
  }


  showMyHistory(contribution: Contributions){
    this.contributionService.getMyContributionsAndHistory(contribution.contributionId).subscribe(data=>{
      this.modal.openDialog({
        body: {
          template: this.historyTemplate,
          context:{
            histories:data.history,
            name : 'Donation'
          }
        },
        headers: {
          headerText:'View Changes'
        },
        action: { buttonTemplate: this.updateActionbuttons, buttonContext: { buttons: { cancel: 'Close'} } },
        dimention: { width: 800 }
      });
    })
  }

  

  addDocument(contribution:Contributions,notifyDonation:boolean=false){
    this.documents=[];
    let addDocumentEvent=new EventEmitter();
    let popup=this.modal.openDialog({
      body: {
        template: this.addDocumentTemplate,
      },
      headers: {
        headerText:'Add Documents'
      },
      action: { buttonTemplate: this.updateActionbuttons, buttonContext: { emitter:{add:addDocumentEvent}, buttons: { cancel: 'Cancel', add: 'Add' } } },
      dimention: { width: 800 }
    });
    addDocumentEvent.subscribe(response=>{
      if(response == true){
        let filesTo: File[] = [];
        this.documents.forEach(element => {
              filesTo.push(element.file)
        });
        if(filesTo.length==0){
          this.selectedFileError = 'Please select a document';
        }else{
          this.contributionService.addAttachment(contribution.contributionId,filesTo).subscribe(data=>{
            if(notifyDonation){
              this.contributionService.updateSelfDonation(contribution,true).subscribe(data=>{
                popup.close();
                this.tabChanged(this.tabIndex);
              });
            }else{
              popup.close();
              this.tabChanged(this.tabIndex);
            }
          })
        }
        
      }
    })
  }


  selectDocumentsEvent(event: any) {
    if (event.target.files && event.target.files[0]) {
      this.selectedFileError = '';
     
      if (!(event.target.files[0].size > 10000 && event.target.files[0].size < 1000000)) {
        this.selectedFileError = 'File size should be greter than 10Kb and less than 1Mb';
        return;
      }
      let file = new FileUploadObject;
      file.file = event.target.files[0];

      //HTML5 FileReader API
      let reader = new FileReader();
      reader.onload = (e: any) => {
        let image = new Image();
        image.src = e.target.result;
        image.onload = rs => {
          file.preview = e.target.result;
        };
      };
      reader.readAsDataURL(event.target.files[0]);
      this.documents.push(file);
    }
  }

  getFileSize(file: File) {
    return file.size > 1024
      ? file.size > 1048576
        ? Math.round(file.size / 1048576) + "MB"
        : Math.round(file.size / 1024) + "kB"
      : file.size + "b";
  }

  viewAttachment(id: string) {
    this.contributionService.viewAttachment(id, true).subscribe(data => {
      openWindow(data);
    })
  }

  downloadAttachment(id: string, fileName: string) {
    this.contributionService.viewAttachment(id, false).subscribe(data => {
      saveAs(data, fileName);
    })
  }

  deleteAttachment(id: string) {
    let modal=this.modal.openNotificationModal('Delete Attachment','Are you sure to delete this attachment?',AlertType.warning,AlertMode.confirmation);
    modal.componentInstance.onSubmit.subscribe(response=>{
      if(response == true){
        this.contributionService.deleteAttachment(id).subscribe(data => {
          this.tabChanged(this.tabIndex);
        })
      }
      modal.close();
    });
    
  }
 
  payNow(contribution: Contributions){
    if(contribution.contributionStatus == this.contributionsMetadata.status.paid){
      this.modal.openNotificationModal('Error','You have already paid this donation.',AlertType.error,AlertMode.notification)
      return;
    }
    //window.location.replace("tez://upi/pay?pa=9123899870@axl&pn=Souvik&mc=0000&mode=02&purpose=00");

    
    this.contributionService.getQRCode(contribution.contributionId).subscribe((data:any)=>{
      if(isMobileDevice()){
        window.location.replace(data.upi);
      }else{
        let modalRef=this.modal.openQRModal(data.qr,"Pay using QR code","Please scan the above UPI QR code and complete payment. Payment will be confirmed only after receiving supporting screenshots in Official WhatsApp group. Upload screenshot in web portal to faster this process.");
      modalRef.componentInstance.setDefaultClose(false);

      modalRef.componentInstance.onExit.subscribe(d=>{
        let modalRef2=this.modal.openNotificationModal('Confirm Payment','Have you successfully paid this donation?',AlertType.info,AlertMode.confirmation)
        modalRef2.componentInstance.onSubmit.subscribe(data=>{
          if(data == true){
            this.addDocument(contribution,true);
            //send notification to cashier
            //this.modal.openNotificationModal('Info','We have notified about this payment. Please send supporting screenshots in Official WhatsApp group also upload screenshot in web portal.',AlertType.info,AlertMode.notification)
          }
          modalRef2.close()
          modalRef.close()
        })
      })
      
    
     }
   })
      
  }

  transformImage(base64Image:string): SafeResourceUrl {
    return this.sanitizer.bypassSecurityTrustResourceUrl(base64Image);
  }

  cotributionType = (code: string)=>{
    if(isEmpty(code) || isEmpty(this.contributionsReferencedata) ||isEmpty(this.contributionsReferencedata.contributionTypes)){
      return code;
    }
    return this.contributionsReferencedata.contributionTypes.find((obj: any)=>obj.key === code)?.displayValue || null;
  }

  cotributionStatus = (code: string)=>{
    if(isEmpty(code) || isEmpty(this.contributionsReferencedata) || isEmpty(this.contributionsReferencedata.contributionStatuses)){
      return code;
    }
    return this.contributionsReferencedata.contributionStatuses.find((obj: any)=>obj.key === code)?.displayValue || null;
  }

  paymentMethod = (code: string)=>{
    if(isEmpty(code) ||isEmpty(this.contributionsReferencedata) || isEmpty(this.contributionsReferencedata.paymentMethods)){
      return code;
    }
    return this.contributionsReferencedata.paymentMethods.find((obj: any)=>obj.key === code)?.displayValue || null;
  }

  exportPendingDonation(){
    this.contributionService.getContributionsProfileWise().subscribe(data => {
      let donation:any[]=[];
      for (var i=0;i<data.length;i++){
        //console.log(data[i].comprehensiveContributionSummary)
        donation.push({
          'Name' : data[i].firstName+' '+data[i].lastName,
          'Status' : data[i].status,
          'Total Pending Amount' : data[i].comprehensiveContributionSummary.totalOutstandingAmount,
          'Outstanding Months' : data[i].comprehensiveContributionSummary.outstandingMonths.join(',')
        });
       }
       this.excelService.exportAsExcelFile(new Map().set("Memberwise_Pending_Donation",donation),"Pending_Donation")
    });
  }

  exportPaidDonations(){
    
    this.contributionService.getAllContributions(new Map().set('status','PAID')).subscribe(data => {
      let donation:any[]=[];
      for (var i=0;i<data.length;i++){
        //console.log(data[i].comprehensiveContributionSummary)
        donation.push({
          'Donor Name' : data[i].profile ? (data[i].profile.firstName+' '+data[i].profile.lastName): data[i].guestFullName,
          'Type' : data[i].contributionType,
          'Month' : data[i].startDate ?new Date(data[i].startDate).toLocaleString('en-us',{month:'short', year:'numeric'}):'',
          'Status' : data[i].contributionStatus,
          'Amount' : data[i].amount,
          'Paid on' : new Date(data[i].paidOn),
          'Confirmed on' : new Date(data[i].confirmedOn),
          'Confirmed by' : data[i].confirmedBy,
        });
       }
       let dataToExport= new Map();
       dataToExport.set("Paid_Donation",donation)
       this.excelService.exportAsExcelFile(dataToExport,"Paid_Donation")
    });
  }

  visitProfile(event: MouseEvent,id:string) {
      event.stopPropagation();
    this.profileService.getReferenceData().subscribe(referenceData => {
      this.profileService.getProfile(id).subscribe(data=>{
        this.router.navigate([approutes.profile.url], { queryParams: { action: this.profileMetadata.action.view, backTo: 'DON~2' } ,state:{myProfile:data,profileReferenceData:referenceData}});
      })
    })
  }
  getUpiNames(names:string[]){
    if(names == null)
      return null;

    return  names.map(m=>availableUpis.find(f=>f.value === m)).map(m=>m?.name).toString();
    
  }

}
