import { AbstractControl, ValidatorFn } from "@angular/forms";

export function passwordMatchValidator(comparingFieldName:string): ValidatorFn {
  return (control: AbstractControl) => {
    const compareableValue = control.parent?.get(comparingFieldName)?.value;
    if(control.value==null || control.value==''){
      return null;
    }
    if(control.value!=compareableValue){
      return {
        'passwordMatchError': { message: ' Password Not matched.' }
      };
    }
    return null;
  };
}

export function samePasswordValidator(comparingFieldName:string): ValidatorFn {
  return (control: AbstractControl) => {
    const compareableValue = control.parent?.get(comparingFieldName)?.value;
    if(control.value==null || control.value==''){
      return null;
    }
    if(compareableValue!='' && control.value===compareableValue){
      return {
        'samePasswordError': { message: ' Password is same.' }
      };
    }
    return null;
  };
}


