import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { approutes } from 'src/app/constants/routes.config';
import { NoticeService } from 'src/app/services/resource/internal/notice.service';
import { AuthUserService } from 'src/app/services/utils/auth-user.service';

@Component({
  selector: 'app-notice-callback',
  template: '',
})
export class NoticeCallBackComponent implements OnInit {
  appRoutes=approutes;
  constructor(private notice:NoticeService,
    private route:ActivatedRoute,
    private authUserService:AuthUserService,
    private router:Router,

    ) { }

  ngOnInit(): void {
    this.route.queryParams.subscribe(params=>{
      console.log(params)
      let id=this.authUserService.getCustomState('noticeId') || '';
      this.notice.createNoticeMeeting(params.code,id).toPromise().then(data=>{
        this.router.navigate([this.appRoutes.viewNotice.url.replace(':id',data.id)]);
      }).catch(c=>{
        console.log(c)
        this.router.navigate([this.appRoutes.updateNotice.url.replace(':id',id)]);

      })
    });
  }

}
