import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { Title } from '@angular/platform-browser';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';
import { AngularEditorConfig } from '@kolkov/angular-editor';
import { AlertMode, AlertType } from 'src/app/components/modal/notification-modal/notification-modal.component';
import { NOTICE } from 'src/app/constants/notice.constant';
import { approutes } from 'src/app/constants/routes.config';
import { Attendee, MeetingInfo, NoticeObject } from 'src/app/models/notices/notice.model';
import { ProfileObject } from 'src/app/models/user/profile.model';
import { ModalService } from 'src/app/services/modal/modal.service';
import { NoticeService } from 'src/app/services/resource/internal/notice.service';
import { ProfileService } from 'src/app/services/resource/internal/profile.service';
import { compareObjects, HelperService, isEmptyObject } from 'src/app/services/utils/helper.service';
import { ThemePalette } from '@angular/material/core';
import { conditionalValidator } from 'src/app/validator/conditionally-required.validator';
import { AuthUserService } from 'src/app/services/utils/auth-user.service';

@Component({
  selector: 'app-create-notice',
  templateUrl: './create-notice.component.html',
  styleUrls: ['./create-notice.component.scss']
})
export class CreateNoticeComponent implements OnInit {
  roles!: string[];
  noticeAttrs!: NoticeObject;
  meetingAgendaConfig: AngularEditorConfig = {
  editable: true,
  spellcheck: true,
  showToolbar: false,
  placeholder: 'Enter meeting agenda',
  sanitize: true
  };

  descriptionConfig: AngularEditorConfig = {
    editable: true,
    spellcheck: true,
    height: '200px', 
    showToolbar: false,
    placeholder: 'Enter event description',
    sanitize: true
  };

  appRoutes=approutes;
  profiles!: ProfileObject[];
  constructor(
    private noticeService:NoticeService,
    private activeRoute: ActivatedRoute,
    private title: Title,
    private modal:ModalService,
    private router:Router,
    private profileService:ProfileService,
    private authUserService:AuthUserService
    ) {}

  noticeBoardForm:UntypedFormGroup=new UntypedFormGroup({
    noticeTitle: new UntypedFormControl('',[Validators.required,Validators.maxLength(100),Validators.minLength(5)]),
    noticeDescription: new UntypedFormControl('',[Validators.required,Validators.maxLength(1500),Validators.minLength(50)]),
    noticeDate: new UntypedFormControl(new Date(),[Validators.required]),
    publishAsRole: new UntypedFormControl('',[Validators.required]),
    noticeVisibility: new UntypedFormControl(NOTICE.states.internal,[Validators.required]),
    needMeeting: new UntypedFormControl(null,[Validators.required]),
    meetingInfo: new UntypedFormGroup({
      meetingSummary: new UntypedFormControl('',[conditionalValidator(() =>
        (this.noticeBoardForm.get('needMeeting')?.value === 'true'),
        Validators.required
      )]),
      meetingAgenda: new UntypedFormControl('',[]),
      meetingDate: new UntypedFormControl('',[conditionalValidator(() =>
        (this.noticeBoardForm.get('needMeeting')?.value === 'true'),
        Validators.required
      )]),
      meetingLocation: new UntypedFormControl('Virtually via Google Meet',[conditionalValidator(() =>
        (this.noticeBoardForm.get('needMeeting')?.value === 'true'),
        Validators.required
      )]),
      meetingStartTime: new UntypedFormControl('',[conditionalValidator(() =>
        (this.noticeBoardForm.get('needMeeting')?.value === 'true'),
        Validators.required
      )]),
      meetingEndTime: new UntypedFormControl('',[conditionalValidator(() =>
        (this.noticeBoardForm.get('needMeeting')?.value === 'true'),
        Validators.required
      )]),
    }) 
  });

  isUpdate:boolean=false;
  isDraft:boolean=false;
  noticeMetaData= NOTICE;
  ngOnInit(): void {
    this.roles=this.activeRoute.snapshot.data.publisherrole;
    this.noticeAttrs=this.activeRoute.snapshot.data.notice;
    if(this.roles.length !=0 && this.roles.filter(f=>f=="").length == this.roles.length){
      this.noticeBoardForm.get('publishAsRole')?.removeValidators(Validators.required);
    }

    if(this.activeRoute.snapshot.data.route.mode === 'create'){
      HelperService.setPageTitle(this.title,"Create Notice");  
      this.isUpdate=false;
      if(this.noticeAttrs){
        this.isDraft=true;
        this.noticeBoardForm.setValue({
          noticeTitle: this.noticeAttrs.title,
          noticeDescription:  this.noticeAttrs.description,
          noticeDate:  this.noticeAttrs.noticeDate,
          publishAsRole: this.noticeAttrs.creatorRole,
          noticeVisibility:  this.noticeAttrs.visibility,
          needMeeting : this.noticeAttrs.needMeeting == null ? null : (this.noticeAttrs.needMeeting?'true':'false'),
          meetingInfo: {
          meetingSummary: this.noticeAttrs.meetingInfo ? this.noticeAttrs.meetingInfo.summary : '',
          meetingAgenda: this.noticeAttrs.meetingInfo ? this.noticeAttrs.meetingInfo.description : '',
          meetingDate: this.noticeAttrs.meetingInfo ? this.noticeAttrs.meetingInfo.meetingDate : null,
          meetingLocation: this.noticeAttrs.meetingInfo ? this.noticeAttrs.meetingInfo.location : null,
          meetingStartTime: this.noticeAttrs.meetingInfo ? this.noticeAttrs.meetingInfo.startTime : null,
          meetingEndTime: this.noticeAttrs.meetingInfo ? this.noticeAttrs.meetingInfo.endTime : null,
        }
        });
      }
    }else if(this.activeRoute.snapshot.data.route.mode === 'edit'){
      HelperService.setPageTitle(this.title,"Update Notice");
      this.isUpdate=true;
      this.noticeBoardForm.patchValue({
        noticeTitle: this.noticeAttrs.title,
        noticeDescription:  this.noticeAttrs.description,
        noticeDate:  this.noticeAttrs.noticeDate,
        publishAsRole: this.noticeAttrs.creatorRole,
        noticeVisibility:  this.noticeAttrs.visibility,
        needMeeting : this.noticeAttrs.needMeeting ? 'true':'false',
        meetingInfo: {
          meetingSummary: this.noticeAttrs.meetingInfo ? this.noticeAttrs.meetingInfo.summary : '',
          meetingAgenda: this.noticeAttrs.meetingInfo ? this.noticeAttrs.meetingInfo.description : '',
          meetingDate: this.noticeAttrs.meetingInfo ? this.noticeAttrs.meetingInfo.meetingDate : null,
          meetingLocation: this.noticeAttrs.meetingInfo ? this.noticeAttrs.meetingInfo.location : null,
          meetingStartTime: this.noticeAttrs.meetingInfo ? this.noticeAttrs.meetingInfo.startTime : null,
          meetingEndTime: this.noticeAttrs.meetingInfo ? this.noticeAttrs.meetingInfo.endTime : null,
        }
      });
      this.noticeBoardForm.get('publishAsRole')?.removeValidators(Validators.required);
    }
    this.noticeBoardForm.controls.needMeeting.valueChanges.subscribe(value=>{
      //console.log(typeof value)
      if(value === 'true'){
        this.profileService.getAllProfiles({roleRequired:false}).subscribe(data=>{
          this.profiles=data;
        });
      }
      this.noticeBoardForm.controls.meetingInfo.get("meetingSummary")?.updateValueAndValidity();
      this.noticeBoardForm.controls.meetingInfo.get("meetingAgenda")?.updateValueAndValidity();
      this.noticeBoardForm.controls.meetingInfo.get("meetingDate")?.updateValueAndValidity();
      this.noticeBoardForm.controls.meetingInfo.get("meetingLocation")?.updateValueAndValidity();
      this.noticeBoardForm.controls.meetingInfo.get("meetingStartTime")?.updateValueAndValidity();
      this.noticeBoardForm.controls.meetingInfo.get("meetingEndTime")?.updateValueAndValidity();

    });
   
  }
  

  


  isRequiredField(controlName:string) :boolean{
    return this.noticeBoardForm.controls[controlName].hasValidator(Validators.required)
    || this.noticeBoardForm.controls[controlName].errors?.required;
  }

  get NBF(){
    return this.noticeBoardForm.controls;
  }

  discardDraft(){
    let modal=this.modal.openNotificationModal('Discard Notice','Are you sure to discard drafted notice?',AlertType.warning,AlertMode.confirmation);
    modal.componentInstance.onSubmit.subscribe(response=>{
      if(response == true){
        this.noticeService.deleteNotice(this.noticeAttrs.id,true).subscribe(data=>{
          this.noticeAttrs={} as any;
          this.noticeBoardForm.reset();
          this.noticeBoardForm.setValue({
            noticeTitle: '',
            noticeDescription: '',
            noticeDate:  new Date(),
            publishAsRole: '',
            noticeVisibility:  NOTICE.states.internal,
          });
          this.isDraft=false;
        })   //
      }
      modal.close();
    });
    
  }

  saveAsDraftNotice(){
   // if(this.noticeBoardForm.valid){
      let notice=new NoticeObject;
      notice.title=this.noticeBoardForm.value.noticeTitle;
      notice.description=this.noticeBoardForm.value.noticeDescription;
      notice.noticeDate=this.noticeBoardForm.value.noticeDate;
      notice.creatorRole=this.noticeBoardForm.value.publishAsRole;
      notice.visibility=this.noticeBoardForm.value.noticeVisibility;
      notice.isDraft=true;
      if(this.isDraft){  
      
        if(isEmptyObject(compareObjects(notice,this.noticeAttrs))){
          this.modal.openNotificationModal('Error','No changes have been made',AlertType.error,AlertMode.notification);
        }else{
          this.noticeService.updateNotice(this.noticeAttrs.id,compareObjects(notice,this.noticeAttrs)).subscribe(data=>{
            this.noticeAttrs=data;
          })
        }
      }else{
        this.noticeService.createNotice(notice).subscribe(data=>{
          this.noticeAttrs=data;
          this.isDraft=true;
        })
      }
    // }else{
    //   this.noticeBoardForm.markAllAsTouched();
    // }
    
  }

  publishNotice(){

    if(this.noticeBoardForm.valid){
      let notice=new NoticeObject;
      notice.title=this.noticeBoardForm.value.noticeTitle;
      notice.description=this.noticeBoardForm.value.noticeDescription;
      notice.noticeDate=this.noticeBoardForm.value.noticeDate;
      notice.creatorRole=this.noticeBoardForm.value.publishAsRole;
      notice.visibility=this.noticeBoardForm.value.noticeVisibility;
      notice.isDraft=false;
      notice.needMeeting=this.noticeBoardForm.value.needMeeting === 'true' ?true :false;
      if(notice.needMeeting){
        let mInfoVal=this.noticeBoardForm.value.meetingInfo;
        //console.log(mInfoVal)
        let mInfo= new MeetingInfo;
        mInfo.summary=mInfoVal.meetingSummary; 
        mInfo.description=mInfoVal.meetingAgenda;
        mInfo.meetingDate=mInfoVal.meetingDate;
        mInfo.location=mInfoVal.meetingLocation;
        mInfo.startTime=mInfoVal.meetingStartTime;
        mInfo.endTime=mInfoVal.meetingEndTime; 
        mInfo.attendees=this.profiles.map(m=> new Attendee( m.firstName+' '+m.lastName,m.email));
        mInfo.emailReminderBeforeMin=[60,1440];
        mInfo.popupReminderBeforeMin=[15,30];
        notice.meetingInfo=mInfo;
        notice.redirectUrl=window.location.origin+this.appRoutes.noticeCallback.url;
      }
      let pText = notice.needMeeting ? "You will be redirected to Google's authorization page to create meeting. <br>Are you sure to publish this notice?": 'Are you sure to publish this notice?';
      let modal=this.modal.openNotificationModal('Publish Notice',pText,AlertType.warning,AlertMode.confirmation);
      modal.componentInstance.onSubmit.subscribe(response=>{
        if(response == true){
          if(this.isDraft){

            //notice.redirectUrl=window.location.origin+this.appRoutes.viewNotice.url.replace(':id',this.noticeAttrs.id);  
            this.noticeService.updateNotice(this.noticeAttrs.id,notice).subscribe(data=>{
              this.noticeAttrs=data;
              this.isDraft=false;
              if(data.needMeeting && data.authorizationUrl){
                this.authUserService.setCustomState('noticeId',data.id)
                window.location.href = data.authorizationUrl;
              }else{
                this.router.navigate([this.appRoutes.viewNotice.url.replace(':id',data.id)]);
              }
            })
          }else{
            this.noticeService.createNotice(notice).subscribe(data=>{
              this.noticeAttrs=data;
              if(data.needMeeting && data.authorizationUrl){
                // let updateN=new NoticeObject;
                // updateN.redirectUrl=window.location.origin+this.appRoutes.viewNotice.url.replace(':id',this.noticeAttrs.id);
                // this.noticeService.updateNotice(this.noticeAttrs.id,updateN).subscribe(p=>{
                  this.authUserService.setCustomState('noticeId',data.id)
                  window.location.href = data.authorizationUrl;
                //})
              }else{
                this.router.navigate([this.appRoutes.viewNotice.url.replace(':id',data.id)]);
              }
            })
          }
        }
        modal.close();
      });
      

    }else{
      this.noticeBoardForm.markAllAsTouched();
    }
    
  }

  updateNotice(){
    if(this.noticeBoardForm.valid){
      let notice=new NoticeObject;
      notice.title=this.noticeBoardForm.value.noticeTitle;
      notice.description=this.noticeBoardForm.value.noticeDescription;
      notice.noticeDate=this.noticeBoardForm.value.noticeDate;
      notice.creatorRole=this.noticeBoardForm.value.publishAsRole;
      notice.visibility=this.noticeBoardForm.value.noticeVisibility;
      notice.isDraft=false;
      if(isEmptyObject(compareObjects(notice,this.noticeAttrs))){
        this.modal.openNotificationModal('Error','No changes have been made',AlertType.error,AlertMode.notification);
      }else{
        
        let modal=this.modal.openNotificationModal('Update Notice','Are you sure to update this notice?',AlertType.warning,AlertMode.confirmation);
      modal.componentInstance.onSubmit.subscribe(response=>{
        if(response == true){
          this.noticeService.updateNotice(this.noticeAttrs.id,compareObjects(notice,this.noticeAttrs)).subscribe(data=>{
            this.noticeAttrs=data;
            this.router.navigate([this.appRoutes.viewNotice.url.replace(':id',data.id)]);
          })
        }
        modal.close();
      });
      }
      
      
    }else{
      this.noticeBoardForm.markAllAsTouched();
    }
   
  }

  // back(notice?:NoticeObject){
  //   if(notice){
  //     this.router.navigate([this.appRoutes.noticeBoard.url],{state:{viewNotice:notice}});
  //     // this.noticeService.getNotices().subscribe(allNotices=>{
  //     //   this.router.navigate([this.appRoutes.noticeBoard],{state:{notices:allNotices,fragment:notice.id}});
  //     // })
  //   }else{
  //     this.noticeService.getNotices().subscribe(allNotices=>{
  //       this.router.navigate([this.appRoutes.noticeBoard.url],{state:{notices:allNotices}});
  //     })
  //   }
    
  // }

}
