import { Component, Inject, OnInit, TemplateRef } from '@angular/core';
// import { AngularFireMessaging } from '@angular/fire/messaging';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { OAuthEvent, OAuthService } from 'angular-oauth2-oidc';
import { SpinnerVisibilityService } from 'ng-http-loader';
import { approutes } from 'src/app/constants/routes.config';


@Component({
  selector: 'app-callback',
  template: `
  <app-header showNav="true" *ngIf="showPage"></app-header>
  <div class="flex items-center justify-center h-screen bg-gray-200" *ngIf="showPage">
    <div class="container">
      <div class="bg-white rounded-lg shadow-lg p-5 md:p-20 mx-2">
        <div class="text-center">
          <h2 class="text-4xl tracking-tight leading-10 font-extrabold text-gray-900 sm:text-5xl sm:leading-none md:text-6xl">
            <span class="text-indigo-600 uppercase">{{heading}}</span>
          </h2>
          <p class="text-md md:text-xl mt-10 uppercase">{{message}}</p>
        </div>

      </div>
    </div>
  </div>
  <app-footer *ngIf="showPage"></app-footer>

  `,
})
export class CallbackComponent implements OnInit {
  heading:string='Processing Request';
  message:string='Please wait...';
  showPage: boolean = false;
  constructor(
    private router:Router,
    private oAuthService:OAuthService,
    private activeRoute: ActivatedRoute,

    ) {
    //console.log('callback')
  }
  ngOnInit(): void {
    this.oAuthService.events.subscribe(({ type }: OAuthEvent) => {
      switch (type) {
        case 'token_received':
         this.heading='Almost done';
         
         this.activeRoute.queryParams
          .subscribe(params => {
            if(params.navigate_url){
              this.router.navigate([params.navigate_url]);
            }else{
              window.location.href= window.location.origin+approutes.home.url;
            }
          });
          break;
        }
    });

    this.activeRoute.queryParams
    .subscribe(params => {
      console.log(params);
      if(params.error){
        this.showPage=true;
        this.heading=params.error;
        this.message=params.error_description;
      }
    }
  );
  }
 


  

}
