import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { resourceEndpointConfig } from 'src/app/constants/resource-endpoint.config';
import { ProfileObject } from 'src/app/models/user/profile.model';
import { auth0Config } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ProfileService {
  deleteProfile(id: string) : Observable<any>{
    return this.httpClient.delete(resourceEndpointConfig.profile.profiles + '/' + encodeURIComponent(id));
  }
  


  constructor(
    private httpClient: HttpClient,

  ) { }

  getMyProfile(options: { roleRequired: boolean }): Observable<ProfileObject> {
    // this.getUserInfo().subscribe(data=>{
    //   console.log(data)
    //   //return this.getProfile(data);
    // })
    return this.httpClient.get(resourceEndpointConfig.profile.myProfile, { params: { roleRequired: options.roleRequired } })
      .pipe(
        map((data: any) => {
          return data.responsePayload;
        })
      );
  }

  getUserInfo(): Observable<any>{
    return this.httpClient.get(auth0Config.issuer+'userinfo');
  }

  getAllProfiles(options: { roleRequired: boolean}): Observable<ProfileObject[]> {
    return this.httpClient.get(resourceEndpointConfig.profile.profiles,{ params: { roleRequired: options.roleRequired } })
      .pipe(
        map((data: any) => {
          return data.responsePayload;
        })
      );
  }

  getProfile(id: string): Observable<ProfileObject> {
    return this.httpClient.get(resourceEndpointConfig.profile.profiles + '/' + encodeURIComponent(id))
      .pipe(
        map((data: any) => {
          return data.responsePayload;
        })
      );
  }

  getReferenceData(): Observable<any> {
    return this.httpClient.get(resourceEndpointConfig.profile.referenceData)
      .pipe(
        map((data: any) => {
          return data.responsePayload;
        })
      );
  }

  updateMyProfile(profile: ProfileObject): Observable<ProfileObject> {
    return this.httpClient.patch(resourceEndpointConfig.profile.myProfile, profile)
      .pipe(
        map((data: any) => {
          return data.responsePayload;
        })
      );
  }

  updateProfileAttribute(id: string, profile: ProfileObject): Observable<ProfileObject> {
    return this.httpClient.patch(resourceEndpointConfig.profile.profiles + '/' + encodeURIComponent(id), profile)
      .pipe(
        map((data: any) => {
          return data.responsePayload;
        })
      );
  }

  changeProfilePicture(file: File) :  Observable<any>{
    let headers = new HttpHeaders();
    //this is the important step. You need to set content type as null
   // headers.set('Content-Type', null);
    headers.set('Accept', "multipart/form-data");
    let params = new HttpParams();
    const formData: FormData = new FormData();
    formData.append('profilePicture', file,file.name);
    
    return this.httpClient.post(resourceEndpointConfig.profile.changeMyProfilePicture, formData, { params, headers });

  }


  removeProfilePicture() {
    return this.httpClient.post(resourceEndpointConfig.profile.removeMyProfilePicture, null);
  }
}
