import { DatePipe } from '@angular/common';
import { Pipe, PipeTransform } from '@angular/core';
import { EventsObject } from 'src/app/models/events/event.model';

@Pipe({
  name: 'eventSearchFilter'
})
export class EventSearchPipe implements PipeTransform {
  private datePipe = new DatePipe("en-US");
  private format='MMMM y';

  transform(events: EventsObject[], searchValue: string): EventsObject[] {
    if(!events || !searchValue){
      return events;
    }
    return events.filter((event:EventsObject)=> 
      event.eventDescription.toLowerCase().includes(searchValue.toLowerCase())  
      ||
      event.eventLocation.toLowerCase().includes(searchValue.toLowerCase())
      ||
      event.eventTitle.toLowerCase().includes(searchValue.toLowerCase())
      ||
      event.eventStage.toLowerCase().includes(searchValue.toLowerCase())
      ||
      this.datePipe.transform(event.eventDate,this.format)!.toLowerCase().includes(searchValue.toLowerCase())
    );
  }

}
