import { DatePipe } from '@angular/common';
import { Pipe, PipeTransform } from '@angular/core';
import { Contributions, ProfileContribution } from 'src/app/models/contributions/contributions.model';
import { isEmpty } from 'src/app/services/utils/helper.service';

@Pipe({
  name: 'contributionSearch'
})
export class ContributionSearchPipe implements PipeTransform {
  datePipe = new DatePipe("en-US");

  transform(contributions: Contributions[], search:string): any {
    //console.log(search,contributions)
    if(!contributions || !search){
      return contributions;
    }
    let format='MMMM y';
    search=search.toLowerCase();
    return contributions.filter((contribution:Contributions)=>

      (contribution.amount != null && contribution.amount.toString().includes(search))
      ||
      (contribution.comment != null && contribution.comment.toLowerCase().includes(search)) 
      ||
      (contribution.confirmedBy != null && contribution.confirmedBy.toLowerCase().includes(search)) 
      ||
      (contribution.confirmedOn != null && this.datePipe.transform(Date.parse(contribution.confirmedOn),format)!.toLowerCase().includes(search)) 
      ||
      (contribution.endDate != null && this.datePipe.transform(Date.parse(contribution.endDate),format)!.toLowerCase().includes(search)) 
      ||
      (contribution.startDate != null && this.datePipe.transform(Date.parse(contribution.startDate),format)!.toLowerCase().includes(search)) 
      ||
      (contribution.raisedOn != null && this.datePipe.transform(Date.parse(contribution.raisedOn),format)!.toLowerCase().includes(search)) 
      ||
      (contribution.contributionStatus != null && contribution.contributionStatus.toLowerCase().includes(search)) 
      ||
      (contribution.contributionType != null && contribution.contributionType.toLowerCase().includes(search)) 
      ||
      (contribution.guestFullName != null && contribution.guestFullName.toLowerCase().includes(search)) 
      ||
      (contribution.guestEmail != null && contribution.guestEmail.toLowerCase().includes(search)) 
      ||
      (contribution.guestMobile != null && contribution.guestMobile.toLowerCase().includes(search)) 
      ||
      (contribution.profile != null && contribution.profile.firstName.toLowerCase().includes(search)) 
      ||
      (contribution.profile != null && contribution.profile.lastName.toLowerCase().includes(search)) 
      ||
      (contribution.profile != null && contribution.profile.email.toLowerCase().includes(search)) 
    );
  }

}

@Pipe({
  name: 'profileContributionSearch'
})
export class ProfileContributionSearchPipe implements PipeTransform {
  datePipe = new DatePipe("en-US");

  transform(contributions: ProfileContribution[], search:string): any {
   // console.log(search,contributions)
    if(!contributions || !search){
      return contributions;
    }
    search=search.toLowerCase();
    return contributions.filter((profile:ProfileContribution)=>

      (profile.firstName != null && profile.firstName.toLowerCase().includes(search))
      ||
      (profile.lastName != null && profile.lastName.toLowerCase().includes(search)) 
      ||
      (profile.middleName != null && profile.middleName.toLowerCase().includes(search)) 
      ||
      (profile.email != null && profile.email.toLowerCase().includes(search)) 
    );
  }
}







