  ngOnInit(): void {
    
    //this.notification=this.motificationService.currentMessage;
    
    
    this.networkCallDashboard=true;
    this.dashboardService.getComprehensiveSummary({member:true,notice:true,notification:true,donation:true}).subscribe((data:ComprehensiveSummaryObject)=>{
      this.comprehensiveSummary=data;
      this.networkCallDashboard=false;
      //
      this.authUserService.setUserInactive(data.members.isInactive);
    },error=>{
      this.networkCallDashboard=false;
    });
    this.greetingsMessage=this.generateGreetingsMessage(this.authUserService.getName());
    
    this.canReadExpenditures=this.authUserService.isAccrediatedToAny(SCOPE.read.expenditure,SCOPE.read.funds);
    this.canReadContribution=this.authUserService.isAccrediatedToAny(SCOPE.read.my_contribution,SCOPE.read.contribution);
    this.canReadAllProfile=this.authUserService.isAccrediatedTo(SCOPE.read.all_profiles)
    this.canReadEvents=this.authUserService.isAccrediatedTo(SCOPE.read.events);
    this.canReadRequests=this.authUserService.isAccrediatedToAny(SCOPE.read.self_request,SCOPE.read.join_request);
    this.canReadNotice=this.authUserService.isAccrediatedTo(SCOPE.read.notice);
    this.canReadContactRequest=this.authUserService.isAccrediatedTo(SCOPE.read.contact_request);
    this.canMonitorApplication=this.authUserService.isAccrediatedToAny(SCOPE.read.actuators);
    if(this.canMonitorApplication){
      // this.networkCallAdmin=true;
      // this.dashboardService.getApplicationHealth().subscribe(data=>{
      //   this.appState=data;
      //   this.networkCallAdmin=false;
      // },error=>{
      //   this.networkCallAdmin=false;
      // })
    }
  }

  gotoEvents(){
    this.eventService.getEventsInternal().subscribe(data=>{
      this.router.navigate([this.appRoutes.ourEvents.url],{state:{allEvents:data}});
    })
  }

  gotoRequests(){
    this.requestService.getRequests({includeOutstanding:true,includeResolved:false}).subscribe((requests: RequestObject[]) => {
      this.router.navigate([this.appRoutes.requests.url],{state:{outstandingRequests:requests,requestSummary:this.comprehensiveSummary.requests}});
    });
  }


  gotoFundsAndExpenditure(){
    if(this.authUserService.isAccrediatedTo(SCOPE.read.funds)){
      this.fundsAndExpenditureService.getEarningDetails().subscribe(data => {
        this.router.navigate([this.appRoutes.savings.url],{state:{fundAndExpense:data}});
      });
    }else{
      this.fundsAndExpenditureService.getExpenditureDetails().subscribe(data => {
        this.router.navigate([this.appRoutes.savings.url],{state:{fundAndExpense:data}});
      });
    }
   
  }

  

