  private generateGreetingsMessage(name:string): string{
    var hour = new Date().getHours();
    if(hour>=5 && hour<12){
     return 'good morning, '+name;
    } else if(hour>=12 && hour<18){
      return 'good afternoon, '+name;
    }else{
      return 'good evening, '+name;
    }
  }

