import { ChangeDetectorRef, Component, EventEmitter, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { Title } from '@angular/platform-browser';
import { SpinnerVisibilityService } from 'ng-http-loader';
import { ReCaptcha2Component } from 'ngx-captcha';
import { AlertMode, AlertType } from 'src/app/components/modal/notification-modal/notification-modal.component';
import { SupportRequest } from 'src/app/models/support-requests/support-requests.model';
import { ModalService } from 'src/app/services/modal/modal.service';
import { PublicApiService } from 'src/app/services/resource/public/public-api.service';
import { HelperService } from 'src/app/services/utils/helper.service';
import { phoneNumberValidator } from 'src/app/validator/phone-number.validator';
import { trimValidator } from 'src/app/validator/trim.validator';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {

  contactForm!: UntypedFormGroup;
  searchQueryForm!: UntypedFormGroup;
  siteKey!:string;
  countries: any;
  searchEvent!: EventEmitter<any>;

  @ViewChild("searchTemplate")
  searchTemplate!: TemplateRef<any>;
  @ViewChild("updateActionbuttons")
  updateActionbuttons!: TemplateRef<any>;

  @ViewChild('captchaElem')
  captchaElem!: ReCaptcha2Component;

  requestSearchResult: SupportRequest[] | undefined;
  
  constructor(
    private titleService: Title,
    private registrationService: PublicApiService,
    private modal: ModalService,
    private spinner: SpinnerVisibilityService,

    ) {
    //this.titleService.setTitle(this.titleService.getTitle().split("-")[0] + " - " + "Contact Us");
    HelperService.setPageTitle(this.titleService,'Contact Us');
  }

  get f() {
    return this.contactForm.controls;
  }

  get g() {
    return this.searchQueryForm.controls;
  }

  ngOnInit(): void {
    this.siteKey=environment.googleSiteKey;
    this.contactForm = new UntypedFormGroup({
      fullname: new UntypedFormControl('', [Validators.required, Validators.pattern('[a-zA-Z ]*'), Validators.minLength(2), Validators.maxLength(30), trimValidator('Fullname')]),
      code: new UntypedFormControl('91', [Validators.required]),
      mobile: new UntypedFormControl('', [Validators.required, phoneNumberValidator('Mobile number', 'code')]),
      email: new UntypedFormControl('', [Validators.required, Validators.email, trimValidator('Email address')]),
      query: new UntypedFormControl('', [Validators.required, Validators.maxLength(250), Validators.minLength(10), trimValidator('Query')]),
      recaptcha: new UntypedFormControl('', [Validators.required]),

    });
    this.contactForm.get('code')?.valueChanges
        .subscribe(value => {
          this.contactForm.get('mobile')?.updateValueAndValidity();
        });
        this.registrationService.getRefData().subscribe((response:any)=>{
          this.countries=response;
        });
  }

  onSubmit(){
    //console.log(this.contactForm.controls)
    if (this.contactForm.valid) {
      let email= this.contactForm.value.email;
      let mobile=this.contactForm.value.code+' '+this.contactForm.value.mobile;
      let token=this.contactForm.value.recaptcha;
      let dialogObject=this.modal.openNotificationModal('Confirm Details',
      'Please ensure that your contact details are correct.</br><b>Email address</b>: '+email+' </br><b>Phone number</b> : +'+mobile+
      '</br></br>Are you sure to continue?'
      ,AlertType.warning,AlertMode.confirmation)
      dialogObject.componentInstance.onSubmit.subscribe(decision=>{
        if(decision){
          let request=new SupportRequest;
          request.fullName=this.contactForm.value.fullname;
          request.phoneCode=this.contactForm.value.code;
          request.phoneNumber=this.contactForm.value.mobile;
          request.email=this.contactForm.value.email;
          request.description=this.contactForm.value.query;
          this.spinner.show();
          this.registrationService.submitQuery(request,token).subscribe(data=>{
            this.ngOnInit();
            this.spinner.hide();
            this.contactForm.reset();
            this.captchaElem.resetCaptcha();

          })
        }
        dialogObject.close();
      })
    }else{
      this.contactForm.markAllAsTouched();
    }
    
  }

  knowRequestStatus(){
    this.requestSearchResult=undefined;
    this.searchEvent = new EventEmitter();
    this.searchQueryForm=new UntypedFormGroup({
      requestId: new UntypedFormControl('', [Validators.required]),
      code: new UntypedFormControl('', [Validators.required]),
    });

    this.modal.openDialog({
      body: {
        template: this.searchTemplate
      },
      headers: {
        headerText: 'Know Your Query Status'
      },
      action: { buttonTemplate: this.updateActionbuttons, buttonContext: { buttons: { cancel: 'Exit', add: 'Search' } } },
      dimention: { width: 700 }
    });

    this.searchEvent.subscribe(decision => {
      if (decision === true) {
        if (this.searchQueryForm.valid) {
          let id=this.searchQueryForm.value.requestId;
          let code=this.searchQueryForm.value.code;
          this.registrationService.getQueryStatus(id,code).subscribe(data=>{
            this.requestSearchResult=data;
          })
        }else{
          this.searchQueryForm.markAllAsTouched();
        }
      }
    });

  }

  

}
