import { Directive, ElementRef, HostListener, Input } from '@angular/core';

@Directive({
  selector: '[appMoveNextByMaxLength]'
})
export class MoveNextByMaxLengthDirective {

  constructor(private _el: ElementRef) { }
  @Input('appMoveNextByMaxLength')
  appMoveNextByMaxLength!: any;

  @HostListener('keyup', ['$event']) onKeyDown(e: any) {
    if(e.srcElement.value.length>e.srcElement.maxLength){
      e.srcElement.value=String(e.srcElement.value).substring(0,e.srcElement.maxLength);
    }
    if (e.srcElement.maxLength === e.srcElement.value.length) {
      e.preventDefault();
      let nextControl: any = e.srcElement.nextElementSibling;
      while (true) {
        if (nextControl) {
          if (nextControl.type === e.srcElement.type) {
            nextControl.focus();
            return;
          }
          else {
            nextControl = nextControl.nextElementSibling;
          }
        }
        else {
          return;
        }
      }
    }else if(e.srcElement.value.length===0){
      e.preventDefault();
      let nextControl: any = e.srcElement.previousElementSibling;
      while (true) {
        if (nextControl) {
          if (nextControl.type === e.srcElement.type) {
            nextControl.focus();
            return;
          }
          else {
            nextControl = nextControl.previousElementSibling;
          }
        }
        else {
          return;
        }
      }
    }
  }
}
