import { HttpClient, HttpHeaders, HttpParams, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { resourceEndpointConfig } from 'src/app/constants/resource-endpoint.config';
import { Contributions, ProfileContribution } from 'src/app/models/contributions/contributions.model';
import { HistoryObject } from 'src/app/models/history/history.model';
import { Pagination } from 'src/app/models/pagination/page.model';

@Injectable({
  providedIn: 'root'
})
export class ContributionService {
  
  
  constructor(
    private httpClient: HttpClient,
  ) { }

  getAllContributions(filter?:Map<string,any>) : Observable<any> {
    let params:any={};
    
    if(filter && filter.has('pageSize') && filter.has('pageIndex')){
      params.pageSize=filter.get('pageSize');
      params.pageIndex=filter.get('pageIndex');
    }
    if(filter && filter.has('status')){
      params.status=filter.get('status');
    }
    if(filter && filter.has('guest')){
      params.guest=filter.get('guest');
    }
    return this.httpClient.get(resourceEndpointConfig.contributions.contribution,{params:params})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );

  }

  getMyContributions() : Observable<ProfileContribution>{
    return this.httpClient.get(resourceEndpointConfig.contributions.getMyContributions)
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  getMyContributionsAndHistory(id:string) : Observable<Contributions>{
    const params =new HttpParams().set('contributionId',id);
    return this.httpClient.get(resourceEndpointConfig.contributions.getMyContributions,{params:params})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  getContributionsProfileWise() : Observable<ProfileContribution[]>{
    return this.httpClient.get(resourceEndpointConfig.contributions.getContributionsMemberWise)
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  getQRCode(contributionId: string) : Observable<any>{
    // const param = new HttpParams()
    // .set('payeeKey','')
    // .set('upiIdKey','')
    return this.httpClient.get(resourceEndpointConfig.contributions.getQRCode.replace('${id}',contributionId))
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  } 

  getReferenceData() : Observable<any>{
    return this.httpClient.get(resourceEndpointConfig.contributions.referenceData)
    .pipe(
      map((data: any) => {
        return data.responsePayload; 
      })
    );
  }

  createContribution(data:Contributions,isGuest:boolean=false) : Observable<Contributions>{
    return this.httpClient.post(resourceEndpointConfig.contributions.contribution,data,{params:{isGuest:isGuest}})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }


  updateContribution(id:string,body:Contributions) : Observable<Contributions>{
    return this.httpClient.patch(resourceEndpointConfig.contributions.updateContribution.replace('${id}',id),body)
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  getHistory(id:string) : Observable<HistoryObject[]>{
    return this.httpClient.get(resourceEndpointConfig.contributions.history.replace('${id}',id))
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  deleteDonation(id:string) : Observable<any>{
    return this.httpClient.delete(resourceEndpointConfig.contributions.updateContribution.replace('${id}',id))
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  viewAttachment(id: string, viewOnly: boolean): Observable<any> {
    if (viewOnly) {
      const headers = new HttpHeaders()
        .set('Content-Type', 'application/json');
      return this.httpClient.get(resourceEndpointConfig.contributions.attachment.replace('${id}', id), { headers: headers,params:{viewOnly:true} })
        .pipe(
          map((data: any) => {
            return data.responsePayload;
          })
        );
    }
    return this.httpClient.get(resourceEndpointConfig.contributions.attachment.replace('${id}', id), { responseType: 'blob' });
  }

  deleteAttachment(id:string) : Observable<any>{
    return this.httpClient.delete(resourceEndpointConfig.contributions.attachment.replace('${id}',id))
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  addAttachment(id: string, files: File[]) : Observable<any>{
    const headers = new HttpHeaders()
    //this is the important step. You need to set content type as null
    // headers.set('Content-Type', null);
    headers.set('Accept', "multipart/form-data");
    const formData: FormData = new FormData();
    files.forEach(file => {
      formData.append('file', file, file.name);
    })
    // let params = new HttpParams()
    // params.set('isPicture',isImage);
    return this.httpClient.post(resourceEndpointConfig.contributions.addAttachment.replace('${contributionId}', id), formData, {headers: headers });

  }

  updateSelfDonation(data:Contributions,isPaid:boolean) : Observable<Contributions>{
    return this.httpClient.post(resourceEndpointConfig.contributions.getMyContributions,{},{params:{isPaid:isPaid,contributionId:data.contributionId}})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  
}
