import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ProfileObject } from 'src/app/models/user/profile.model';
import { PublicApiService } from 'src/app/services/resource/public/public-api.service';
import { HelperService } from 'src/app/services/utils/helper.service';

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.scss']
})
export class AboutComponent implements OnInit {
  membersData!: ProfileObject[];

  constructor(
    private titleService: Title,
    private apiService: PublicApiService,
    ) {
    ///this.titleService.setTitle(this.titleService.getTitle().split("-")[0] + " - " + "About Us");
    HelperService.setPageTitle(this.titleService,'About Us');

  }


  ngOnInit(): void {
    this.apiService.getMemberDetails().subscribe(data=>{
      this.membersData=data;

    });

  }

}
