import { AbstractControl, ValidatorFn } from "@angular/forms";

export class DateValidators {
    static dateLessThan(dateField1: string, dateField2: string, validatorField: { [key: string]: boolean }): ValidatorFn {
        return (c: AbstractControl): { [key: string]: boolean } | null => {
            console
            const date1 = c.parent?.get(dateField1)?.value;
            const date2 = c.parent?.get(dateField2)?.value;
            console.log('date1='+date1+"   "+'date2='+date2)
            if ((date1 !== null && date2 !== null) && date1 < date2) {
                console.log(validatorField)

                return validatorField;
            }
            return null;
        };
    }
    static dateGreterThan(dateField1: string, dateField2: string, validatorField: { [key: string]: boolean }): ValidatorFn {
        return (c: AbstractControl): { [key: string]: boolean } | null => {
            const date1 = c.parent?.get(dateField1)?.value;
            const date2 = c.parent?.get(dateField2)?.value;
            console.log('date1='+date1+"   "+'date2='+date2)
            console.log('result='+date1>date2)
            if ((date1 !== null && date2 !== null) && date1 > date2) {
                console.log(validatorField)

                return validatorField;
            }
            return null;
        };
    }
  }