import { Injectable, Optional } from '@angular/core';
import {
  Router, Resolve,
  RouterStateSnapshot,
  ActivatedRouteSnapshot
} from '@angular/router';
import { map } from 'rxjs/operators';
import { forkJoin, Observable, of } from 'rxjs';
import { NoticeObject } from 'src/app/models/notices/notice.model';
import { NoticeService } from '../resource/internal/notice.service';

@Injectable({
  providedIn: 'root'
})
export class NoticeResolver implements Resolve<NoticeObject[]> {
  constructor(private notice:NoticeService){}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<NoticeObject[]> {
    return this.notice.getNotices();
  }
}

@Injectable({
  providedIn: 'root'
})
export class NoticeDataResolver implements Resolve<NoticeObject> {
  constructor(private notice:NoticeService){}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<NoticeObject> {
    let id=route.paramMap.get('id');
    if(id){
      return this.notice.getNotice(id);
    }else{
      return this.notice.getDraftNotice();
    }
  }
}

@Injectable({
  providedIn: 'root'
})
export class NoticePublisherRoleResolver implements Resolve<string[]> {
  constructor(private notice:NoticeService){}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<string[]> {
    return this.notice.getAvailableRoleNotice();
  }
}

@Injectable({
  providedIn: 'root'
})
export class ViewNoticeResolver implements Resolve<{action:string,notice:NoticeObject,roles:string[]}> {
  constructor(private notice:NoticeService){}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<{action:string,notice:NoticeObject,roles:string[]}> {
    let id=route.paramMap.get('id')||'null';
    return forkJoin([
      this.notice.getNotice(id),
      this.notice.getAvailableRoleNotice()  
    ]).pipe(map(result => {
      return {
          notice: result[0],
          roles: result[1],
          action : 'create'
      };
  }));
};
}

@Injectable({
  providedIn: 'root'
})
export class CreateNoticeResolver implements Resolve<{draftedNotice:NoticeObject,roles:string[]}> {
  constructor(private notice:NoticeService){}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<{action:string,draftedNotice:NoticeObject,roles:string[]}> {
     return forkJoin([
        this.notice.getDraftNotice(),
        this.notice.getAvailableRoleNotice()  
      ]).pipe(map(result => {
        return {
            draftedNotice: result[0],
            roles: result[1],
            action : 'create'
        };
    }));
};
  
  }

