import { HttpErrorResponse, HttpHandler, HttpHeaderResponse, HttpHeaders, HttpInterceptor, HttpProgressEvent, HttpRequest, HttpResponse, HttpSentEvent, HttpStatusCode, HttpUserEvent } from '@angular/common/http';
import { EventEmitter, Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { retry, catchError, switchMap, tap } from 'rxjs/operators';
import { HelperService } from '../utils/helper.service';
import { AlertMode, AlertType } from 'src/app/components/modal/notification-modal/notification-modal.component';
import { OAuthService } from 'angular-oauth2-oidc';
import { environment, RESOURCE_API_CONFIG } from 'src/environments/environment';
import { ModalService } from '../modal/modal.service';
import { actuator_api_path, internal_api_path } from 'src/app/constants/resource-endpoint.config';

@Injectable({
  providedIn: 'root'
})
export class RequestIntercepterService implements HttpInterceptor {


  constructor(
      private oAuthService: OAuthService,
      private modalService: ModalService,
    ) {}


  private addTokenIfRequired(request:HttpRequest<any>) :HttpRequest<any>{
    if(
      request.url.includes(RESOURCE_API_CONFIG.base_url+internal_api_path) 
      || 
      request.url.includes(RESOURCE_API_CONFIG.base_url+actuator_api_path)
      || 
      request.url.includes('/userinfo')
      ){
      return request.clone({ 
        
        headers: request.headers.set('Authorization', 'Bearer ' +this.oAuthService.getAccessToken()),
      });
    }
  return request;
}
  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpSentEvent | HttpHeaderResponse | HttpProgressEvent | HttpResponse<any> | HttpUserEvent<any>> {
    var showError=request.headers.get('hideError') ==  null ? true: false;
    //request.headers
    return next.handle(this.addTokenIfRequired(request))
      .pipe(
        retry(0),
        tap(event=>{
          if (event instanceof HttpResponse) {
            //console.log(event);
            if(event.body && !HelperService.isEmpty(event.body.messages) && HelperService.isEmpty(event.body.error)){
              //console.log(event);
              this.showInformationMessage(event);
            }
          }
        }),
        catchError((error) => {
          if(showError){
            if (error instanceof HttpErrorResponse) {
              this.showErrorResponse(error);
            }else if (error.error instanceof ErrorEvent) {
              this.showErrorResponse(error);
            }
          }
          return throwError(error);
        })
      )
  }

  private showErrorResponse(errorResponse:any){
    console.log(errorResponse);
    let message:string; 
    let heading:string='Error'; 
    if(errorResponse.status === HttpStatusCode.Forbidden && !errorResponse.url.includes(environment.tokenEndpoint)){
      message='Sorry!! You do not have permission to execute this task.';
    }
    else if(HelperService.isEmpty(errorResponse.error)){
      message=errorResponse.message;
    }
    else if(!HelperService.isEmpty(errorResponse.error.messages)){
      message=new Array(errorResponse.error.messages).toString();
      heading=errorResponse.error.info;
    }else if(!HelperService.isEmpty(errorResponse.error.message)){
      message=errorResponse.error.message;
      heading=errorResponse.error.info;
    }else if(!HelperService.isEmpty(errorResponse.error.error_description)){
      message=errorResponse.error.error_description;
    }
    else{
      message='Something went wrong.';
    }  
    /*else if(!HelperService.isEmpty(errorResponse.error.errors)){
      var msgs = new Array(); 
      for (var errorObj of  errorResponse.error.errors) {
        msgs.push(errorObj.defaultMessage);
      }
      message=msgs.toString();
    }*/
    /*else if(!HelperService.isEmpty(errorResponse.error.errors)){
      var msgs = new Array(); 
      for (var errorObj of  errorResponse.error.errors) {
        msgs.push(errorObj.defaultMessage);
      }
      message=msgs.toString();
    }else if(!HelperService.isEmpty(errorResponse.error.messages)){
      message=new Array(errorResponse.error.messages).toString();
    }else if(!HelperService.isEmpty(errorResponse.error.message)){
      message=errorResponse.error.message;
    }else if(!HelperService.isEmpty(errorResponse.error.error_description)){
      message=errorResponse.error.error_description;
    }*/ 

    let moreDetails=null;
    if(errorResponse.error !=null && !HelperService.isEmpty(errorResponse.error.details)){
      moreDetails=new Array(errorResponse.error.details).toString()
    }
    

    let modal=this.modalService.openNotificationModal(
      (heading || 'Error'),
      message,
      AlertType.error,
      AlertMode.notification,{okayButtonText:'Close',moreDetails:moreDetails});

    modal.componentInstance.onSubmit.subscribe(action=>{
      modal.close();

    });
  }

  private showInformationMessage(response:HttpResponse<any>){
    let message:string; 
     if(!HelperService.isEmpty(response.body.messages)){
      message=new Array(response.body.messages).toString();   
    
      let modal=this.modalService.openNotificationModal(
        response.body.info || 'Info',
        message,
        AlertType.info,
        AlertMode.notification,{okayButtonText:'Okay'});
  
      modal.componentInstance.onSubmit.subscribe(action=>{
        modal.close();
      });
    }
    
  }


  

}

