import { HttpStatusCode } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { Title } from '@angular/platform-browser';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';
import { AlertMode, AlertType } from 'src/app/components/modal/notification-modal/notification-modal.component';
import { approutes } from 'src/app/constants/routes.config';
import { OtpValidate } from 'src/app/models/register/registration.model';
import { ChangeEmailObject } from 'src/app/models/settings/settings.model';
import { ModalService } from 'src/app/services/modal/modal.service';
import { SettingsService } from 'src/app/services/resource/internal/settings.service';
import { AuthUserService } from 'src/app/services/utils/auth-user.service';
import { HelperService, isEmpty } from 'src/app/services/utils/helper.service';
import { passwordMatchValidator, sameEmailValidator, samePasswordValidator } from 'src/app/validator/password.validator';

@Component({
  selector: 'app-setttings',
  templateUrl: './setttings.component.html',
  styleUrls: ['./setttings.component.scss']
})
export class SetttingsComponent implements OnInit {

  isCredExpired: boolean=false;
  passwordForm!: UntypedFormGroup;
  emailChangeForm!: UntypedFormGroup;
  //serviceToken!: string;
  appRoutes=approutes;
  uimetadata:any;
  passwordFormDetails:any;
  constructor(
    private modalService: ModalService,
    private titleService: Title,
    private settingsService:SettingsService,
    private authUserService:AuthUserService,
    private modal: ModalService,
    private route:ActivatedRoute
    ) { 
      HelperService.setPageTitle(this.titleService,"Settings");
      this.uimetadata=this.route.snapshot.data['uimetadata'];
      this.passwordFormDetails=this.uimetadata.forms.find((f: { name: string; })=>f.name=='passwordForm');
    }

    get passwordform(){
      return this.passwordForm.controls;
    }

    get emailchangeform(){
      return this.emailChangeForm.controls;
    }

  ngOnInit(): void {
      // this.passwordForm = new FormGroup({
      //   currentPassword: new FormControl('', [Validators.required,]),
      //   newPassword: new FormControl('', [Validators.required,samePasswordValidator('currentPassword'), Validators.pattern('^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$'),]), 
      //   confirmPassword: new FormControl('', [Validators.required,passwordMatchValidator('newPassword')]),
      // });

      this.passwordForm = new UntypedFormGroup({});
      this.passwordFormDetails.fields.forEach((field:any) => {
        this.passwordForm.addControl(field.key, new UntypedFormControl('', [Validators.required,]))
      });
      // this.passwordForm.addControl('newPassword',new FormControl('', [Validators.required,samePasswordValidator('currentPassword'), Validators.pattern('^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$'),]))
      // this.passwordForm.addControl('confirmPassword',new FormControl('', [Validators.required,passwordMatchValidator('newPassword')]))

      this.passwordForm.get('currentPassword')?.valueChanges
      .subscribe(value => {
        this.passwordForm.get('newPassword')?.updateValueAndValidity();
      });
      this.passwordForm.get('newPassword')?.valueChanges
      .subscribe(value => {
        this.passwordForm.get('confirmPassword')?.updateValueAndValidity();
      });

      this.emailChangeForm = new UntypedFormGroup({
        currentEmail: new UntypedFormControl({value:this.authUserService.getEmail(),disabled:true}, [Validators.required,Validators.email,]),
        currentPassword: new UntypedFormControl('', [Validators.required,]),
        newEmail: new UntypedFormControl('', [Validators.required, Validators.email,sameEmailValidator('currentEmail')]), 
      });
  }

  submitPassword(){
    if(this.passwordForm.valid){
      let modal = this.modalService.openNotificationModal('Confirm Submission',
        'Are you sure to update your existing password?',
        AlertType.warning, AlertMode.confirmation);
        modal.componentInstance.onSubmit.subscribe(data => {
          modal.close();
          if (data === true) {
            this.settingsService.changePassword(this.passwordForm.value.currentPassword,this.passwordForm.value.newPassword).subscribe(response=>{
              this.passwordForm.reset();
            });
          }
        })  
    }else{
      Object.keys(this.passwordForm.controls).forEach(field => {
        const control = this.passwordForm.get(field);
        control?.markAsTouched({ onlySelf: true });
      });
    }

  }

  changeEmail(){
    if(this.emailChangeForm.valid){
      let modal = this.modalService.openNotificationModal('Confirm Submission',
        'Are you sure to update your existing email?',
        AlertType.warning, AlertMode.confirmation);
        modal.componentInstance.onSubmit.subscribe(data => {
          modal.close();
          if (data === true) {
            let body=new ChangeEmailObject();
            body.currentPassword=this.emailChangeForm.value.currentPassword;
            body.newEmail=this.emailChangeForm.value.newEmail;
            this.settingsService.requestChangeEmailOtp(body).subscribe(response=>{
                /** opening otp modal */
              let modal = this.modal.openOtpModal(response.maskedIdentifier,response.expireAfter,response.otpLength,'Email');
              modal.componentInstance.onSubmit.subscribe((data: any) => {
                let request=new ChangeEmailObject();
                request.currentPassword=this.emailChangeForm.value.currentPassword;
                request.newEmail=this.emailChangeForm.value.newEmail;
                request.validate=new OtpValidate(data.otp,response.otpServiceToken);
                this.settingsService.changeEmail(request).subscribe((response) => {
                  modal.close();
                  this.emailChangeForm.reset();
                }, error => {
                  console.log(error)
                  if (error.status == HttpStatusCode.BadRequest) {
                    modal.componentInstance.otpForm.reset();
                    modal.componentInstance.attemptsLeft=error.error.responsePayload.attempts_left;
                  } else if (error.status == HttpStatusCode.UnprocessableEntity) {
                    modal.close();
                  }
                })
              });
              modal.componentInstance.onResend.subscribe((data: any) => {
                /** user opted to  otp */
                if (data.resend === true) {
                  this.settingsService.resendOtp(response.otpServiceToken).subscribe(() => {
                    modal.componentInstance.hideResendButton();
                  }, (error:any) => {
                    if (error.status == HttpStatusCode.UnprocessableEntity) {
                      modal.close();
                    }
                  });
                }
              });
              modal.componentInstance.onCancel.subscribe((data) => {
                /** user opted to close otp modal */
                modal.close();
                this.settingsService.invalidateOtp(response.otpServiceToken).subscribe(data=>{
                  this.emailChangeForm.reset();
                  this.emailChangeForm.setValue({currentEmail: this.authUserService.getEmail()});
                });
              });
            })
          }
        })  
    }else{
      Object.keys(this.emailChangeForm.controls).forEach(field => {
        const control = this.emailChangeForm.get(field);
        control?.markAsTouched({ onlySelf: true });
      });
    }
  }

  

 

}
