import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { resourceEndpointConfig } from 'src/app/constants/resource-endpoint.config';
import { OtpResponse } from 'src/app/models/register/registration.model';
import { ChangeEmailObject } from 'src/app/models/settings/settings.model';

@Injectable({
  providedIn: 'root'
})
export class SettingsService {

  constructor(    private httpClient: HttpClient,   ) { }

  
   changePassword(oldPassword:string,newPassword:string):Observable<any>{
     const body={
       oldPassword: oldPassword,
       newPassword: newPassword,
     }
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    return this.httpClient.post(resourceEndpointConfig.settings.updatePassword,body,{headers: headers});
  }

  requestChangeEmailOtp(body:ChangeEmailObject):Observable<OtpResponse>{
    const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    
    return this.httpClient.post(resourceEndpointConfig.settings.requestEmailChangeOtp, body,{headers: headers})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  changeEmail(body:ChangeEmailObject):Observable<any>{
   const headers = new HttpHeaders()
    .set('Content-Type', 'application/json')
    return this.httpClient.post(resourceEndpointConfig.settings.emailChange, body,{headers: headers})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  

  resendOtp(id:string):Observable<any>{   
 
    return this.httpClient.patch(resourceEndpointConfig.settings.requestEmailChangeOtp+'/resend/'+encodeURIComponent(id),null)
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  invalidateOtp(id:string) {
    return this.httpClient.patch(resourceEndpointConfig.settings.requestEmailChangeOtp+'/invalidate/'+encodeURIComponent(id),null)
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }
}
