import { Component, EventEmitter, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup, ValidatorFn, Validators } from '@angular/forms';
import { Title } from '@angular/platform-browser';
import { ModalService } from 'src/app/services/modal/modal.service';
import { phoneNumberValidator } from 'src/app/validator/phone-number.validator';
import { trimValidator } from 'src/app/validator/trim.validator';
import { HelperService } from 'src/app/services/utils/helper.service';
import { OtpResponse, RegisterRequest } from 'src/app/models/register/registration.model';
import { PublicApiService } from 'src/app/services/resource/public/public-api.service';
import { RequestObject } from 'src/app/models/request/requests.model';
import { HttpStatusCode } from '@angular/common/http';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {
  registrationForm!: UntypedFormGroup;
  private serviceToken!: string;
  searchQueryForm!: UntypedFormGroup;
  searchEvent!: EventEmitter<any>;
  @ViewChild("searchTemplate")
  searchTemplate!: TemplateRef<any>;
  @ViewChild("updateActionbuttons")
  updateActionbuttons!: TemplateRef<any>;
  requestSearchResult!: RequestObject[] | undefined;

  constructor(
    public titleService: Title,
    private registrationService: PublicApiService,
    private modal: ModalService,
  ) {
    //this.titleService.setTitle(this.titleService.getTitle().split("-")[0] + " - " + "");
    HelperService.setPageTitle(this.titleService,'Join Us');
    this.registrationService.getRefData().subscribe((response:any)=>{
      this.countries=response;
    });
  }

  countries:any;

  @ViewChild("rules")
  rules!: TemplateRef<any>;

  @ViewChild("modalActions")
  modalActions!: TemplateRef<any>;

  get f() {
    return this.registrationForm.controls;
  }

  submit() {
    if (this.registrationForm.valid) {
      const dialogProp=this.modal.openDialog(
        {headers:{headerText:'Rules and Regulation'},
        body:{template:  this.rules},
        action:{buttonTemplate:this.modalActions}
      });
      dialogProp.afterClosed().subscribe(accepted=>{
        if(accepted==true){
          let formData=this.registrationForm.value;
          /** user clicked on I agree button */
          this.registrationService.requestOtp(formData).subscribe((response:OtpResponse) => {
            console.log(formData);
            this.registrationForm.disable();
            this.serviceToken=response.otpServiceToken;
            /** opening otp modal */
            let modal = this.modal.openOtpModal(response.maskedIdentifier,response.expireAfter,response.otpLength,'Email');

            modal.componentInstance.onSubmit.subscribe((data: any) => {
              /** user submitting otp and registration end point is getting called */
              console.log(formData);

              this.registrationService.register(formData,data.otp,this.serviceToken).subscribe((response) => {
                this.registrationForm.enable();
                modal.close();
                this.ngOnInit();
              }, error => {
                if (error.status == HttpStatusCode.BadRequest) {
                  /** This block will execute when otp is incorrect */
                  modal.componentInstance.otpForm.reset();
                  modal.componentInstance.attemptsLeft=error.error.responsePayload.attempts_left;
                } else if (error.status == HttpStatusCode.UnprocessableEntity) {
                  /** This block will execute when max attempt exceeded or OTP expired */
                  this.registrationForm.enable();
                  modal.close();
                  this.ngOnInit();
                }
              })
            });

            modal.componentInstance.onResend.subscribe((data: any) => {
              /** user opted to resend otp */
              if (data.resend === true) {
                this.registrationService.resendOtp(this.serviceToken).subscribe(() => {
                  modal.componentInstance.hideResendButton();
                });
              }
            }, (error:any) => {
              if (error.status == HttpStatusCode.UnprocessableEntity) {
                 /** when otp got expired */
                this.registrationForm.enable();
                modal.close();
                this.ngOnInit();
              }
            });
            modal.beforeClosed().subscribe((data) => {
              /** user opted to close otp modal */
              this.registrationForm.enable();
              modal.close();
              this.ngOnInit();
              if (!HelperService.isEmpty(data.closeButtonClieked) && data.closeButtonClieked == true) {
                /** invalidating otp */
                this.registrationService.invalidateOtp(this.serviceToken).subscribe();
              }
            });
         });
        }else{
          /** user clicked on I disagee button */
          this.ngOnInit();
        }
      })
    } else {
      Object.keys(this.registrationForm.controls).forEach(field => {
        const control = this.registrationForm.get(field);
        control?.markAsTouched({ onlySelf: true });

      });
    }
  }

  ngOnInit(): void {
    this.registrationForm = new UntypedFormGroup({
      firstname: new UntypedFormControl('', [Validators.required, Validators.pattern('[a-zA-Z ]*'), Validators.minLength(2), Validators.maxLength(20), trimValidator('Firstname')]),
      middlename: new UntypedFormControl('', [Validators.minLength(2), Validators.maxLength(20), trimValidator('Middlename')]),
      lastname: new UntypedFormControl('', [Validators.required,Validators.pattern('[a-zA-Z ]*'), Validators.minLength(2), Validators.maxLength(20), trimValidator('Lastname')]),
      email: new UntypedFormControl('', [Validators.required, Validators.email, trimValidator('Email address')]),
      code: new UntypedFormControl('91', [Validators.required]),
      mobile: new UntypedFormControl('', [Validators.required, phoneNumberValidator('Mobile number', 'code')]),
      hometown: new UntypedFormControl('', [Validators.required]),
      reasonForJoining: new UntypedFormControl('', [Validators.required, Validators.maxLength(200), Validators.minLength(10), trimValidator('Reason for joining')]),
      otherSocialWork: new UntypedFormControl('', [Validators.required])
      //acceptDeclariation: new FormControl('', [Validators.requiredTrue])
    });
    this.registrationForm.get('code')?.valueChanges
        .subscribe(value => {
          this.registrationForm.get('mobile')?.updateValueAndValidity();
        });

  }

  get g() {
    return this.searchQueryForm.controls;
  }

  knowRequestStatus(){
    this.requestSearchResult=undefined;
    this.searchEvent = new EventEmitter();
    this.searchQueryForm=new UntypedFormGroup({
      requestId: new UntypedFormControl('', [Validators.required]),
      code: new UntypedFormControl('', [Validators.required]),
    });

    this.modal.openDialog({
      body: {
        template: this.searchTemplate
      },
      headers: {
        headerText: 'Know Your Request Status'
      },
      action: { buttonTemplate: this.updateActionbuttons, buttonContext: { buttons: { cancel: 'Exit', add: 'Search' } } },
      dimention: { width: 700 }
    });

    this.searchEvent.subscribe(decision => {
      if (decision === true) {
        if (this.searchQueryForm.valid) {
          let id=this.searchQueryForm.value.requestId;
          let code=this.searchQueryForm.value.code;
          this.registrationService.getJoinRequestStatus(id,code).subscribe(data=>{
            this.requestSearchResult=data;
          })
        }else{
          this.searchQueryForm.markAllAsTouched();
        }
      }
    });

  }


}
