import { AbstractControl, ValidatorFn } from "@angular/forms";

export function trimValidator(fieldname:string): ValidatorFn {
  return (control: AbstractControl) => {
    if (control && control.value && control.value.startsWith(' ')) {
      return {
        'trimError': { message: fieldname+' has leading whitespace.' }
      };
    }else
    if (control && control.value && control.value.endsWith(' ')) {
      return {
        'trimError': { message: fieldname+' has trailing whitespace.' }
      };
    }
    
    return null;
  };
}

   
