export function getDirtyValues123(form: any) {
  let dirtyValues: any = {};

  Object.keys(form.controls)
    .forEach(key => {
      let currentControl = form.controls[key];

      if (currentControl.dirty) {
        if (currentControl.controls) {
          dirtyValues[key] = getDirtyValues123(currentControl);
        }
        else {
          dirtyValues[key] = currentControl.value;
        }

      }
    });
  console.log(dirtyValues)
  return dirtyValues;
}

export function isEmpty(str: string): boolean {
  return (!str || str.length === 0);
}

export function getFirstDigit(number: number): number {
  while (number >= 10)
    number /= 10;

  return number | 0;
}

export function openWindow(url: string) {
  window.open(url, "_blank");
}


export function compareObjects(latest: any, old: any): any {
  const keys1: string[] = [];
  const values1: any[] = [];
  Object.keys(latest).forEach((element) => {
    keys1.push(element);
  });
  Object.values(latest).forEach((element) => {
    values1.push(element);
  });
  const keys2: any[] = [];
  const values2: any[] = [];
  Object.keys(old).forEach((element) => {
    keys2.push(element);
  });
  Object.values(old).forEach((element) => {
    values2.push(element);
  });
  const obj: any = {};
  keys1.forEach((element, i) => {
    for (let index = 0; index < keys2.length; index++) {
      if (element === keys2[index]) {
        let updatedValue = sanitize(values1[i]);
        let oldValue = sanitize(values2[index]);

        /*
         let a = {
           updatedValue: updatedValue,
           updatedValueType: typeof updatedValue,
           oldValue: oldValue,
           oldValueType: typeof oldValue,
         }
         console.warn('field name=' + element, a);
        */

        if (updatedValue !== oldValue) {
          const jsonKey = element;
          obj[jsonKey] = values1[i];
        }

        break;
      }
    }
  });

  return obj;
}

function sanitize(value: any) {
  if (typeof value === 'number' || value instanceof Number) {
    return value.toString();
  }
  if (typeof value === 'object' && value instanceof Date) {
    return value.toLocaleDateString();
  }
  if (typeof value === 'string' || value instanceof String) {
    if (!isNaN(Date.parse(value.toString()))) {
      return new Date(value.toString()).toLocaleDateString();
    }
    return value.trim();
  }

  return value;
}



export function isEmptyObject(obj: any) {
  //added 20/11/2021
  //An null or undefined object should return true, So filtering null values
  if (obj != null && obj != undefined)
    Object.keys(obj).forEach(key => obj[key] === undefined && delete obj[key]);
  return (obj && (Object.keys(obj).length === 0));
}

/**
 * Check if 2 arrays are equal 
 */
export function isDuplicateArray(a1: any, a2: any) {
  //return JSON.stringify(obj1) === JSON.stringify(obj2);
  if (Array.isArray(a1) && Array.isArray(a2)) {
    a1.sort();
    a2.sort();
  }
  console.log(a1, a2)

    return a1.length === a2.length && a1.every((o: any, idx: string | number) => isEmptyObject(compareObjects(o, a2[idx])))
 }

export function saveAs(blob: Blob, fileName: string) {
  const url = window.URL.createObjectURL(blob);
  const a = document.createElement('a');
  a.href = url;
  a.download = fileName;
  a.click();
  window.URL.revokeObjectURL(url);
  a.remove();
}



export function objectsEqual(o1: any, o2: any): boolean {
  return isEmptyObject(compareObjects(o1, o2))
}

export const weekdaysFilter = (d: Date | null): boolean => {
  const day = (d || new Date()).getDay();
  // Prevent Saturday and Sunday from being selected.
  return day !== 0 && day !== 6;
};

export const futureDateFilter = (d: Date | null): boolean => {
  const date = (d || new Date());
  return date <= new Date();
}


export const pastDateFilter = (d: Date | null): boolean => {
  const date = (d || new Date());
  return date > new Date();
}

export const buildHistoryMessage=(changeObj:ChangeObject)=>{
  if(changeObj.from && changeObj.to){
    return '<span class="font-bold capitalize">'+changeObj.fieldname+'</span> field value changed from <span class="italic">'+changeObj.from+'</span>  to <span class="italic">'+changeObj.to+'</span>.';
  }
  if(!changeObj.from && changeObj.to){
    return '<span class="font-bold capitalize">'+changeObj.fieldname+'</span> field value set as <span class="italic">'+changeObj.to+'</span>.';
  }
  if(changeObj.from && !changeObj.to){
    return '<span class="font-bold capitalize">'+changeObj.fieldname+'</span> field value removed. Earlier it was <span class="italic">'+changeObj.from+'</span>.';;
  }
  return null;
}

const history=HISTORY;

export const historyHeader= (name:string,action:string)=>{
  if(action === history.actions.created){
    return name+' created';
  }
  if(action === history.actions.updated){
    return name+' updated';
  }
  if(action === history.actions.deleted){
    return name+' deleted';
  }
  if(action === history.actions.restored){
    return name+' restored';
  }
  return null;
}


export const getKeyByValue= (object:any,value:string):string =>{
  return Object.keys(object).find(key => object[key] === value) || '';
}

export const path =(s:string)=>{
  if(s.startsWith('/')){
    return s.substring(1);
  }
  return s;
}

