import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { Title } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import { SCOPE } from 'src/app/constants/auth-scope.constats';
import { ProfileObject, Roles } from 'src/app/models/user/profile.model';
import { ProfileService } from 'src/app/services/resource/internal/profile.service';
import { Location } from '@angular/common';
import { ThemePalette } from '@angular/material/core';
import { approutes } from 'src/app/constants/routes.config';
import { ModalService } from 'src/app/services/modal/modal.service';
import { AlertMode, AlertType } from 'src/app/components/modal/notification-modal/notification-modal.component';
import { phoneNumberValidator } from 'src/app/validator/phone-number.validator';
import { AuthUserService } from 'src/app/services/utils/auth-user.service';
import { SpinnerVisibilityService } from 'ng-http-loader';
import { PROFILE } from 'src/app/constants/profile.constants';
import { compareObjects, HelperService, isDuplicateArray, isEmpty, objectsEqual } from 'src/app/services/utils/helper.service';
import { conditionalValidator } from 'src/app/validator/conditionally-required.validator';


@Component({
  selector: 'app-update',
  templateUrl: './update.component.html',
  styleUrls: ['./update.component.scss']
})
export class UpdateComponent implements OnInit {
  color: ThemePalette = 'primary';
  updateProfileDetailsForm: UntypedFormGroup=new UntypedFormGroup({
    id: new UntypedFormControl(''),
    firstName: new UntypedFormControl('', [Validators.required, Validators.pattern('[a-zA-Z ]*'), Validators.minLength(2), Validators.maxLength(20),]),
    middleName: new UntypedFormControl('', [Validators.pattern('[a-zA-Z ]*'), Validators.minLength(2), Validators.maxLength(20)]),
    lastName: new UntypedFormControl('', [Validators.required, Validators.pattern('[a-zA-Z ]*'), Validators.minLength(2), Validators.maxLength(20),]),
    gender: new UntypedFormControl('', [Validators.required]),
    about: new UntypedFormControl('', [Validators.minLength(5), Validators.maxLength(200)]),
    birthDay: new UntypedFormControl('', []),
    dialCode: new UntypedFormControl('', [Validators.required]),
    contactNumber: new UntypedFormControl('', [Validators.required, phoneNumberValidator('Mobile number', 'dialCode')]),
    email: new UntypedFormControl({ value: '', disabled: true }, [Validators.required]),
    address: new UntypedFormControl('', [Validators.minLength(5), Validators.maxLength(200)]),
    hometown: new UntypedFormControl('', [Validators.required, Validators.maxLength(100)]),
    district: new UntypedFormControl('', [
      conditionalValidator(() =>
        this.isIndia(this.updateProfileDetailsForm.get('country')?.value),
        Validators.required
      )]),
    state: new UntypedFormControl('', [
      conditionalValidator(() =>
        this.isIndia(this.updateProfileDetailsForm.get('country')?.value),
        Validators.required
      )]),
    country: new UntypedFormControl('', []),
    consent: new UntypedFormControl('', []),
  });

  //authUser!: AuthUser;
  canUpdateOtherProfileInfo!: boolean;
  profileData!: ProfileObject;
  referenceData: any = {};
  profileViewOrEdit!: boolean;

  rolesControl!: UntypedFormControl;
  userAttributeForm!: UntypedFormGroup;
  canUpdateRoles!: boolean;
  canUpdateAdminAttributes!: boolean;
  canUpdateCashierAttributes!: boolean;

  constructor(
    public titleService: Title,
    //private authService: OAuthService,
    private profileService: ProfileService,
    private location: Location,
    private router: Router,
    private route: ActivatedRoute,
    private modalService: ModalService,
    private authUserService: AuthUserService,
    private spinner: SpinnerVisibilityService,
  ) {

  }

  ngOnInit(): void {

    //this.authUser = new AuthUser(this.authService.getIdentityClaims(), this.authService.getGrantedScopes());
    this.canUpdateOtherProfileInfo = this.authUserService.isAccrediatedToAny(SCOPE.update.other_profile_info,SCOPE.update.volunteer_donation_attributes,SCOPE.update.volunteer_admin_attributes)
    this.canUpdateRoles = this.authUserService.isAccrediatedTo(SCOPE.update.other_profile_info)
    this.canUpdateAdminAttributes = this.authUserService.isAccrediatedToAny(SCOPE.update.other_profile_info,SCOPE.update.volunteer_admin_attributes)
    this.canUpdateCashierAttributes = this.authUserService.isAccrediatedToAny(SCOPE.update.other_profile_info,SCOPE.update.volunteer_donation_attributes)

    let stateData = this.location.getState() as any;
    if (stateData.profileData && stateData.referenceData) {
      this.referenceData = stateData.referenceData;
      this.profileData = stateData.profileData;
      this.setProfileUpdateFormData(this.profileData);

    } else {
      this.profileService.getReferenceData().subscribe(data => {
        this.referenceData = data;
        
        if (!this.route.snapshot.queryParams['action']) {
          this.profileService.getMyProfile({ roleRequired: false }).subscribe(data => {
            this.profileData = data;
            this.setProfileUpdateFormData(this.profileData);
            if(stateData.isAlert){
              this.updateProfileDetailsForm.markAllAsTouched();
            }
          })
        }
      })
    }


    if (this.route.snapshot.queryParams['action']) {
      this.profileViewOrEdit = true;
      HelperService.setPageTitle(this.titleService,"Update Member Details");

      this.rolesControl = new UntypedFormControl(this.profileData.roles.map(x => x.id), [Validators.required]);

      this.userAttributeForm = new UntypedFormGroup({
        isBlocked: new UntypedFormControl({
          value:!this.profileData.isUnblocked,
          disabled:!(this.canUpdateRoles || this.canUpdateAdminAttributes)
        }, [Validators.required]),
        isActiveContributor: new UntypedFormControl({
          value:this.profileData.isActiveContributor,
          disabled:!(this.canUpdateRoles || this.canUpdateCashierAttributes)
        }, [Validators.required]),
      });
    } else {
      this.profileViewOrEdit = false;
      HelperService.setPageTitle(this.titleService,"Update Profile");

      this.updateProfileDetailsForm.get('dialCode')?.valueChanges
        .subscribe(value => {
          this.updateProfileDetailsForm.get('contactNumber')?.updateValueAndValidity();
        });

      this.updateProfileDetailsForm.get('country')?.valueChanges
        .subscribe(value => {
          this.updateProfileDetailsForm.get('state')?.updateValueAndValidity();
          this.updateProfileDetailsForm.get('district')?.updateValueAndValidity();
          if (!this.isIndia(value)) {
            this.updateProfileDetailsForm.get('state')?.setValue('');
            this.updateProfileDetailsForm.get('district')?.setValue('');
          }
        });
    }



  }

  setProfileUpdateFormData(profileData:ProfileObject){
    this.updateProfileDetailsForm.setValue({
      id: profileData.id,
      firstName: profileData.firstName,
      middleName: profileData.middleName,
      lastName: profileData.lastName,
      gender: profileData.gender != null ? profileData.gender : '',
      about: profileData.about,
      birthDay: profileData.birthDay,
      dialCode: profileData.dialCode != null ? profileData.dialCode : '',
      contactNumber: profileData.contactNumber,
      email: profileData.email,
      address: profileData.address,
      hometown: profileData.hometown,
      district: profileData.district != null ? profileData.district : '',
      state: profileData.state != null ? profileData.state : '',
      country: profileData.country != null ? profileData.country : '',
      consent: profileData.publicProfileConsent,
    })
  }

  isIndia(value: string) {
    return ( this.referenceData.countries && value === this.referenceData.countries[0].key) || value === 'IN';
  }


  updateProfileData() {
    if (this.updateProfileDetailsForm.valid) {
      let profile = new ProfileObject;
      profile.firstName = this.updateProfileDetailsForm.value.firstName;
      profile.middleName = this.updateProfileDetailsForm.value.middleName;
      profile.lastName = this.updateProfileDetailsForm.value.lastName;
      profile.about = this.updateProfileDetailsForm.value.about;
      profile.dialCode = this.updateProfileDetailsForm.value.dialCode;
      profile.contactNumber = this.updateProfileDetailsForm.value.contactNumber;
      profile.email = this.updateProfileDetailsForm.value.email;
      profile.gender = this.updateProfileDetailsForm.value.gender;
      profile.birthDay = this.updateProfileDetailsForm.value.birthDay;
      profile.address = this.updateProfileDetailsForm.value.address;
      profile.hometown = this.updateProfileDetailsForm.value.hometown;
      profile.state = this.updateProfileDetailsForm.value.state;
      profile.district = this.updateProfileDetailsForm.value.district;
      profile.country = this.updateProfileDetailsForm.value.country;
      profile.publicProfileConsent = this.updateProfileDetailsForm.value.consent;
      console.log(profile)
      console.log(this.profileData)
      if (objectsEqual(profile, this.profileData)) {
        this.modalService.openNotificationModal('Error', 'No changes have been made', AlertType.error, AlertMode.notification);
      } else {
        let modal = this.modalService.openNotificationModal('Confirm Submission',
          'Are you sure to update the data you modified?',
          AlertType.warning, AlertMode.confirmation);
        modal.componentInstance.onSubmit.subscribe(data => {
          modal.close();
          if (data === true) {
            this.profileService.updateMyProfile(compareObjects(profile, this.profileData)).subscribe(data => {
              this.profileData = data;
              this.backToMyProfile();
            })
          }
        })
      }


    } else {
      Object.keys(this.updateProfileDetailsForm.controls).forEach(field => {
        const control = this.updateProfileDetailsForm.get(field);
        control?.markAsTouched({ onlySelf: true });
      });
    }

  }

  updateProfileAttribute() {
    if (this.userAttributeForm.valid && this.rolesControl.valid) {
      let profile = new ProfileObject;
      profile.isUnblocked = !(this.userAttributeForm.value.isBlocked);
      profile.isActiveContributor = this.userAttributeForm.value.isActiveContributor;
      profile.roles = this.rolesControl.value;

      if (profile.isUnblocked === this.profileData.isUnblocked && profile.isActiveContributor === this.profileData.isActiveContributor && isDuplicateArray(profile.roles, this.profileData.roles.map(x => x.id))) {
        this.modalService.openNotificationModal('Error', 'No changes have been made', AlertType.error, AlertMode.notification);
      } else {
        let modal = this.modalService.openNotificationModal('Confirm Submission',
          'Are you sure to update the data you modified?',
          AlertType.warning, AlertMode.confirmation);
        modal.componentInstance.onSubmit.subscribe(data => {
          modal.close();
          if (data === true) {

            this.profileService.updateProfileAttribute(this.profileData.id, profile).subscribe(data => {
              this.profileData = data;
              this.backToMembers();
            })

          }
        })
      }
    } else {
      Object.keys(this.userAttributeForm.controls).forEach(field => {
        const control = this.userAttributeForm.get(field);
        control?.markAsTouched({ onlySelf: true });
      });
      this.rolesControl.markAsTouched({ onlySelf: true });
    }
  }
  get UAF() {
    return this.userAttributeForm.controls;
  }

  get UPDF() {
    return this.updateProfileDetailsForm.controls;
  }

  @ViewChild('fileInput')
  fileInput!: ElementRef;
  dataimage: File | undefined;
  selectedFileName = '';
  selectedFile!: any;
  selectedFileErrors: string[] = [];

  uploadFileEvt(imgFile: any) {
    if (imgFile.target.files && imgFile.target.files[0]) {
      this.selectedFileName = '';
      this.selectedFileErrors = [];

      //(imgFile.target.files[0].type === 'image/jpeg' || imgFile.target.files[0].type === 'image/png' || imgFile.target.files[0].type === 'image/jpg')
      if (!imgFile.target.files[0].type.match("image.*")) {
        this.selectedFileErrors.push('File type must be PNG or JPG or JPEG')
        this.reset();
        return;
      }
      if (!(imgFile.target.files[0].size > 50000 && imgFile.target.files[0].size < 1000000)) {
        this.selectedFileErrors.push('File size should be greter than 50Kb and less than 1Mb')
        this.reset();
        return;
      }
      this.selectedFile = imgFile.target.files[0];
      Array.from(imgFile.target.files).forEach((file: any) => {
        this.selectedFileName += file.name;
      });
      console.log(imgFile)
      // HTML5 FileReader API
      let reader = new FileReader();
      reader.onload = (e: any) => {
        let image = new Image();
        image.src = e.target.result;
        image.onload = rs => {
          this.dataimage = e.target.result;
        };
      };
      reader.readAsDataURL(imgFile.target.files[0]);
      // Reset if duplicate image uploaded again
      this.fileInput.nativeElement.value = "";
    } else {
      this.selectedFileName = '';
    }
  }
  private reset() {
    this.fileInput.nativeElement.value = "";
    this.selectedFile = undefined;
    this.selectedFileName = '';
    this.dataimage = undefined;
  }

  changePicture() {
    if (this.selectedFile != undefined) {
      let modal = this.modalService.openNotificationModal('Confirm Submission',
        'Are you sure to update your Profile Picture?',
        AlertType.warning, AlertMode.confirmation);
      modal.componentInstance.onSubmit.subscribe(data => {
        modal.close();
        if (data === true) {
          this.profileService.changeProfilePicture(this.selectedFile).subscribe(data => {
            this.backToMyProfile();
          });
        }
      })
    } else {
      this.selectedFileErrors = [];
      this.selectedFileErrors.push("Please select a file")
    }
  }

  removePicture() {
    let modal = this.modalService.openNotificationModal('Confirm Submission',
      'Are you sure to update your Profile Picture?',
      AlertType.warning, AlertMode.confirmation);
    modal.componentInstance.onSubmit.subscribe(data => {
      modal.close();
      if (data === true) {
        this.profileService.removeProfilePicture().subscribe(data => {
          this.backToMyProfile();
        });
      }
    })
  }

  backToMyProfile() {
    this.spinner.show();
    this.profileService.getReferenceData().subscribe(referenceData => {
      this.profileService.getMyProfile({ roleRequired: true }).subscribe(data => {
        this.spinner.hide();
        this.router.navigate([approutes.myprofile.url], { state: { myProfile: data, profileReferenceData: referenceData } });
      })
    })
  }

  backToMembers() {
    this.spinner.show();
    this.profileService.getAllProfiles({roleRequired:true}).subscribe(data => {
      this.spinner.hide();
      this.router.navigate([approutes.member.url], { state: { allProfiles: data } });
    })
  }

  backToMemberDetails() {
    this.profileService.getReferenceData().subscribe(referenceData => {
      this.profileService.getProfile(this.profileData.id).subscribe(data => {
        this.router.navigate([approutes.profile.url], { queryParams: { action: this.canUpdateOtherProfileInfo ? PROFILE.action.viewOrEdit : PROFILE.action.view }, state: { myProfile: data, profileReferenceData: referenceData } });
      })
    })
  }
}
