import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ProfileObject } from 'src/app/models/user/profile.model';
import { ProfileService } from 'src/app/services/resource/internal/profile.service';
import { approutes } from 'src/app/constants/routes.config';
import { ActivatedRoute, Router } from '@angular/router';
import { OAuthService } from 'angular-oauth2-oidc';
import { SCOPE } from 'src/app/constants/auth-scope.constats';
import { Location } from '@angular/common';
import { HelperService, isEmpty } from 'src/app/services/utils/helper.service';
import { AuthUserService } from 'src/app/services/utils/auth-user.service';
import { PROFILE } from 'src/app/constants/profile.constants';


@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {
  myProfile: ProfileObject = new ProfileObject;
  pageName!: string;
  appRoutes = approutes;
  //warning: string="WARNING";
  canUpdateOtherProfileInfo!: boolean;
  referenceData: any;
  isInactiveUser!: boolean;
  profileMetadata=PROFILE;

  actionView!: boolean;
  actionEdit!: boolean;
  backTo!:string;

  constructor(
    public titleService: Title,
    private profileService: ProfileService,
    private router: Router,
    private route: ActivatedRoute,
   // private authService: OAuthService,
    private location:Location,
    private authUserService:AuthUserService,


  ) {

  }

  ngOnInit(): void {
    //this.authUser = new AuthUser(this.authService.getIdentityClaims(), this.authService.getGrantedScopes());
    this.isInactiveUser=this.authUserService.isUserInactive();

    this.canUpdateOtherProfileInfo = this.authUserService.isAccrediatedToAny(SCOPE.update.other_profile_info,SCOPE.update.volunteer_donation_attributes,SCOPE.update.volunteer_admin_attributes)
    let statedata=this.location.getState() as any;
    if(statedata && statedata.myProfile && statedata.profileReferenceData){
      this.myProfile=statedata.myProfile;
      this.referenceData = statedata.profileReferenceData;
      
    }else{
      this.profileService.getReferenceData().subscribe(data => {
        this.referenceData = data;
        if(!this.route.snapshot.queryParams['action']){
          this.profileService.getMyProfile({ roleRequired: true }).subscribe(data => {
            this.myProfile = data;
          })
        }
      })
    }
    if (this.route.snapshot.queryParams['action']) {
      this.pageName='Member Details';
      HelperService.setPageTitle(this.titleService,"Member Details");
      let routeParam=this.route.snapshot.queryParams['action'] as string;
      console.log(routeParam.split('~').includes(this.profileMetadata.action.view))
      console.log(routeParam.split('~').includes(this.profileMetadata.action.edit))
      if(routeParam.split('~').includes(this.profileMetadata.action.edit)){
        this.actionEdit=true;
      }
      if(routeParam.split('~').includes(this.profileMetadata.action.view)){
        this.actionView=true;
      }
      this.backTo=this.route.snapshot.queryParams['backTo']? this.route.snapshot.queryParams['backTo'] :'MEM';

     }else{
      this.pageName="My Profile";
      HelperService.setPageTitle(this.titleService,"My Profile");
      this.actionView=false;
      this.backTo='DB';
    }
    
    
  }

  updateProfile() {
    this.router.navigate([approutes.updateprofile.url], { queryParams: { action: PROFILE.action.edit } ,state:{profileData:this.myProfile,referenceData:this.referenceData} });
  }

  updateMyProfile() {
    this.router.navigate([approutes.updateprofile.url], {state:{profileData:this.myProfile,referenceData:this.referenceData}  });
  }

  getCountryName(countryCode:string){
    if(isEmpty(countryCode) || isEmpty(this.referenceData.countries)){
      return countryCode;
    }
    return this.referenceData.countries.find((country: { key: string; })=>country.key === countryCode)?.displayValue || null;
  }

   getStateName(stateCode:string){
    if(isEmpty(stateCode) || isEmpty(this.referenceData.states)){
      return stateCode;
    }
    return this.referenceData.states.find((state: { key: string; })=>state.key === stateCode)?.displayValue || null;
  }

  getDistrictName(districtCode:string){
    if(isEmpty(districtCode) || isEmpty(this.referenceData.districts)){
      return districtCode;
    }
    return this.referenceData.districts.find((district: { key: string; })=>district.key === districtCode)?.displayValue || null;
  }

  getDialcode(code:string){
    if(isEmpty(code) || isEmpty(this.referenceData.countries)){
      return code;
    }
    return this.referenceData.countries.find((country: { dialCode: { key: string; }; } )=>country.dialCode.key === code)?.dialCode.displayValue || null;
  }

  backToMembers(){
    this.profileService.getAllProfiles({roleRequired:true}).subscribe(data=>{
      this.router.navigate([approutes.member.url],{state:{allProfiles:data}});
    })
  }
}
