import { AbstractControl, ValidatorFn } from "@angular/forms";

export function phoneNumberValidator(phoneNumberFieldName:string,countryCodeFieldName:string): ValidatorFn {
  return (control: AbstractControl) => {
    const country = control.parent?.get(countryCodeFieldName)?.value;
    const length=String(control.value).length;
    if(control.value!="" && (country==='91' || country==='+91') && length!=10 ){
      return {
        'phoneNumberError': { message: phoneNumberFieldName+' must have 10 digits.' }
      };
    }else if(control.value!="" && (length<4 || length>13)){
      return {
        'phoneNumberError': { message: phoneNumberFieldName+' must have digits between 4 to 13.' }
      };
    }
    return null;
  };
}

   