import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'actuatorSearch'
})
export class ActuatorSearchPipe implements PipeTransform {

  transform(actuators: any, searchValue: string): any[] {
    if(!searchValue){
      return actuators;
    }
    
    return actuators.filter((actuator: any)=>
    (actuator.key && actuator.key.toLowerCase().includes(searchValue.toLowerCase())) ||
    (actuator.href && actuator.href.toLowerCase().includes(searchValue.toLowerCase()))
    );
  }

}
