import { Pipe, PipeTransform } from '@angular/core';
import { HelperService } from '../services/utils/helper.service';

@Pipe({
  name: 'replaceNull'
})
export class ReplaceNullPipe implements PipeTransform {

  transform(value: any, repleceText: string = '-'): any {
    //console.log(value)
    if (typeof value === 'undefined' || value === null || HelperService.isEmpty(value)) {
      return repleceText;
    }
    return value;
  }

}


@Pipe({
  name: 'replaceUnderScore'
})
export class ReplaceUnderScorePipe implements PipeTransform {

  transform(value: string, repleceText: string = ' '): any {
    //console.log(value)
    if (typeof value === 'undefined' || value === null || HelperService.isEmpty(value)) {
      return repleceText;
    }
    return value.replace('_',repleceText);
  }

}
