import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { resourceEndpointConfig } from 'src/app/constants/resource-endpoint.config';
import { ComprehensiveSummaryObject } from 'src/app/models/dashboard/comprehensive-summary.model';
import { Cacheable } from 'ts-cacheable';

@Injectable({
  providedIn: 'root'
})
export class DashboardService {

  constructor(    private httpClient: HttpClient,   ) { }

  getComprehensiveSummary(options?:{notification?:boolean,member?:boolean,donation?:boolean,request?:boolean,event?:boolean,notice?:boolean}):Observable<ComprehensiveSummaryObject>{
    return this.httpClient.get(resourceEndpointConfig.dashboard.comprehensiveSummary,{params:options})
        .pipe(
          map((data: any) => {
            return data.responsePayload;
          })
        );
  }

  
  getApplicationHealth():Observable<string>{
    return this.httpClient.get(resourceEndpointConfig.dashboard.health)
        .pipe(
          map((data: any) => {
            return data.status;
          })
        );
  }

  // getFirebaseToken():Observable<any>{
  //   return this.httpClient.post(resourceEndpointConfig.dashboard.firebaseToken,{})
  //       .pipe(
  //         map((data: any) => {
  //           return data.responsePayload;
  //         })
  //       );
  // }

  //@Cacheable({async: true,maxAge: 24*60*60*1000*1000})
  getUIMetadata(name:string):Observable<any>{
    const param = new HttpParams()
     .set('page',name)
    return this.httpClient.get(resourceEndpointConfig.uimetadata,{params:param})
        .pipe(
          map((data: any) => {
            return data.responsePayload;
          })
        );
  }

}



