import { Pipe, PipeTransform } from '@angular/core';
import { CONTACTREQUESTS } from 'src/app/constants/requests.constants';
import { isEmpty } from 'src/app/services/utils/helper.service';

@Pipe({
  name: 'contactRequestStatusFilter'
})
export class ContactRequestStatusFilterPipe implements PipeTransform {

  transform(allStatus: any, currentStatus: string): any {
    if (isEmpty(currentStatus)) {
      return allStatus;
    }

    switch (currentStatus) {
      case CONTACTREQUESTS.status.submitted:
        return allStatus
          .filter((data: any) => data.key !== CONTACTREQUESTS.status.completed)
          .filter((data: any) => data.key !== CONTACTREQUESTS.status.reopen)
          .filter((data: any) => data.key !== CONTACTREQUESTS.status.rejected);
      case CONTACTREQUESTS.status.inprogress:
        return allStatus.filter((data: any) => (
          data.key === CONTACTREQUESTS.status.inprogress ||
          data.key === CONTACTREQUESTS.status.completed ||
          data.key === CONTACTREQUESTS.status.onHold ||
          data.key === CONTACTREQUESTS.status.rejected
        ));
      case CONTACTREQUESTS.status.pending:
        return allStatus.filter((data: any) => (
          data.key === CONTACTREQUESTS.status.pending ||
          data.key === CONTACTREQUESTS.status.inprogress
        ));
      case CONTACTREQUESTS.status.completed:
        return allStatus.filter((data: any) => (
          data.key === CONTACTREQUESTS.status.completed ||
          data.key === CONTACTREQUESTS.status.reopen
        ));
        case CONTACTREQUESTS.status.rejected:
          return allStatus.filter((data: any) => (
            data.key === CONTACTREQUESTS.status.rejected ||
            data.key === CONTACTREQUESTS.status.reopen
          ));
      case CONTACTREQUESTS.status.reopen:
        return allStatus
          .filter((data: any) => data.key !== CONTACTREQUESTS.status.completed)
          .filter((data: any) => data.key !== CONTACTREQUESTS.status.submitted)
          .filter((data: any) => data.key !== CONTACTREQUESTS.status.rejected);
      case CONTACTREQUESTS.status.onHold:
        return allStatus.filter((data: any) => data.key === CONTACTREQUESTS.status.onHold || data.key === CONTACTREQUESTS.status.inprogress);
      default:
        return allStatus.filter((data: string)=>false);
    }

  }

}
