import { Location } from '@angular/common';
import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { AlertMode, AlertType } from 'src/app/components/modal/notification-modal/notification-modal.component';
import { SCOPE } from 'src/app/constants/auth-scope.constats';
import { EVENTS } from 'src/app/constants/events.constants';
import { HISTORY } from 'src/app/constants/history.constants';
import { approutes } from 'src/app/constants/routes.config';
import { EventsObject } from 'src/app/models/events/event.model';
import { ModalService } from 'src/app/services/modal/modal.service';
import { EventsService } from 'src/app/services/resource/internal/events.service';
import { AuthUserService } from 'src/app/services/utils/auth-user.service';
import { buildHistoryMessage, HelperService, historyHeader, openWindow, saveAs } from 'src/app/services/utils/helper.service';

@Component({
  selector: 'app-our-events',
  templateUrl: './our-events.component.html',
  styleUrls: ['./our-events.component.scss']
})
export class OurEventsComponent implements OnInit {
 
  allEvents!:EventsObject[];
  searchValue!:string;
  appRoutes=approutes;
  canCreateEvent!:boolean;
  canEditEvent!: boolean;
  canDeleteEvent!: boolean;
  singleEvent: boolean=false;
  history=HISTORY;
  @ViewChild("historyTemplate")
  historyTemplate!: TemplateRef<any>;
  @ViewChild("updateActionbuttons")
  updateActionbuttons!: TemplateRef<any>;
  canViewEventsHistory!: boolean;
  isInactiveUser!: boolean;

  historyHeader=historyHeader;
  historyMessage=buildHistoryMessage;

  constructor(
    private eventsService:EventsService,
    private router:Router,
    private title:Title,
    private authUserService:AuthUserService,
    private location: Location,
    private modal: ModalService,


    ) { 
      HelperService.setPageTitle(this.title,"Events");

    }

  ngOnInit(): void {
    this.isInactiveUser=this.authUserService.isUserInactive();

    let data=this.location.getState()  as any;    
    if(data && data.allEvents){
      this.allEvents=data.allEvents;
      if(data.fragment){
        this.router.navigate([this.appRoutes.ourEvents.url],{fragment:data.fragment});
      }
    }else if(data && data.viewEvent){
      this.allEvents=[];
      this.allEvents.push(data.viewEvent); 
      this.singleEvent=true;
    }else{
      this.refresh();
    }
    this.canCreateEvent=this.authUserService.isAccrediatedTo(SCOPE.create.events);
    this.canEditEvent=this.authUserService.isAccrediatedTo(SCOPE.update.events);
    this.canDeleteEvent=this.authUserService.isAccrediatedTo(SCOPE.delete.events);
    this.canViewEventsHistory=this.authUserService.isAccrediatedTo(SCOPE.read.event_history);

  }

  refresh(){
    this.eventsService.getEventsInternal().subscribe(data=>{
      this.allEvents=data;
      this.singleEvent=false;
    });

  }

  viewAttachment(id:string){
    this.eventsService.viewAttachment(id,true).subscribe(data=>{
      openWindow(data);
    })
  }
  
  downloadAttachment(id:string,fileName:string){
    this.eventsService.viewAttachment(id,false).subscribe(data=>{
      saveAs(data,fileName);
    })
  }

  createEvent(){
    this.eventsService.getDraftedEvent().subscribe(response=>{
      //response
      this.router.navigate([this.appRoutes.createEvent.url],{state:{draftedEvent:response},queryParams:{action:EVENTS.action.create}})

    })
  }


  editEvent(id:string){
    this.eventsService.getEvent(id)
     .subscribe((data:EventsObject)=>{
    this.router.navigate([this.appRoutes.updateEvent.url,id],{state:{event:data},queryParams:{action:EVENTS.action.edit}})
     });
  }

  deleteEvent(id:string){
    let modal=this.modal.openNotificationModal('Delete Event','This action will erase all related items along with this event. Are you still want to delete this event?',AlertType.warning,AlertMode.confirmation);
          modal.componentInstance.onSubmit.subscribe(response=>{
            if(response == true){
              this.eventsService.deleteEvent(id).subscribe(data=>{
                this.allEvents.splice(this.allEvents.findIndex(e=>e.id === id),1);
              })
            }
            modal.close();
          });
  }

  showEventHistory(id:string){
    this.eventsService.getEventHistory(id).subscribe(data=>{
      this.modal.openDialog({
        body: {
          template: this.historyTemplate,
          context:{
            histories:data,
            name: 'Event'
          }
        },
        headers: {
          headerText:'View Changes'
        },
        action: { buttonTemplate: this.updateActionbuttons, buttonContext: { buttons: { cancel: 'Close'} } },
        dimention: { width: 800 }
      });
    })
  }

  

}
