import { Injectable } from '@angular/core';
import { OAuthService } from 'angular-oauth2-oidc';

@Injectable({
  providedIn: 'root'
})
export class AuthUserService {
  private scopes:string[];
  private identity:any;
  constructor(private oAuthService:OAuthService) { 
    this.identity=oAuthService.getIdentityClaims();
    this.scopes=oAuthService.getGrantedScopes() as string[];
    console.log('Authenticated users GrantedScopes : ', this.scopes)

  }

  isAccrediatedTo(access : string): boolean {
    /*
    var userScopes = '';
    Object.values(this.scopes).forEach(value => {
        userScopes = userScopes + ' ' + value;
    })
    var result = userScopes.includes(access);
    */
    var result =this.scopes.includes(access);
    console.log('Has ' + access + ' access = ', result);
    return result;
  }

  isAccrediatedToAny(...access : string[]): boolean {
      var result:boolean[]=[];
      access.forEach(scope=>{
          result.push(this.isAccrediatedTo(scope));
      });
      return result.includes(true);
  }

  getName(){
    return this.identity.given_name || this.identity.nickname || this.identity.name;
  }

  getEmail(){
    return this.identity.email;
  }

  getAvatarUrl(){
    return this.identity.picture || 'https://eu.ui-avatars.com/api/?name='+this.getName()+'&size=250';
  }

  getUserId(){
    return this.identity.sub;
  }

  isUserInactive() : boolean{
    return sessionStorage.getItem('isCurrentUserInactive') === "true" ? true : false;
  }

  clearUserCache(){
    sessionStorage.removeItem('isCurrentUserInactive');
  }

  setUserInactive(inactive:boolean){
    sessionStorage.setItem('isCurrentUserInactive',( inactive === true ? "true" : "false"));
  }

  setCustomState(key:string,value:string){
    sessionStorage.setItem('custom'+key,value);
    let expOn=(new Date().getTime()+10*60*1000).toString();
    sessionStorage.setItem('customExpire'+key,expOn)
  }
  getCustomState(key:string){
    if(new Date().getTime() > new Number(sessionStorage.getItem('customExpire'+key))){
      sessionStorage.removeItem('customExpire'+key);
    }
    return sessionStorage.getItem('custom'+key);
  }
}
