import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { approutes, pathvariable } from 'src/app/constants/routes.config';
import { CallbackComponent } from './components/callback/callback.component';
import { AboutComponent } from './pages/about/about.component';
import { ContactComponent } from './pages/contact/contact.component';
import { EventsComponent } from './pages/events/events.component';
import { ForgotPasswordComponent } from './pages/forgot-password/forgot-password.component';
import { LogoutComponent } from './pages/logout/logout.component';
import { RegisterComponent } from './pages/register/register.component';
import { ApplicationMonitoringComponent } from './pages/secured/application-monitoring/application-monitoring.component';
import { ContributionsComponent } from './pages/secured/contributions/contributions.component';
import { DashboardComponent } from './pages/secured/dashboard/dashboard.component';
import { ProfileComponent } from './pages/secured/profile/profile.component';
import { UpdateComponent } from './pages/secured/profile/update/update.component';
import { RegistrationRequestComponent } from './pages/secured/request/registration-request.component';
import { SetttingsComponent } from './pages/secured/setttings/setttings.component';
import { WelcomeComponent } from './pages/welcome/welcome.component';
import { AuthGuardService } from './services/guards/auth-guard.service';
import { NoAuthGuardService } from './services/guards/no-auth-guard.service';
import { MembersComponent } from './pages/secured/members/members.component';
import { FundsAndExpenditureComponent } from './pages/secured/funds-and-expenditure/funds-and-expenditure.component';
import { NoticeBoardComponent } from './pages/secured/notice-board/notice-board.component';
import { OurEventsComponent } from './pages/secured/our-events/our-events.component';
import { SupportCenterComponent } from './pages/secured/support-center/support-center.component';
import { EventActionComponent } from './pages/secured/our-events/event-action/event-action.component';
import { CreateNoticeComponent } from './pages/secured/notice-board/create-notice/create-notice.component';
import { EventSeeMoreComponent } from './pages/events/event-see-more/event-see-more.component';
import { CreateRequestComponent } from './pages/secured/request/create-request/create-request.component';
import { FeedbackComponent } from './pages/feedback/feedback.component';
import { PaymentDetailsComponent } from './pages/secured/contributions/payment-details/payment-details.component';
import { UiMetadataResolver } from './services/resolvers/ui-metadata.resolver';
import { ContributionResolver } from './services/resolvers/contribution.resolver';
import { CreateNoticeResolver, NoticeDataResolver, NoticePublisherRoleResolver, NoticeResolver, ViewNoticeResolver } from './services/resolvers/notice.resolver';
import { path } from './services/utils/helper.service';
import { NoticeCallBackComponent } from './pages/secured/notice-board/notice-call-back/notice-call-back.component';



const routes: Routes = [
  { path: path(approutes.welcome.url ), component: WelcomeComponent },
  // { path: path(approutes.aboutus.url), component: AboutComponent, },
  // { path: path(approutes.events.url), component: EventsComponent ,},
  // { path: path(approutes.contactus.url), component: ContactComponent ,},
  // { path: path(approutes.joinus.url), component: RegisterComponent ,},
  // { path: path(approutes.seeMoreEvent.url), component: EventSeeMoreComponent ,},
  { path: path(approutes.feedback.url), component: FeedbackComponent ,},
  {
    path: path(approutes.logout.url), component: LogoutComponent, canActivate: [
      NoAuthGuardService
    ]
  },
  { path: path(approutes.forgotpassword.url), component: ForgotPasswordComponent },
  {
    path: path(approutes.home.url), component: DashboardComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      metadata:UiMetadataResolver
    },
    data: {route: approutes.home}
  },
  {
    path: path(approutes.myprofile.url), component: ProfileComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      metadata:UiMetadataResolver
    }
    ,
    data: {route: approutes.myprofile}
  },
  {
    path: path(approutes.profile.url), component: ProfileComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      metadata:UiMetadataResolver
    },
    data: {route: approutes.profile}
  },
  {
    path: path(approutes.updateprofile.url), component: UpdateComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      metadata:UiMetadataResolver
    },
    data: {route: approutes.updateprofile}
  },
  {
    path: path(approutes.settings.url), component: SetttingsComponent,canActivate: [
      AuthGuardService,
    ],
    resolve:{
      metadata:UiMetadataResolver
    },
    data: {route: approutes.settings}
  },
  {
    path: path(approutes.contributions.url), component: ContributionsComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      metadata:UiMetadataResolver,
      //data : ContributionResolver
    },
    data: {route: approutes.contributions}
  },
  {
    path: path(approutes.requests.url), component: RegistrationRequestComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      metadata:UiMetadataResolver
    },
    data: {route: approutes.requests}
  },
  {
    path: path(approutes.createRequest.url), component: CreateRequestComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      metadata:UiMetadataResolver
    },
    data: {route: approutes.createRequest}
  },
  {
    path: path(approutes.monitoring.url), component: ApplicationMonitoringComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      metadata:UiMetadataResolver
    },
    data: {route: approutes.monitoring}
  },
  {
    path: path(approutes.member.url), component: MembersComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      metadata:UiMetadataResolver
    },
    data: {route: approutes.member}
  },
  {
    path: path(approutes.savings.url), component: FundsAndExpenditureComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      metadata:UiMetadataResolver
    },
    data: {route: approutes.savings}
  },
  {
    path: path(approutes.expenditure.url), component: FundsAndExpenditureComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      metadata:UiMetadataResolver
    },
    data: {route: approutes.expenditure}
  },
  {
    path: path(approutes.ourEvents.url), component: OurEventsComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      metadata:UiMetadataResolver
    },
    data: {route: approutes.ourEvents}
  },
  {
    path: path(approutes.createEvent.url), component: EventActionComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      metadata:UiMetadataResolver
    },
    data: {route: approutes.createEvent}
   
  },
  {
    path: path(approutes.updateEvent.url)+pathvariable.id, component: EventActionComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      metadata:UiMetadataResolver
    },
    data: {route: approutes.updateEvent}
   
  },
  {
    path: path(approutes.supportCenter.url), component: SupportCenterComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      metadata:UiMetadataResolver
    },
    data: {route: approutes.supportCenter}
  },
  {
    path: path(approutes.noticeBoard.url), component: NoticeBoardComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      notices:NoticeResolver,
      metadata:UiMetadataResolver
    },
    data: {route: approutes.noticeBoard}
  },
  {
    path: path(approutes.viewNotice.url), component: NoticeBoardComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      notice:NoticeDataResolver,
      metadata:UiMetadataResolver
    },
    data: {
      route: approutes.viewNotice,
    }
  },
  {
    path: path(approutes.createNotice.url), component: CreateNoticeComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      notice:NoticeDataResolver,
      publisherrole:NoticePublisherRoleResolver,
      metadata:UiMetadataResolver
    },
    data: {route: approutes.createNotice}
  },
  {
    path: path(approutes.updateNotice.url), component: CreateNoticeComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      notice:NoticeDataResolver,
      publisherrole:NoticePublisherRoleResolver,
      metadata:UiMetadataResolver
    },
    data: {route: approutes.updateNotice}
  },
  {
    path: path(approutes.noticeCallback.url), component: NoticeCallBackComponent,
    canActivate: [
      AuthGuardService,
    ],
  },
  {
    path: path(approutes.paymentDetails.url), component: PaymentDetailsComponent,
    canActivate: [
      AuthGuardService,
    ],
    resolve:{
      metadata:UiMetadataResolver
    },
    data: {route: approutes.paymentDetails}
  },
  {
    path: path(approutes.callback.url), component:CallbackComponent,
  },
  { path: 'secured/**', redirectTo: '/secured/home' },

  {
    path: '**', redirectTo: ''
  },
  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
