import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { resourceEndpointConfig } from 'src/app/constants/resource-endpoint.config';
import { BankingDetails, PaymentDetails } from 'src/app/models/contributions/payment-details.model';


@Injectable({
  providedIn: 'root'
})
export class PaymentDetailsService {

  constructor(    private httpClient: HttpClient,   ) { }

  
  getPaymentDetails()   : Observable<PaymentDetails>{
    return this.httpClient.get(resourceEndpointConfig.payment.paymentdetails)
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }

  addOrUpdatePaymentDetails(type:string,data:PaymentDetails)   : Observable<PaymentDetails>{
  
    //console.log(body)
    return this.httpClient.post(resourceEndpointConfig.payment.paymentdetails,data,{params:{type:type}})
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }


  getQR(): Observable<BankingDetails>{
    return this.httpClient.get(resourceEndpointConfig.payment.paymentdetails)
    .pipe(
      map((data: any) => {
        return data.responsePayload;
      })
    );
  }
}
