import { AfterViewInit, Component, ElementRef, Inject, Input, OnInit, QueryList, ViewChild, ViewChildren } from '@angular/core';
import { UntypedFormArray, FormBuilder, UntypedFormControl, UntypedFormGroup, NgModel, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { HelperService } from 'src/app/services/utils/helper.service';
import { EventEmitter } from '@angular/core';
import { CountdownComponent, CountdownGlobalConfig } from 'ngx-countdown';

@Component({
  selector: 'app-otp-modal',
  templateUrl: './otp-modal.component.html',
  styleUrls: ['./otp-modal.component.scss']
})
export class OtpModalComponent implements OnInit {

  public data;
  private showResendAfterMinutes:number= 1;
  public showResendButton:boolean | undefined ;
  
  timerConfig:any;
  onSubmit = new EventEmitter();
  onResend = new EventEmitter();
  onCancel = new EventEmitter();

  attemptsLeft!:number;
  
  otpForm: UntypedFormGroup =new UntypedFormGroup({
    digits: new UntypedFormArray([])
  });
  showSubmitButton: boolean=true;

  constructor(
    private dialogRef: MatDialogRef<OtpModalComponent>,
    @Inject(MAT_DIALOG_DATA) data:OtpData,
  ) {
    this.data = data;
  }

  get digits(){
    return this.otpForm.get('digits') as UntypedFormArray;
  }

  ngOnInit(): void {
    for(let i=1;i<=this.data.otpDigits;i++){
      this.digits.push(new UntypedFormControl('',Validators.required));
    }
    this.hideResendButton();
    setTimeout(() => {
      this.showResendButton=false;
      this.showSubmitButton=false;
    }, this.data.validInSec*1000 );
   
    this.timerConfig={ leftTime: this.data.validInSec, format: 'mm:ss', demand: false };
  }

  submit(){
    let otpVal: string='';
    if(this.otpForm.valid){
      this.otpForm.value.digits.forEach((digits:number) => {
        otpVal=otpVal+HelperService.getFirstDigit(digits);
      });
      this.onSubmit.emit({submit:true,close:false,resend:false,otp:otpVal});
    }else{
      Object.keys(this.otpForm.controls).forEach(field => { 
        const control = this.otpForm.get(field)?.markAllAsTouched();            
      });
  }
}

  close() {
    this.dialogRef.close({submit:false,closeButtonClieked:true,resend:false});
    this.onCancel.emit({submit:false,close:true,resend:false});
  }

  resend() {
    this.onResend.emit({submit:false,close:false,resend:true});
  }

  hideResendButton(){
    this.showResendButton=false;
    setTimeout(() => {
      if(this.showSubmitButton==true)
        this.showResendButton=true;
    }, this.showResendAfterMinutes*60*1000);
  }
}

export class OtpData{
    identifier!: string;
    otpDigits!: number;
    validInSec!: number;
    identifierType!: string;
}


