import { Component, OnInit } from '@angular/core';
import {  OAuthService } from 'angular-oauth2-oidc';
import { BnNgIdleService } from 'bn-ng-idle';
import { Spinkit } from 'ng-http-loader';
import { auth0Config, auth0LogoutConfig, environment } from 'src/environments/environment';
import { SpinnerComponent } from './components/spinner/spinner.component';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  
  filterSpinnerUrlPatterns=[
    'comprehensivesummary', 
    'health',
    'token',
    'savetoken',
    'jwks.json',
    'openid-configuration',
    'publicmembers',
    'dialcode',
    'publicevents',
    'actuator'
  ]



  constructor(  
    private oauth2Service:OAuthService, 
    private bnIdle: BnNgIdleService,

    ) {
    }
  ngOnInit(): void {

   
    if (environment.production) {
      if(window){
        window.console.log=function(){};
      }
    }

    

    this.oauth2Service.configure(auth0Config);
    this.oauth2Service.setupAutomaticSilentRefresh({},'access_token');
    this.oauth2Service.loadDiscoveryDocumentAndTryLogin();

    this.bnIdle.startWatching(environment.inactivityTimeFrame).subscribe((isTimedOut: boolean) => {
      if (isTimedOut) {
        console.warn('session expired due to inactivity');
        if(this.oauth2Service.hasValidAccessToken()){
         // this.notificationService.deleteToken();
        }
        this.oauth2Service.revokeTokenAndLogout(auth0LogoutConfig);
      }
    });


    
    
  }
  
  public spinkit = Spinkit; 
  public matSpinner = SpinnerComponent;

}

