import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'districtFilter'
})
export class DistrictFilterPipe implements PipeTransform {

  transform(districts: any, state: string,country:string): any {
    console.log(state,country)
    if(country && state){
      return districts.filter((district:any)=>{
        return district.stateKey === state && district.countryKey === country});
    }
    return null;
  }

}
