export class BankingDetails{   
    IFSCNumber!: string;
    accountHolderName!: string;
    accountNumber!: string;
    accountType!: string;
    bankName!: string;
    branchName!: string;
}

export class UPIDetails{  
    //if any fields are changed also remember to change at QR age at java 
    UPIId!: string;
    UPIName!: string[];
    payeeName!: string;
    UPINumber!: string;
    UPINumberCode!: string;
}

export class PaymentDetails{   
    upi!: UPIDetails;
    banking!: BankingDetails;
    id!:string;
}